import { actionTypes } from '@seismic/core';
import handleComponentDomReady from '../effects/component-dom-ready';
import { TOOLBAR } from '../../constants';
const {
    COMPONENT_PROPERTY_CHANGED,
    COMPONENT_DOM_READY
} = actionTypes;

export const lifeCycleActionHandlers = {
    [COMPONENT_DOM_READY]: {
        effect: handleComponentDomReady
    },
    [COMPONENT_PROPERTY_CHANGED]: ({
        action,
        state,
        updateState,
        dispatch

    }) => {
        const { properties } = state;
        const { payload: { name, value } } = action;
        if (name === 'config') {
            updateState({ config: value });
        }

        if (name === 'roadmapItems') {
            handleComponentDomReady({ action, properties, state, updateState });
        }
        if (name === 'showSidePanel') {
            if (!value) {
                dispatch(TOOLBAR.SIDE_PANEL_CLOSED);
            }
        }
    }
};
