import { createCustomElement } from '@seismic/core';
import snabbdom from '@seismic/snabbdom-renderer';
import styles from './panel.scss';
import rtlBehavior from '@servicenow/behavior-rtl';
import { handleTabPress } from './accessibilityHelpers';
import { t } from 'sn-translate';
import { actionTypes } from '@seismic/core';

const {
    COMPONENT_TREE_RENDERED
} = actionTypes;

const SIDE_PANEL_CLOSED = 'SN_CONFIG_PANEL#CLOSED';
const ESC_KEY = 27;
const TAB_KEY = 9;
const CLOSE = t('Close');
const BUTTON_ICONIC_CLICKED = 'NOW_BUTTON_ICONIC#CLICKED';
const CLOSE_BUTTON = 'CLOSE_BUTTON';
const RTL = 'rtl';

const view = (state) => {
    const { properties: { displayHeader, headerTitle, dir } } = state;

    return (
        <div className={`sn-roadmap-config-panel${dir === RTL? ' rtl': ''}`}>
            {displayHeader
                ? <div className="sn-roadmap-config-panel-header">
                    <div className="sn-roadmap-config-panel-header-title">
                        <now-heading variant="header-secondary" label={headerTitle} hasNoMargin={true}/>
                    </div>
                    <now-button-iconic
                        className="sn-roadmap-config-panel-close"
                        bare
                        config-aria={{
                            'aria-label': CLOSE
                        }}
                        icon="close-fill"
                        size="md"
                        variant="tertiary"
                        append-to-payload={{
                            type: CLOSE_BUTTON
                        }}
                    />
                </div>
                : null
            }
            <div className="sn-roadmap-config-panel-body">
                <slot name="sn-roadmap-config-panel-content"></slot>
            </div>
        </div>
    );
};


createCustomElement('sn-roadmap-config-panel', {
    renderer: { type: snabbdom },
    view,
    properties: {
        displayHeader: {
            default: true,
            schema: { type: 'boolean' }
        },
        headerTitle: {
            schema: {
                type: 'string'
            }
        }
    },
    styles,
    behaviors: [
        { behavior: rtlBehavior }
    ],
    actionHandlers: {
        [COMPONENT_TREE_RENDERED]: ({ host })=> {
            const closeBtn = host.shadowRoot.querySelector('.sn-roadmap-config-panel-close');
            if (closeBtn) {
                closeBtn.focus();
            }
        },
        [BUTTON_ICONIC_CLICKED]: ({ dispatch, action: { payload } }) => {
            switch (payload.type) {
                case CLOSE_BUTTON: {
                    dispatch(SIDE_PANEL_CLOSED);
                    break;
                }
            }
        }
    },
    eventHandlers: [{
        events: ['keydown'],
        effect({
            action: { payload },
            dispatch
        }) {
            const { event, host } = payload;
            if (event.keyCode === ESC_KEY) {
                dispatch(SIDE_PANEL_CLOSED);
            } else if (event.keyCode === TAB_KEY) {
                handleTabPress({
                    event,
                    host
                });
            }
        }
    }]
});
