import { SCROLL_DIR, THROTTLE_TIME } from '../../constants';
import throttle from 'lodash/throttle';

const scrollParams = {
    left: {
        interval: 0,
        intensity: 0
    },
    right: {
        interval: 0,
        intensity: 0
    },
    top: {
        interval: 0,
        intensity: 0
    },
    bottom: {
        interval: 0,
        intensity: 0
    }
};

const getScrollIntensity = (scrollDir, x, range) => {
    const scrollIntensity = [0, 2, 3, 5, 7];
    const rangeStatus = x >= range.start && x <= range.end;

    if (!rangeStatus) {
        return 0;
    }

    const speed = Math.ceil((x - range.start)/10);

    switch (scrollDir) {
        case SCROLL_DIR.LEFT:
        case SCROLL_DIR.TOP:
            return scrollIntensity[4-speed];
        case SCROLL_DIR.RIGHT:
        case SCROLL_DIR.BOTTOM:
            return scrollIntensity[speed];
    }
};

export const clearAllScrollIntervals = () => {
    for (const dir of Object.keys(scrollParams)) {
        if (scrollParams[dir].interval > 0) {
            clearInterval(scrollParams[dir].interval);
        }
        scrollParams[dir] = {
            intensity: 0,
            interval: 0
        };
    }
};

export const delayedClearScrollIntervals = () => setTimeout(clearAllScrollIntervals, 100);

export const clearAutoScroll = (dir) => {
    if (dir) {
        clearInterval(scrollParams[dir].interval);
        scrollParams[dir] = {
            intensity: 0,
            interval: 0
        };
    }
};

const setAutoScroll = (dir, scrollContainer, currentIntensity, scrollX, scrollY, timelineWidth = -1) => {
    let canScroll = true;
    if ((dir === SCROLL_DIR.LEFT || dir === SCROLL_DIR.RIGHT) && timelineWidth !== -1) {
        canScroll = scrollContainer.scrollWidth <= timelineWidth;
    }

    if (currentIntensity !== 0 && canScroll) {
        if (currentIntensity !== scrollParams[dir].intensity) {
            scrollParams[dir].intensity = currentIntensity;
        }
        if (scrollParams[dir].interval === 0) {
            scrollParams[dir].interval = setInterval(() => {
                scrollContainer.scrollBy(scrollX * scrollParams[dir].intensity, scrollY * scrollParams[dir].intensity);
            }, THROTTLE_TIME);
        }
    } else {
        clearAutoScroll(dir);
    }
};

const autoScroll = (
    mouseParams,
    edgeWidth,
    horizontalScrollContainer,
    verticalScrollContainer = null,
    timelineWidth = -1
) => {
    const { clientX, clientY } = mouseParams;
    let currentIntensity = 0;
    const horizontalScrollContainerRect = horizontalScrollContainer.getBoundingClientRect();

    currentIntensity = getScrollIntensity(SCROLL_DIR.LEFT, clientX, {
        start: horizontalScrollContainerRect.left,
        end: horizontalScrollContainerRect.left + edgeWidth
    });
    setAutoScroll(SCROLL_DIR.LEFT, horizontalScrollContainer, currentIntensity, -1, 0, timelineWidth);

    currentIntensity = getScrollIntensity(SCROLL_DIR.RIGHT, clientX, {
        start: horizontalScrollContainerRect.right - edgeWidth,
        end: horizontalScrollContainerRect.right
    });
    setAutoScroll(SCROLL_DIR.RIGHT, horizontalScrollContainer, currentIntensity, 1, 0, timelineWidth);

    if (verticalScrollContainer) {
        const verticalScrollContainerRect = verticalScrollContainer.getBoundingClientRect();

        currentIntensity = getScrollIntensity(SCROLL_DIR.TOP, clientY, {
            start: verticalScrollContainerRect.top,
            end: verticalScrollContainerRect.top + edgeWidth
        });
        setAutoScroll(SCROLL_DIR.TOP, verticalScrollContainer, currentIntensity, 0, -1);

        currentIntensity = getScrollIntensity(SCROLL_DIR.BOTTOM, clientY, {
            start: verticalScrollContainerRect.bottom - edgeWidth,
            end: verticalScrollContainerRect.bottom
        });
        setAutoScroll(SCROLL_DIR.BOTTOM, verticalScrollContainer, currentIntensity, 0, 1);
    }
};

export const throttledScroll = throttle(autoScroll, THROTTLE_TIME);
