// eslint-disable-next-line no-unused-vars
import { Fragment } from '@seismic/snabbdom-renderer';
import { ACTIVE_ROW_PATH_UPDATED, BAR_FOCUS_CLEARED } from '../../comp-config/actions';
import { ROW_CLASS, USER_AVATAR, BAR_OVERFLOW_ICON_WIDTH } from '../../constants';
import isEmpty from 'lodash/isEmpty';
import { ifJsx } from '../../util';
import { cancelPopover } from './resizable';

const renderLine2Data = (metricFieldsWithValues) => {
    return metricFieldsWithValues.map((metric) => {
        const metricValue = isEmpty(metric.value) ? '0' : metric.value;
        const ariaLabel = `${metric.label}: ${metricValue}`;

        return <span aria-label={ariaLabel} className="sn-roadmap-bar-left-container-line2-item">{metricValue}</span>;
    });
};

const updateMetricVisibility = (elm, overflow) => {
    const firstChildWidth = elm.children[0].offsetWidth;
    let currentElementWidth = elm.offsetWidth;
    if (overflow) {
        currentElementWidth = currentElementWidth - BAR_OVERFLOW_ICON_WIDTH;
    }
    if (firstChildWidth > currentElementWidth) {
        elm.style.visibility = 'hidden';
    } else {
        elm.style.visibility = 'visible';
    }
};

export const renderLine2 = (metricFieldsWithValues, overflow) => {
    return ifJsx(metricFieldsWithValues && metricFieldsWithValues.length > 0,
        <div className="sn-roadmap-bar-left-container-line2"
            hook-insert={(e) => updateMetricVisibility(e.elm, overflow)}
            hook-update={(e) => updateMetricVisibility(e.elm, overflow)}>
            {renderLine2Data(metricFieldsWithValues)}
        </div>
    );
};

const getInitials = (userName) => {
    if (!userName) {
        return;
    }
    const names = userName.split(' ');
    if (names.length === 1) {
        return (
            <span className="sn-roadmap-bar-avatar-label">
                <span className="line-height-crop">
                    { names[0].substring(0, 1).toUpperCase() }
                </span>
            </span>
        );
    } else {
        return (
            <span className="sn-roadmap-bar-avatar-label">
                <span className="line-height-crop">
                    { names[0].substring(0, 1).toUpperCase() +
						names[1].substring(0, 1).toUpperCase() }
                </span>
            </span>
        );
    }
};

const getAvartarLabel = (userName) => {
    return `${USER_AVATAR}: ${userName}`;
};

export const renderRightBarContainer = (itemOwnerName, avatar) => {
    return ifJsx(avatar || itemOwnerName,
        <div className="sn-roadmap-bar-right-container">
            <span class={{
                'sn-roadmap-bar-avatar': true,
                'has-image': avatar
            }}>
                {
                    ifJsx(avatar, () => {
                        return <img
                            className="sn-roadmap-bar-avatar-img"
                            src={ avatar }
                            alt={ getAvartarLabel(itemOwnerName) }
                        />;
                    },
                    () => {
                        return <span role="img" aria-label={ getAvartarLabel(itemOwnerName) }>
                            { getInitials(itemOwnerName) }
                        </span>;
                    })
                }
            </span>
        </div>
    );
};

export const getParentElement = (currentElement, targetElementClassName) => {
    let targetElement = currentElement;

    while (targetElement && !targetElement.classList.contains(targetElementClassName)) {
        targetElement = targetElement.parentElement;
    }

    return targetElement;
};

export const updateDropTargetPath = (status, targetElement, dispatch, activeBarId = -1) => {
    if (status && targetElement) {
        if (!targetElement.classList.contains(ROW_CLASS)) {
            targetElement = getParentElement(targetElement, ROW_CLASS);
        }
        dispatch(ACTIVE_ROW_PATH_UPDATED, {
            path: targetElement && targetElement.groupByValues ? targetElement.groupByValues : [],
            activeBarId,
            enableResizers: true
        });
    } else {
        dispatch(ACTIVE_ROW_PATH_UPDATED, {
            path: [],
            activeBarId: -1,
            enableResizers: false
        });
    }
};

const isInViewport = (elm) => {
    const rect = elm.getBoundingClientRect();

    return (
        rect.top >= 0 &&
        rect.left >= 0 &&
        rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
        rect.right <= (window.innerWidth || document.documentElement.clientWidth)
    );
};

export const delayedFocus = (htmlElement, dispatch) => {
    if (!isInViewport(htmlElement)) {
        htmlElement.scrollIntoView({ behavior: 'smooth', block: 'center' });
    }

    setTimeout(() => {
        htmlElement.focus();
        cancelPopover();
    }, 10);

    dispatch(BAR_FOCUS_CLEARED);
};

export const highlightBar = (htmlElement, status) => {
    if (status) {
        htmlElement.classList.add('sn-roadmap-bar-highlight');
    } else {
        htmlElement.classList.remove('sn-roadmap-bar-highlight');
    }
};
