import '@servicenow/now-heading';
import '@servicenow/now-button';
import '@servicenow/now-dropdown';
import { isNumber, isEmpty } from 'lodash';
import {
    ADD_FIELD,
    PILLAR_DRAG_EVENT,
    PILLAR_LABEL,
    PILLAR_ACTIONS,
    NEUTRAL0_COLOR
} from './constants';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { delayedFocusLastModifiedPillar, delayedFocusSelectedItem, delayedFocusAddFieldList } from './accessibility';

const getFieldSelectDropdown = (state) => {
    const {
        properties: {
            items
        }
    } = state;
    const itemsDropdown = state.itemsDropdown || {};

    if (!isEmpty(itemsDropdown)) {
        return (
            <now-dropdown-panel
                manage-selected-items={true}
                manage-opened={true}
                items={items}
                selected-items={isEmpty(itemsDropdown.selectedItem) ? [''] : [itemsDropdown.selectedItem]}
                opened={itemsDropdown.opened}
                select="single"
                target-ref={itemsDropdown.targetRef}
                append-to-payload={{
                    index: itemsDropdown.index
                }}
                constrain={{ maxWidth: 'target', minWidth: 'target' }}
                hook-insert={({ elm }) => delayedFocusSelectedItem(elm, itemsDropdown.selectedItem)}
                hook-update={({ elm }) => delayedFocusSelectedItem(elm, itemsDropdown.selectedItem)}
            />
        );
    }
};

const handlePillarDragStart = (event, index)=> {
    event.target.style.opacity = '0.9';
    event.target.style.backgroundColor = NEUTRAL0_COLOR;
    event.dataTransfer.setData('startIndex', index);
    event.target.blur();
};

const handlePillarDrop = (event, index, dispatch)=> {
    const startIndex = event.dataTransfer.getData('startIndex');
    const targetIndex = index;

    dispatch(PILLAR_DRAG_EVENT, {
        startIndex,
        targetIndex
    });
};

const buildPillars = (state, dispatch) => {
    const {
        properties: { items, draggable, selectorId },
        selectedItems,
        pillarActions
    } = state;
    const pillarItems = items.filter(({ name }) => selectedItems.includes(name));

    pillarItems.sort(function(a, b) {
        if (selectedItems.indexOf(a.name) < selectedItems.indexOf(b.name)) {
            return -1;
        }

        return 1;
    });

    if (pillarItems.length > 0) {
        return (
            <div className="sn-roadmap-config-selector-pillar-container" >
                {
                    pillarItems.map((pillarItem, index) => {
                        const pillarId = selectorId + '-' + pillarItem.name;

                        return (
                            <div className="sn-roadmap-config-selector-pillar"
                                id = {pillarId}
                                ellipsis={true}
                                draggable={draggable ? 'true' : false}
                                actions={pillarActions}
                                ondragstart={(e) => {
                                    handlePillarDragStart(e, index);
                                }}
                                ondragover={(e) => {
                                    e.preventDefault();
                                }}
                                ondragend={(e) => {
                                    e.target.style.opacity = '1';
                                    e.target.style.backgroundColor='none';
                                }}
                                ondrop={(e) => {
                                    handlePillarDrop(e, index, dispatch);
                                }}
                                hook-update={({ elm }) => delayedFocusLastModifiedPillar(elm)}
                            >
                                { draggable? <now-icon icon="grid-vertical-outline" size="md" /> : null }
                                <div className='sn-roadmap-config-selector-pillar-label-container'>{pillarItem.label}</div>
                                <now-button-iconic
                                    className={'edit-' + pillarId}
                                    icon="pencil-outline"
                                    tooltipContent={PILLAR_LABEL.EDIT}
                                    size="sm"
                                    variant="tertiary"
                                    bare={true}
                                    configAria={{ button: { 'aria-label': pillarItem.label + ' ' + PILLAR_LABEL.EDIT } }}
                                    append-to-payload={{
                                        id: PILLAR_ACTIONS.PILLAR_EDIT_ACTION,
                                        type: pillarItem.name,
                                        selectedItem: pillarItem.name,
                                        index
                                    }}
                                />
                                <now-button-iconic
                                    className={'close-' + pillarId}
                                    icon="close-outline"
                                    tooltipContent={PILLAR_LABEL.REMOVE}
                                    size="sm"
                                    variant="tertiary"
                                    bare={true}
                                    configAria={{ button: { 'aria-label': pillarItem.label + ' ' + PILLAR_LABEL.REMOVE } }}
                                    append-to-payload={{
                                        id: PILLAR_ACTIONS.PILLAR_REMOVE_ACTION,
                                        type: pillarItem.name,
                                        selectedItem: pillarItem.name,
                                        index
                                    }}
                                />
                            </div>
                        );
                    })
                }
            </div>
        );
    }
};

export const view = (state, { dispatch }) => {
    const {
        properties: {
            items,
            limit,
            maxLimitMsg
        },
        selectedItems,
        display
    } = state;

    if (!display) {
        return null;
    }

    return (
        <div className="sn-roadmap-config-selector">
            { buildPillars(state, dispatch) }
            {isNumber(limit)
                ? selectedItems.length < limit
                    ?<now-dropdown
                        manage-selected-items={true}
                        items={items}
                        selectedItems={[]}
                        icon="plus-fill"
                        placeholder={ADD_FIELD}
                        size="sm"
                        variant="secondary"
                        select="single"
                        bare={true}
                        panelFitProps={{ constrain: { maxWidth: 'target', minWidth: 'target' } }}
                        hook-insert={({ elm }) => delayedFocusAddFieldList(elm)}
                        hook-update={({ elm }) => delayedFocusAddFieldList(elm)}
                    />
                    : !isEmpty(maxLimitMsg) ? <div className="sn-roadmap-config-selector-msg">{maxLimitMsg}</div> : <Fragment />
                : <Fragment />
            }
            {getFieldSelectDropdown(state)}
        </div>
    );
};
