import { KEYS } from './constants/roadmap-constants';

function setFocusToFirstChild(parentEl) {
    if (parentEl && parentEl.firstChild) {
        if (parentEl.firstChild.nodeName === 'SLOT') {
            parentEl = parentEl.firstChild.assignedNodes()[0];
        }
        parentEl.firstChild.focus();
    }
}

function setFocusToLastChild(parentEl) {
    if (parentEl && parentEl.lastChild) {
        parentEl.lastChild.focus();
    }
}

export function focusLoop(event, parent) {
    if ([KEYS.UP, KEYS.DOWN, KEYS.RIGHT, KEYS.LEFT, KEYS.HOME, KEYS.END].indexOf(event.keyCode) === -1 ||
    event.shiftKey) {
        return;
    }
    event.preventDefault();
    event.stopPropagation();
    const activeEl = event.target;
    if (parent) {
        setFocusToFirstChild(activeEl);

        return;
    }

    switch (event.keyCode) {
        case KEYS.DOWN:
        case KEYS.RIGHT: {
            const sibling = activeEl.nextElementSibling;
            if (sibling) {
                sibling.focus();
            } else {
                setFocusToFirstChild(activeEl.parentNode);
            }
            break;
        }
        case KEYS.UP:
        case KEYS.LEFT: {
            const sibling = activeEl.previousElementSibling;
            if (sibling) {
                sibling.focus();
            } else {
                setFocusToLastChild(activeEl.parentNode);
            }
            break;
        }
        case KEYS.HOME: {
            setFocusToFirstChild(activeEl.parentNode);
            break;
        }
        case KEYS.END: {
            setFocusToLastChild(activeEl.parentNode);
            break;
        }
    }
}
