import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line
import { CALENDAR_ZOOM_LEVEL_TITLE } from '../constants/translated';
import { CALENDAR } from '../constants/constants';

const renderCalendar = ({
    possibleTimeScales,
    currentTimeScale,
    headerHeight
}) => {
    const calendarHeight = `${ headerHeight }px`;

    return (
        <div className="sn-roadmap-timeline-header-calendar"
            style={{
                height: calendarHeight,
                lineHeight: calendarHeight
            }}
        >
            <now-dropdown
                aria-label={CALENDAR_ZOOM_LEVEL_TITLE}
                className="sn-roadmap-timeline-header-calendar-icon"
                bare
                hide-label icon="calendar-outline"
                items={possibleTimeScales}
                panel-fit-props='{}'
                select="single"
                size="md"
                selected-items={[currentTimeScale]}
                variant="secondary"
                tooltip-content={CALENDAR_ZOOM_LEVEL_TITLE}
                config-aria={{ 'aria-label': CALENDAR_ZOOM_LEVEL_TITLE }}
                append-to-payload={{ type: CALENDAR }}
            ></now-dropdown>
        </div>
    );
};

const renderRow = ({
    timelineData
}) => {
    const { topLevelScales, bottomLevelScales, isTopLevel, scaleWidth, config: { headerHeight } } = timelineData;
    const subScaleHeight = `${isTopLevel ? headerHeight : headerHeight / 2}px`;

    return (
        <Fragment>
            {
                isTopLevel ? null
                    : <div
                        className="sn-roadmap-timeline-header-row"
                        role="rowgroup"
                        style={{
                            height: subScaleHeight,
                            lineHeight: subScaleHeight
                        }}
                    >
                        { Object.keys(topLevelScales).length === 0 ? null
                            : topLevelScales.map((top, index) => {
                                return (
                                    <div
                                        className="sn-roadmap-timeline-header-cell"
                                        role="row"
                                        style={{
                                            width: `${top.width}px`,
                                            maxWidth: `${top.width}px`
                                        }}
                                        key={'timeline_header_top_cell_' + index}
                                        title={top.displayValue}
                                    >
                                        { top.parentLabel ? <span role="gridcell">{top.displayValue} - {top.parentLabel}</span>
                                            : <span role="gridcell">{top.displayValue}</span> }
                                    </div>
                                );
                            })}
                    </div>
            }
            <div
                className="sn-roadmap-timeline-header-row"
                style={{
                    height: subScaleHeight,
                    lineHeight: subScaleHeight
                }}
            >
                {Object.keys(bottomLevelScales).length === 0 ? null
                    : bottomLevelScales.map((bottom, index) => {
                        return (
                            <div
                                className='sn-roadmap-timeline-header-cell'
                                role="row"
                                style={{
                                    width: `${scaleWidth}px`,
                                    maxWidth: `${scaleWidth}px`
                                }}
                                key={'timeline_header_bottom_cell_' + index}
                                title={bottom}
                            >
                                <span role="gridcell">{bottom}</span>
                            </div>
                        );
                    })}
            </div>
        </Fragment>
    );
};

const renderTimeline = ({
    config,
    timelineData
}) => {
    return (
        <div className="sn-roadmap-timeline-header-grid" role='grid'>
            {
                renderRow({
                    config,
                    timelineData
                })
            }
        </div>
    );
};

export const timelineHeaderView = (
    state
) => {
    const { config, timelineData, possibleTimeScales } = state;
    const { currentTimeScale } = timelineData;
    const { headerHeight } = timelineData.config;

    return (
        <div className="sn-roadmap-timeline-header" >
            { renderTimeline({ config, timelineData })}
            {
                renderCalendar({
                    possibleTimeScales,
                    currentTimeScale,
                    headerHeight
                })
            }
        </div>
    );
};
