import { createCustomElement } from '@seismic/core';
import snabbdom from '@seismic/snabbdom-renderer';
import rtlBehavior from '@servicenow/behavior-rtl';
import { timelineHeaderView } from './comp-config/sn-roadmap-timeline-header-view';
import { initialState } from './comp-config/initial-state';
import { timelineActionHandlers } from './action-handlers/timeline-handlers';
import styles from './sn-roadmap-timeline-header.scss';
import { TIMELINE_SCALE_CHANGED } from './constants/constants';


createCustomElement('sn-roadmap-timeline-header', {
    renderer: { type: snabbdom },
    view: timelineHeaderView,
    initialState: initialState,
    properties: {
        locale: {
            default: 'en',
            schema: {
                type: 'string'
            }
        },
        timelineHeaderData: {
            default: {},
            schema: {
                type: 'object',
                properties: {
                    calendarTimeScales: {
                        default: [],
                        schema: {
                            type: 'array'
                        }
                    },
                    timelineData: {
                        default: [],
                        schema: {
                            type: 'array',
                            items: {
                                type: 'object',
                                properties: {
                                    calendarScale: {
                                        type: 'string'
                                    },
                                    startDateTime: {
                                        type: 'string'
                                    },
                                    endDateTime: {
                                        type: 'string'
                                    },
                                    displayValue: {
                                        type: 'string'
                                    },
                                    parentId: {
                                        type: 'string'
                                    },
                                    topParentLabel: {
                                        type: 'string'
                                    },
                                    id: {
                                        type: 'string'
                                    },
                                    parent: {
                                        type: 'string'
                                    }
                                }
                            }
                        }
                    }
                }
            }
        },
        defaultTimeScale: {
            default: '',
            schema: {
                type: 'string'
            }
        },
        dateTimeFormat: {
            default: '',
            schema: {
                type: 'string'
            }
        },
        startDateTime: {
            default: '',
            schema: {
                type: 'string'
            }
        },
        endDateTime: {
            default: '',
            schema: {
                type: 'string'
            }
        },
        recalculateScale: {
            schema: {
                type: 'boolean'
            }
        },
        hostWidth: {
            schema: {
                type: 'number'
            }
        }
    },
    behaviors: [
        { behavior: rtlBehavior }
    ],
    actionHandlers: timelineActionHandlers,
    styles: styles,
    dispatches: {
    /**
	 *Dispatched when calendar zoom level is changed
     * @type {{ scaleData:  timelineData}}
	 */
        [TIMELINE_SCALE_CHANGED]: {}
    }

});
