import { TIMELINE_SCALE_CHANGED, TIMELINE_HEADER_SCALE_CHANGED } from '../../constants';
import { hasGridBodyVertialScroll, getBrowserScrollbarWidth, getTimelineContainerWidth } from '../../util';
import { getUtcFormat } from '../../date-util';

export const timelineActionHandlers = {
    [TIMELINE_HEADER_SCALE_CHANGED]: ({
        state: { componentHost },
        action,
        updateState,
        dispatch
    }) => {
        const { payload } = action;
        const updatedStartDateTimeDisValue = payload.startDateTime;
        const updatedEndDateTimeDisValue = payload.endDateTime;
        const updatedStartDateTimeValue = getUtcFormat(updatedStartDateTimeDisValue);
        const updatedEndtDateTimeValue = getUtcFormat(updatedEndDateTimeDisValue);

        const updatedTimelineData = {
            ...payload,
            startDateTime: { value: updatedStartDateTimeValue, displayValue: updatedStartDateTimeDisValue },
            endDateTime: { value: updatedEndtDateTimeValue, displayValue: updatedEndDateTimeDisValue }
        };

        action.stopPropagation();
        if (componentHost && componentHost.shadowRoot) {
            updateState({
                timelineData: updatedTimelineData,
                timelineHeaderContainerWidth: getTimelineContainerWidth(componentHost),
                scrollbarWidth: getBrowserScrollbarWidth(componentHost),
                hasBodyVertialScroll: hasGridBodyVertialScroll(componentHost)
            });
        } else {
            updateState({
                timelineData: updatedTimelineData
            });
        }
        dispatch(TIMELINE_SCALE_CHANGED, payload.currentTimeScale);
    }
};
