import DateHelper from './data-moment-helper';
import { TIME_UNITS, TIME_UNIT_FORMAT } from '../constants/constants';

const {
    SECOND,
    DAY,
    YEAR
} = TIME_UNITS;

const {
    getDateTimeFormattedValue,
    getNextUnit,
    getDate,
    getEndOfUnit,
    getFormattedValue,
    getStartOfUnit,
    getDiffOfUnit
} = DateHelper;

export const getStartDayOfUnit = ({
    startDateTime,
    bottomLeveUnit
}) => {
    return getDateTimeFormattedValue(
        getStartOfUnit(startDateTime, bottomLeveUnit)
    );
};

export const getEndDayOfUnit = ({
    endDateTime,
    bottomLeveUnit
}) => {
    return getDateTimeFormattedValue(
        getEndOfUnit(endDateTime, bottomLeveUnit)
    );
};

export const getTimelineDuration = ({
    updatedStartDateTime,
    updatedEndDateTime,
    bottomScaleItem
}) => {
    return Math.ceil(
        getDiffOfUnit(
            updatedStartDateTime,
            updatedEndDateTime,
            bottomScaleItem.config.bottom.rangeUnit
        )
    );
};

export const getCalculatedBottomWidth = ({
    updatedStartDateTime,
    updatedEndDateTime,
    bottomScaleItem,
    visibleWidth
}) => {
    let unitWidth = bottomScaleItem.config.bottom.width;
    const actualUnits = getTimelineDuration({
        updatedStartDateTime,
        updatedEndDateTime,
        bottomScaleItem
    });
    const totalWidth = unitWidth * actualUnits;
    if (totalWidth < visibleWidth) {
        unitWidth = Number((visibleWidth / actualUnits).toFixed(2));
    } else if (totalWidth > visibleWidth) {
        const moreWidth = totalWidth - visibleWidth;
        if (moreWidth < unitWidth) {
            unitWidth -= moreWidth / actualUnits;
        }
    }

    return unitWidth;
};

export const getNextStartDateTime = (date, unit, count = 1) => {
    return getNextUnit(
        getEndOfUnit(
            date,
            unit
        ),
        SECOND,
        count
    );
};

export const getIntervalsOfUnit = (start, end, unit, format) => {
    const _end = getDate(end);
    const result = [];

    let current = getDate(start);
    let next;

    while (current <= _end) {
        next = getNextUnit(
            getStartOfUnit(
                current,
                unit
            ),
            unit
        );

        result.push(getFormattedValue(current, format));

        current = next;
    }

    return result;
};

export const getTopLevelWithBottomLevel = ({
    start,
    end,
    config,
    cache
}) => {
    const {
        bottomRangeUnit,
        bottomLevelFormat,
        topLevelFormat,
        bottomScaleWidth,
        topRangeUnit
    } = config;
    const fStartDateTime = getDateTimeFormattedValue(start);
    const fEndDateTime = getDateTimeFormattedValue(end);
    let bottomLevel;

    const CACHE_FORMAT = TIME_UNIT_FORMAT[bottomRangeUnit];
    const bottomRangeKey = `${start.format(CACHE_FORMAT)}${end.format(CACHE_FORMAT)
    }${bottomRangeUnit === DAY && topRangeUnit === YEAR ? start.isLeapYear(): ''}`;

    // Use cached value if exists
    if (Object.prototype.hasOwnProperty.call(cache, bottomRangeKey)) {
        bottomLevel = cache[bottomRangeKey];
    } else {
        // Process the intervals if not exists in the cache
        bottomLevel = getIntervalsOfUnit(
            fStartDateTime,
            fEndDateTime,
            bottomRangeUnit,
            bottomLevelFormat
        );
        // store processed intervals in the cache
        cache[bottomRangeKey] = bottomLevel;
    }

    return {
        key: `${fStartDateTime}|${fEndDateTime}`,
        topLevel: {
            displayValue: getFormattedValue(start, topLevelFormat),
            width: bottomLevel.length * bottomScaleWidth
        },
        bottomLevel
    };
};

export const getScaleData = ({
    updatedStartDateTime,
    updatedEndDateTime,
    updatedScaleData: {
        config: {
            top: {
                rangeUnit: topRangeUnit,
                format: topLevelFormat
            },
            bottom: {
                rangeUnit: bottomRangeUnit,
                format: bottomLevelFormat,
                width: bottomScaleWidth
            }
        }
    }
}) => {
    const _endDate = getDate(updatedEndDateTime);
    const cache = {};
    const config = {
        topRangeUnit,
        topLevelFormat,
        bottomRangeUnit,
        bottomLevelFormat,
        bottomScaleWidth
    };

    const topLevelScales = [];
    let totalWidth = 0;
    const bottomLevelScales = [];
    let current = getDate(updatedStartDateTime);
    let topLevelEnd;
    let scale;
    let next;

    while (current <= _endDate) {
        topLevelEnd = getEndOfUnit(
            current,
            topRangeUnit
        );

        next = getNextStartDateTime(
            current,
            topRangeUnit
        );

        topLevelEnd = topLevelEnd > _endDate ? _endDate : topLevelEnd;
        scale = getTopLevelWithBottomLevel({
            start: current,
            end: topLevelEnd,
            config,
            cache
        });
        topLevelScales.push(scale.topLevel);
        bottomLevelScales.push.apply(bottomLevelScales, scale.bottomLevel);
        totalWidth += scale.topLevel.width;
        current = next;
    }

    return {
        totalWidth,
        topLevelScales,
        bottomLevelScales
    };
};

