

import { actionTypes } from '@seismic/core';
const { COMPONENT_DOM_READY, COMPONENT_DISCONNECTED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

import { handleScaleChange, initializeComponent, onDisconnected } from '../comp-config/timeline-effect';
import { SELECTED_ITEMS_SET } from '../constants/constants';


export const timelineActionHandlers = {
    [COMPONENT_DOM_READY]: {
        effect: initializeComponent
    },

    [COMPONENT_DISCONNECTED]: {
        effect: onDisconnected
    },

    [COMPONENT_PROPERTY_CHANGED]: {
        effect(params) {
            if (params.action.payload.name === 'recalculateScale' || params.action.payload.name === 'hostWidth') {
                handleScaleChange(params);
            }
        }
    },

    [SELECTED_ITEMS_SET]: {
        stopPropagation: false,
        effect(params) {
            handleScaleChange(params);
        }
    }
};
