import { createCustomElement } from '@seismic/core';
import snabbdom from '@seismic/snabbdom-renderer';
import { roadmapView } from './comp-config/view';
import actionHandlers from './comp-config/action-handlers';
import { initialRoadMapState } from './comp-config/initial-state';
import actions from './comp-config/actions';
import rtlBehavior from '@servicenow/behavior-rtl';
import resizeBehavior from '@servicenow/behavior-resize';
import styles from './styles/index.scss';
import eventHandlers from './comp-config/event-handlers/event-handlers';

/**
 * An interactive component that lets users plan and track their roadmap intuitively.
 *
 * ```
 * <sn-roadmap
        config={}
        roadmapItems={}
        roadmapItemMetadata={}
        settings={}
        startDateTime = {}
        endDateTime = {}
        >
    </sn-roadmap>
 * ```
 *
 * @seismicElement sn-roadmap
 * @summary Lets users view, edit, and plan scheduled and unscheduled roadmap items.
 */
createCustomElement('sn-roadmap', {
    renderer: { type: snabbdom },
    view: roadmapView,
    initialState: {
        config: null,
        ...initialRoadMapState
    },
    dispatches: {
        /**
         * Dispatched when a user updates a roadmap item.
         * @type {{ roadmapItem: object }}
         */
        'SN_ROADMAP#ROADMAP_ITEM_UPDATED': {},
        /**
         * Dispatched when a user selects a roadmap item.
         * @type {{ roadmapItem: object }}
         */
        'SN_ROADMAP#ROADMAP_ITEM_SELECTED': {},
        /**
         * Dispatched when a user updates roadmap preferences.
         * @type {{ field: string, value: string }}
         */
        'SN_ROADMAP#SETTINGS_UPDATED': {},
        /**
         * Dispatched when a user changes a timeline calendar timeline scale.
         * @type {{ timelineData: object }}
         */
        'SN_ROADMAP#TIMELINE_SCALE_CHANGED': {},
        /**
         * Dispatched when a user clicks a sidebar button.
         * @type {{ payload: object }}
         */
        'SN_ROADMAP#SIDEBAR_BUTTON_CLICKED': {},
        /**
         * Dispatched when a user toggles a side panel.
         * @type {{}}
         */
        'SN_ROADMAP#SIDEPANEL_TOGGLED': {}
    },
    slots: {
        /**
        * Adds custom actions in the header.
        */
        'sn-roadmap-header-actions': {},
        /**
        * Adds content to the side panel.
        */
        'sn-roadmap-item-details': {},
        'sn-roadmap-custom-panel-slot1': {},
        'sn-roadmap-custom-panel-slot2': {}
    },
    properties: {
        /**
         * Defines the configuration for the roadmap:
         * - `showHeader`: Boolean. When true, shows the header. Default: false.
         * - `header`: Properties of the header.<br/>
         * &nbsp;&nbsp;-- `showHeaderActionSlot`: Boolean. Shows header slot with slot name
         * sn-roadmap-header-actions. Default: false.
         * - `settingsPanel`: Side panel settings.<br/>
         * &nbsp;&nbsp;-- `hideColorBySelector`: Boolean. Hides color by selector. Default: false.<br/>
         * &nbsp;&nbsp;-- `hideGroupBySelector`: Boolean. Hides group by selector. Default: false.<br/>
         * &nbsp;&nbsp;-- `hideMetricsSelector`: Boolean. Hides metrics selector. Default: false.
         * @type {object}
         */
        config: {
            default: {},
            schema: {
                type: 'object',
                properties: {
                    showHeader: {
                        type: 'boolean'
                    },
                    header: {
                        schema: {
                            type: 'object',
                            properties: {
                                showHeaderActionSlot: {
                                    type: 'boolean'
                                }
                            }
                        }
                    },
                    settingsPanel: {
                        schema: {
                            type: 'object',
                            properties: {
                                hideColorBySelector: {
                                    type: 'boolean'
                                },
                                hideGroupBySelector: {
                                    type: 'boolean'
                                },
                                hideMetricsSelector: {
                                    type: 'boolean'
                                }
                            }
                        }
                    }
                }
            }
        },
        /**
         * Defines the title of the roadmap.
         * @type {string}
         */
        headerTitle: {
            default: '',
            schema: {
                type: 'string'
            }
        },
        /**
         * Enables drag and drop and resizing of the bar.
         * @type {boolean}
         */
        canEditBar: {
            default: false,
            schema: {
                type: 'boolean'
            }
        },
        /**
        * Defines the user preferences in the Settings > Personalize panel:
        * - groupBy: Attributes selected in the group by field.
        * - colorBy: Attribute selected in the color by field.
        * - showItemOwner: Shows item owner on the bar.
        * - metrics: Attributes selected to be displayed on the bar.
        * @type {object}
        */
        settings: {
            default: { groupBy: [], colorBy: '', metrics: [], showItemOwner: false },
            schema: {
                type: 'object',
                properties: {
                    groupBy: {
                        type: 'array',
                        minItems: 0,
                        maxItems: 2,
                        items: {
                            type: 'string'
                        }
                    },
                    colorBy: {
                        type: 'string'
                    },
                    showItemOwner: {
                        type: 'boolean'
                    },
                    metrics: {
                        type: 'array',
                        minItems: 0,
                        maxItems: 3,
                        items: {
                            type: 'string'
                        }
                    }
                }
            }
        },
        /**
        * Shows current date line in roadmap timeline.
        * @type {boolean}
        */
        showCurrentDateLine: {
            default: true,
            schema: {
                type: 'boolean'
            }
        },
        /**
        * Array of roadmap items. Each object has the following properties:
        * - id : Unique identifier of the roadmap item. Can be a Sys ID from the table record.
        * - name : Name of the roadmap item; displayed on the timeline bar.
        * - startDate : Start date of the roadmap item in the format of the dateFormat property.
        * - endDate : End date of the roadmap item in the format of the dateFormat property.
        * - avatar : Avatar of the assignee of the roadmap item; displayed on the timeline bar.
        * @type {[]}
        */
        roadmapItems: {
            default: [],
            schema: {
                type: 'array',
                items: {
                    type: 'object'
                }
            }
        },
        /**
        * Metadata of roadmap item fields. It is an object with following attributes. Every object has name and label attributes.
        * - startDateField: The name and label of the field used as the start date.
        * - endDateField: The name and label of the field used as the end date.
        * - itemNameField: The name and label of the field used as the item name.
        * - itemOwnerField: The name and label of the field used as the item owner.
        * - groupableFields: Array of fields with name and label used for groupable fields.
        * - colorableFields: Array of fields with name and label used for colorable fields.
        * - metricsFields: Array of fields with name and label used for metrics fields.
        * @type {[]}
        */
        roadmapItemMetadata: {
            default: { startDateField: { name: '', label: '' }, endDateField: { name: '', label: '' },
                itemNameField: { name: '', label: '' }, itemOwnerField: { name: '', label: '' },
                groupableFields: [], colorableFields: [], metricsFields: [] },
            schema: {
                type: 'object',
                properties: {
                    startDateField: {
                        schema: {
                            type: 'object',
                            properties: {
                                name: {
                                    type: 'string'
                                },
                                label: {
                                    type: 'string'
                                }
                            }
                        }
                    },
                    endDateField: {
                        default: '',
                        schema: {
                            type: 'object',
                            properties: {
                                name: {
                                    type: 'string'
                                },
                                label: {
                                    type: 'string'
                                }
                            }
                        }
                    },
                    itemNameField: {
                        default: '',
                        schema: {
                            type: 'object',
                            properties: {
                                name: {
                                    type: 'string'
                                },
                                label: {
                                    type: 'string'
                                }
                            }
                        }
                    },
                    itemOwnerField: {
                        default: '',
                        schema: {
                            type: 'object',
                            properties: {
                                name: {
                                    type: 'string'
                                },
                                label: {
                                    type: 'string'
                                }
                            }
                        }
                    },
                    groupableFields: {
                        default: [],
                        schema: {
                            type: 'array',
                            items: {
                                type: 'object',
                                properties: {
                                    name: {
                                        type: 'string'
                                    },
                                    label: {
                                        type: 'string'
                                    }
                                }
                            }
                        }
                    },
                    colorableFields: {
                        default: [],
                        schema: {
                            type: 'array',
                            items: {
                                type: 'object',
                                properties: {
                                    name: {
                                        type: 'string'
                                    },
                                    label: {
                                        type: 'string'
                                    }
                                }
                            }
                        }
                    },
                    metricsFields: {
                        default: [],
                        schema: {
                            type: 'array',
                            items: {
                                type: 'object',
                                properties: {
                                    name: {
                                        type: 'string'
                                    },
                                    label: {
                                        type: 'string'
                                    }
                                }
                            }
                        }
                    }
                },
                required: ['startDateField', 'endDateField', 'itemNameField']
            }
        },
        /**
        * Roadmap's start date and it is expected in the format of the dateFormat property, if provided.
        * Otherwise, defaults to YYYY-MM-DD HH:mm:ss format for value and displayValue.
        *
        * @type {object}
        */
        startDateTime: {
            default: {},
            schema: {
                type: 'object',
                properties: {
                    value: {
                        type: 'string'
                    },
                    displayValue: {
                        type: 'string'
                    }
                }
            }
        },
        /**
        * Roadmap's end date and it is expected in the format of the dateFormat property, if provided.
        * Otherwise, defaults to YYYY-MM-DD HH:mm:ss format for value and displayValue.
        *
        * @type {object}
        */
        endDateTime: {
            default: {},
            schema: {
                type: 'object',
                properties: {
                    value: {
                        type: 'string'
                    },
                    displayValue: {
                        type: 'string'
                    }
                }
            }
        },
        /**
        * Defines the configuration for a custom calendar type.
        *
        * @type {object}
        */
        timelineHeaderData: {
            default: { calendarTimeScales: [], timelineData: [] },
            schema: {
                type: 'object',
                properties: {
                    calendarTimeScales: {
                        type: 'array'
                    },
                    timelineData: {
                        type: 'array'
                    }
                }
            }
        },
        /**
        * Timescale to be seleced by default when the roadmap is initialized.
        * Note: For a custom calendar type, this value must match with one of the values defined
        * for the calendarTimeScales of timelineHeaderData property.
        * For a regular calendar, the options are monthAndQuarter, quarterAndYear, and year.
        * @type {string}
        */
        defaultTimeScale: {
            default: '',
            schema: {
                type: 'string'
            }
        },
        /**
        * Datetime format of the roadmap item's dates.
        * @type {object}
        */
        dateFormat: {
            default: { valueFormat: 'YYYY-MM-DD HH:mm:ss', displayValueFormat: 'YYYY-MM-DD HH:mm:ss' },
            schema: {
                type: 'object',
                properties: {
                    valueFormat: {
                        type: 'string'
                    },
                    displayValueFormat: {
                        type: 'string'
                    }
                }
            }
        },
        /** 
	 * Timezone in which the roadmap dates are defined.
         * @type {string}
         */
        timezone: {
            default: 'GMT',
            schema: {
                type: 'string'
            }
        },
        /** 
	 * Locale in which the roadmap labels display.
         * @type {string}
         */
        locale: {
            default: 'en',
            schema: {
                type: 'string'
            }
        },
        /**
        * When true, opens the last opened side panel when component re-renders.
        * @type {boolean}
        */
        showSidePanel: {
            default: false,
            schema: {
                type: 'boolean'
            }
        },
        /**
         * Contextual side bar action list. An array describing contextual side bar
         * actions. Each object has following properties:
         * - `id`: Unique identifier of the action.
         * - `icon`: Icon of the action.
         * - `label`: Label of the action.
         * - `slotName`: Slot name in which the action content renders.<br/>
         * There are five side bar actions provided by the component. The first three of these actions 
         * have fixed icons. You can configure the icons for the remaining two actions. 
         * Clicking on any of these icons opens a side panel.<br/>
         * 1. Settings: Content in this side panel is provided by the UI component.
         * 2. Roadmap Details: Clicking this icon opens the Roadmap details modal.
         * 3. Roadmap Item Details: Content in this side panel can be configured in the
         * sn-roadmap-item-details slot.<br/>
         * 4. Unscheduled Items: Content in this side panel provided by the UI component.<br/>
         * 5. Custom Action 1: Icon can be configured on the side bar. Corresponding side
         * panel content can be configured in the sn-roadmap-custom-panel-slot1 slot.<br/>
         * 6. Custom Action 2: Icon can be configured on the side bar. Corresponding side
         * panel content can be configured in the sn-roadmap-custom-panel-slot2 slot.<br/>
         * @type {Array<SidebarItem>}
         */
        sideBarActionItems: {
            default: [],
            schema: {
                type: 'array',
                items: {
                    type: 'object',
                    properties: {
                        icon: { type: 'string' },
                        id: { type: 'string' },
                        label: { type: 'string' },
                        slotName: { type: 'string' }
                    },
                    additionalProperties: true
                }
            }
        }
    },
    behaviors: [
        { behavior: rtlBehavior },
        {
            behavior: resizeBehavior,
            options: { timer: { type: 'debounce', wait: 200 } }
        }
    ],
    eventHandlers,
    styles: styles,
    actions: actions,
    actionHandlers: actionHandlers.allActionHandlers
});
