import './sn-roadmap-item-popover-content';
import { ifJsx } from '../../util'; //eslint-disable-line
import './sn-roadmap-colorby-legend-popover/sn-roadmap-colorby-legend-popover';
import { RTL, LTR } from '../../constants/';

export function roadmapItemPopover(popoverData, target, isRTL) {
    return (
        <now-popover interaction-type="none" manageOpened={true} opened={target !== null}
            positions={[
                { target: 'bottom-center', content: 'top-center' },
                { target: 'top-center', content: 'bottom-center' },
                { target: 'center-start', content: 'center-end' },
                { target: 'center-end', content: 'center-start' },
                { target: 'bottom-start', content: 'top-start' },
                { target: 'bottom-end', content: 'top-end' },
                { target: 'top-start', content: 'bottom-start' },
                { target: 'top-end', content: 'bottom-end' }
            ]}
            hook-insert={({ elm }) => {
                elm.positionTarget = target;
            }}
            hook-update={({ elm }) => {
                elm.positionTarget = target;
            }}>
            {ifJsx(target, <sn-roadmap-item-popover-content slot="content" data={popoverData} dir={isRTL ? RTL : LTR}/>)}
        </now-popover>
    );
}

export function colorByLegendPopover({ colorByLegendTargetRef,
    colorMap,
    colorByLbl,
    openColorByLegendPopover,
    colorByFocusFirstEl }) {
    return (
        <now-popover
            interactionType="dialog"
            positions={['top-end bottom-end']}
            hide-tail="true"
            opened={openColorByLegendPopover}
            manageOpened="true"
            positionTarget={colorByLegendTargetRef}>
            <sn-roadmap-colorby-legend-popover parent={colorByLegendTargetRef}
                colorBy={colorByLbl}
                colorMap={colorMap}
                focusFirstChild={colorByFocusFirstEl}
                slot="content" />
        </now-popover>
    );
}
