import '@servicenow/now-heading';
import { ifJsx } from '../../util';
import isEmpty from 'lodash/isEmpty';
import { ARIA_ACTION_GROUP } from '../../constants';

export const renderHeader = (
    {
        header: {
            showHeaderActionSlot
        }
    },
    headerTitle
) => {
    return (
        <div className = "sn-roadmap-header" aria-label={headerTitle}>
            <div className="sn-roadmap-header-title-container">
                { ifJsx(!isEmpty(headerTitle),
                    <now-heading level="1" label={headerTitle} className="main-title" variant="header-primary" wont-wrap hasNoMargin={true}/>
                )}
            </div>
            { ifJsx(showHeaderActionSlot,
                <div className="sn-roadmap-header-actions-container" aria-label={ARIA_ACTION_GROUP}>
                    <slot name="sn-roadmap-header-actions"></slot>
                </div>
            )}
        </div>
    );
};
