import { RESIZE, KEYDOWN, KEYS, ONMOUSEDOWN } from '../../constants';
import { hasGridBodyVertialScroll, getBrowserScrollbarWidth, getTimelineContainerWidth, toggleFocusRing } from '../../util';

export default [{
    events: [RESIZE],
    effect({
        action: {
            payload: { host }
        },
        updateState
    }) {
        updateState({
            timelineHeaderContainerWidth: getTimelineContainerWidth(host),
            scrollbarWidth: getBrowserScrollbarWidth(host),
            hasBodyVertialScroll: hasGridBodyVertialScroll(host)
        });
    },
    target: window
},
{
    events: [KEYDOWN],
    effect({ action: {
        payload: { event, host }
    } }) {
        if (event.keyCode === KEYS.TAB) {
            setTimeout(()=> toggleFocusRing(host.shadowRoot.activeElement, false), 100);
        }
    },
    target: document
},
{
    events: [ONMOUSEDOWN],
    effect({ state, updateState }) {
        const { openColorByLegendPopover } = state;
        if (openColorByLegendPopover) {
            updateState({ openColorByLegendPopover: false });
        }
    },
    target: document
}
];
