import { KEYS, LEAF_GROUP_CLASS, BAR_CLASS, ROADMAP_BODY_CLASS, ROWS_WRAPPER_CLASS,
    ROW_CLASS } from '../../constants/roadmap-constants';
import { getParentNodeByClass, toggleFocusRing, hasClass } from '../../util';

function isGroup(element) {
    return hasClass(element, LEAF_GROUP_CLASS);
}

function isBodyContainer(element) {
    return hasClass(element, ROADMAP_BODY_CLASS);
}

function getBodyContainer(activeEl) {
    return getParentNodeByClass(activeEl, ROADMAP_BODY_CLASS);
}

function getRowsContainer(activeEl) {
    const body = getBodyContainer(activeEl);

    return body.querySelector(`[class=${ROWS_WRAPPER_CLASS}]`);
}

function setGroupFocus(groupEl) {
    if (groupEl) {
        groupEl.focus();
        toggleFocusRing(groupEl, false);
    }
}

function setFocusToBodyFirstElement(bodyEl) {
    const firstGroupEl = bodyEl.querySelector(`[class=${LEAF_GROUP_CLASS}]`);
    if (firstGroupEl) {
        setGroupFocus(firstGroupEl);

        return;
    }
    const firstBar = bodyEl.querySelector(`[class=${BAR_CLASS}]`);
    if (firstBar) {
        firstBar.focus();
    }
}

function getNextSibling(elem, cssClass) {
    let sibling = elem.nextElementSibling;

    if (!cssClass) {
        return sibling;
    }

    while (sibling) {
        if (hasClass(sibling, cssClass)) {
            return sibling;
        }
        sibling = sibling.nextElementSibling;
    }
}

function getPreviousSibling(elem, cssClass) {
    let sibling = elem.previousElementSibling;

    if (!cssClass) {
        return sibling;
    }

    while (sibling) {
        if (hasClass(sibling, cssClass)) {
            return sibling;
        }
        sibling = sibling.previousElementSibling;
    }
}

function moveFocusFromAGroup(groupEl, keyCode) {
    switch (keyCode) {
        case KEYS.UP: {
            const group = getPreviousSibling(groupEl, LEAF_GROUP_CLASS);

            setGroupFocus(group);
            break;
        }
        case KEYS.DOWN: {
            const group = getNextSibling(groupEl, LEAF_GROUP_CLASS);

            setGroupFocus(group);
            break;
        }
        case KEYS.RIGHT: {
            const rowsContainer = getRowsContainer(groupEl);
            const row = rowsContainer.querySelector(`[groupByValues="${groupEl.getAttribute('groupByValues')}"]`);
            if (row) {
                row.firstChild.focus();
            }
            break;
        }
    }
}

function getGroupElement(row, body) {
    const groups = row.getAttribute('groupByValues');

    return body.querySelector(`[class=${LEAF_GROUP_CLASS}][groupByValues="${groups}"]`);
}

function moveFocusFromABar(activeBar, keyCode) {
    const body = getBodyContainer(activeBar);
    const row = getParentNodeByClass(activeBar, ROW_CLASS);

    switch (keyCode) {
        case KEYS.UP: {
            const previousRow = row.previousElementSibling;
            if (previousRow) {
                previousRow.lastChild.focus();
            } else {
                const groupEl = getGroupElement(row, body);

                moveFocusFromAGroup(groupEl, KEYS.UP);
            }
            break;
        }
        case KEYS.DOWN: {
            const nextRow = row.nextElementSibling;
            if (nextRow) {
                nextRow.firstChild.focus();
            } else {
                const groupEl = getGroupElement(row, body);

                moveFocusFromAGroup(groupEl, KEYS.DOWN);
            }
            break;
        }
        case KEYS.RIGHT: {
            // is the bar the last bar of a Group
            if (!activeBar.nextElementSibling && !row.nextElementSibling) {
                const groupEl = getGroupElement(row, body);

                // set focus to next group
                moveFocusFromAGroup(groupEl, KEYS.DOWN);
                break;
            }
            if (activeBar.nextElementSibling) {
                activeBar.nextElementSibling.focus();
                break;
            }
            const nextRow = row.nextElementSibling;
            if (nextRow) {
                nextRow.firstChild.focus();
            }
            break;
        }
        case KEYS.LEFT: {
            // is the bar the first bar of a Group
            if (!activeBar.previousElementSibling && !row.previousElementSibling) {
                const groupEl = getGroupElement(row, body);

                setGroupFocus(groupEl);
                break;
            }
            if (activeBar.previousElementSibling) {
                activeBar.previousElementSibling.focus();
                break;
            }
            const previousRow = row.previousElementSibling;
            if (previousRow) {
                previousRow.lastChild.focus();
            }
            break;
        }
    }
}

export function moveFocusInBody(event) {
    if ([KEYS.UP, KEYS.DOWN, KEYS.RIGHT, KEYS.LEFT, KEYS.HOME, KEYS.END].indexOf(event.keyCode) === -1 ||
    event.shiftKey) {
        return;
    }
    event.preventDefault();
    event.stopPropagation();
    const activeEl = event.target;
    if (isBodyContainer(activeEl)) {
        setFocusToBodyFirstElement(activeEl);
    } else if (isGroup(activeEl)) {
        moveFocusFromAGroup(activeEl, event.keyCode);
    } else {
        moveFocusFromABar(activeEl, event.keyCode);
    }
}


