import { BAR_COLORS, BAR_LARGE_HEIGHT, BAR_COMPACT_HEIGHT, GROUP_BY_LIMIT, BAR_MAX_METRICS, NEUTRAL6_COLOR, EMPTY_GROUP, EMPTY_KEY } from '../constants';
import { defaultSettings } from './default-state';
import isEmpty from 'lodash/isEmpty';


export const getColorMap = (scheduledItems, unscheduledItems, colorBy) => {
    const colorMap = new Map();

    if (isEmpty(colorBy)) {
        return colorMap;
    }
    let colorIndex = 0;
    let showEmptyLegend = false;
    const addValueToColorMap = (item, hasScheduledItem) => {
        const fieldValue = item[colorBy].value;
        if (!colorMap.has(fieldValue)) {
            const fieldDisplayValue = item[colorBy].displayValue;

            colorMap.set(fieldValue, { value: fieldValue, displayValue: fieldDisplayValue, color: NEUTRAL6_COLOR, hasScheduledItem });
        }
    };

    scheduledItems.forEach(item => {
        if (isEmpty(item[colorBy]) || isEmpty(item[colorBy].value)) {
            showEmptyLegend = true;

            return;
        }
        addValueToColorMap(item, true);
    });
    unscheduledItems.forEach(item => {
        if (isEmpty(item[colorBy]) || isEmpty(item[colorBy].value)) {
            return;
        }
        addValueToColorMap(item, false);
    });

    const sortedMap = new Map([...colorMap.entries()].sort(function(item1, item2) {
        let val1 = item1[1].displayValue;
        let val2 = item2[1].displayValue;
        if (val1 === val2) {
            val1 = item1[1].value;
            val2 = item2[1].value;
        }
        if (val1 > val2) {
            return 1;
        } else if (val1 < val2) {
            return -1;
        }

        return 0;
    }));

    sortedMap.forEach((value, key, map) => {
        map.set(key, { ...map.get(key), color: BAR_COLORS[colorIndex++] });
        if (colorIndex >= BAR_COLORS.length) {
            colorIndex = 0;
        }
    });

    if (showEmptyLegend) {
        sortedMap.set(EMPTY_KEY, { displayValue: EMPTY_GROUP, color: NEUTRAL6_COLOR, hasScheduledItem: true });
    } else {
        sortedMap.set(EMPTY_KEY, { displayValue: EMPTY_GROUP, color: NEUTRAL6_COLOR, hasScheduledItem: false });
    }

    return sortedMap;
};

export const buildMetricFields = (roadmapItemMetadata, metrics) => {
    let metricFields = roadmapItemMetadata.metricsFields||[];

    metricFields=metricFields.filter((field)=>{
        return metrics.includes(field.name);
    });

    metricFields.sort(function(a, b) {
        if (metrics.indexOf(a.name) < metrics.indexOf(b.name)) {
            return -1;
        }

        return 1;
    });

    return metricFields;
};

export const getRowHeight = (showItemOwner, metrics) => {
    return metrics.length > 0 || showItemOwner ? BAR_LARGE_HEIGHT: BAR_COMPACT_HEIGHT;
};

export const transformSettings = (settings) => {
    const updatedSettings = {
        ...defaultSettings,
        ...settings
    };
    let {
        groupBy,
        metrics
    } = updatedSettings;

    metrics = !isEmpty(metrics) && metrics.length > BAR_MAX_METRICS ? metrics.slice(0, BAR_MAX_METRICS): metrics;
    groupBy = !isEmpty(groupBy) && groupBy.length > GROUP_BY_LIMIT ? groupBy.slice(0, GROUP_BY_LIMIT): groupBy;
    const rowHeight = getRowHeight(updatedSettings.showItemOwner, metrics);

    return {
        ...updatedSettings,
        groupBy,
        metrics,
        rowHeight
    };
};
