import {
    TOOLBAR,
    SELECTOR_ITEMS_UPDATED,
    SELECTOR_ID,
    SETTINGS_UPDATED,
    TOGGLE_CLICKED,
    TOGGLE_SHOW_ITEM_OWNER,
    SELECTED_PAGE_SET,
    UNSCHEDULED_ITEM_SELECTED,
    ROADMAP_ITEM_SELECTED,
    ROADMAP_SIDEPANEL_TOGGLED
} from '../../constants';
import isEmpty from 'lodash/isEmpty';
import { getColorMap, buildMetricFields, getRowHeight } from '../settings-util';
import { getGroupedRows } from '../../plot-rows';
import { ShouldResetHorizontalScrcoll } from '../../util';
import { cancelPopover } from '../../components/bar/resizable';

export const sidePanelActionHandlers = {
    [TOOLBAR.SIDE_PANEL_CLOSED]: {
        effect: ({ state, updateState, host, dispatch }) => {
            const recalculateScale = !state.recalculateScale;
            const { panelName, lastBarSelected } = state.sidePanel;

            updateState({
                sidePanel: {
                    ...state.sidePanel,
                    display: false,
                    currentSidePanelSlotName: '',
                    sidePanelTitle: '',
                    panelName: '',
                    lastBarSelected: ''
                },
                recalculateScale,
                unscheduledItemSelected: false,
                displayPanelHeader: true
            });

            const lastActiveEl = lastBarSelected ? host.shadowRoot.querySelector(`[id="${lastBarSelected}"]`)
                :host.shadowRoot.querySelector(`[panel="${panelName}"]`);
            if (lastActiveEl) {
                lastActiveEl.focus();
                cancelPopover();
            }
            dispatch(ROADMAP_SIDEPANEL_TOGGLED, {
                display: false,
                panelName: ''
            });
        },
        stopPropagation: true
    },
    [SELECTOR_ITEMS_UPDATED]: ({
        state: { settings, properties, scheduledItems, unscheduledItems, recalculateScale },
        action: { payload },
        updateState,
        dispatch
    }) => {
        const { selectorId, value } = payload;
        let updatedSettings = {};
        switch (selectorId) {
            case SELECTOR_ID.COLOR_BY: {
                const colorBy = isEmpty(value[0]) ? '' : value[0];
                const colorMap = getColorMap([...scheduledItems], [...unscheduledItems], colorBy);

                updatedSettings = {
                    ...settings,
                    colorBy,
                    colorMap
                };
                updateState({
                    settings: updatedSettings
                });
                break;
            }
            case SELECTOR_ID.GROUP_BY: {
                const groupBy = value;
                const groupedRoadmapItems = getGroupedRows([...scheduledItems], groupBy);

                ShouldResetHorizontalScrcoll.set(true);
                updatedSettings = {
                    ...settings,
                    groupBy
                };
                updateState({
                    settings: updatedSettings,
                    groupedRoadmapItems,
                    recalculateScale: !recalculateScale
                });
                break;
            }
            case SELECTOR_ID.METRICS: {
                const { roadmapItemMetadata } = properties;
                const { showItemOwner } = settings;
                const metrics = value;
                const metricFields = buildMetricFields(roadmapItemMetadata, metrics);
                const rowHeight = getRowHeight(showItemOwner, metrics);

                updatedSettings = {
                    ...settings,
                    metricFields,
                    metrics,
                    rowHeight
                };
                updateState({
                    settings: updatedSettings
                });
                break;
            }
        }
        const { colorBy, groupBy, showItemOwner, metrics } = updatedSettings;

        dispatch(SETTINGS_UPDATED, { colorBy, groupBy, showItemOwner, metrics });
    },
    [TOGGLE_CLICKED]: ({
        state: { settings },
        action: { payload: { type, value } },
        updateState,
        dispatch
    }) => {
        switch (type) {
            case TOGGLE_SHOW_ITEM_OWNER: {
                const { colorBy, groupBy, metrics } = settings;
                const showItemOwner = value;
                const rowHeight = getRowHeight(showItemOwner, metrics);

                updateState({
                    settings: {
                        ...settings,
                        rowHeight,
                        showItemOwner
                    }
                });

                dispatch(SETTINGS_UPDATED, { colorBy, groupBy, showItemOwner, metrics });
                break;
            }
        }
    },
    [SELECTED_PAGE_SET]: {
        effect: ({
            action: { payload: { value } },
            updateState
        }) => {
            updateState({
                path: 'unscheduledItemsPanelPagination.selectedPage',
                value,
                operation: 'set'
            });
        },
        stopPropagation: true
    },
    [UNSCHEDULED_ITEM_SELECTED]: {
        effect: ({
            action: { payload },
            updateState,
            dispatch
        }) => {
            updateState({
                unscheduledItemSelected: true,
                displayPanelHeader: false
            });
            dispatch(ROADMAP_ITEM_SELECTED, payload);
        },
        stopPropagation: true
    }
};
