import { actionTypes } from '@seismic/core';
import isNumber from 'lodash/isNumber';
import isEmpty from 'lodash/isEmpty';
import {
    PILLAR_ACTIONS,
    SELECTED_ITEMS_UPDATED,
    DROPDOWN_OPEN_SET,
    DROPDOWN_PANEL_SELECTED_ITEMS_SET,
    DROPDOWN_SELECTED_ITEMS_SET,
    PILLAR_DRAG_EVENT,
    BUTTON_ICONIC_CLICKED
} from './constants';
import { setFocusItemName } from './accessibility';

const handleComponentBootstrap = ({ state, updateState, action }) => {
    const { properties: { selectedItems } } = state;

    updateState({
        componentHost: action.payload.host,
        selectedItems,
        display: true
    });
};

const handleDropdownClose = ({ updateState, state }) => {
    updateState({
        itemsDropdown: {
            targetRef: state.itemsDropdown.targetRef,
            opened: false
        }
    });
};

const handlePillarClicked = ({
    state,
    action,
    updateState,
    dispatch
}) => {
    const { payload } = action;
    const { properties: { selectorId } } = state;

    action.stopPropagation();
    switch (payload.id) {
        case PILLAR_ACTIONS.PILLAR_EDIT_ACTION: {
            const pillarId = selectorId + '-' + payload.type;
            const popoverTarget = state.componentHost.shadowRoot.querySelector(`#${pillarId}`);

            updateState({
                itemsDropdown: {
                    targetRef: popoverTarget,
                    opened: true,
                    selectedItem: payload.selectedItem,
                    index: payload.index
                }
            });
            break;
        }
        case PILLAR_ACTIONS.PILLAR_REMOVE_ACTION: {
            const { selectedItems, properties } = state;

            selectedItems.splice(payload.index, 1);
            const focusIndex = payload.index < selectedItems.length ? payload.index : selectedItems.length - 1;
            const lastFocusableItemPillarId = isEmpty(selectedItems) ? properties.selectorId + '-add-field' : properties.selectorId + '-' + selectedItems[focusIndex];

            setFocusItemName(lastFocusableItemPillarId);
            updateState({
                selectedItems: [...selectedItems]
            });
            dispatch(SELECTED_ITEMS_UPDATED, {
                selectorId: properties.selectorId,
                value: [...selectedItems]
            });
        }
    }
};

const handleFieldSelected = ({
    state: { properties, selectedItems },
    action: { payload },
    dispatch,
    updateState
}) => {
    if (selectedItems.includes(payload.items[0].name)) {
        if (isNumber(payload.index) && selectedItems[payload.index] !== payload.items[0].name) {
            selectedItems.splice(payload.index, 1);
        }
    } else if (isNumber(payload.index)) {
        selectedItems.splice(payload.index, 1, payload.items[0].name);
    } else {
        selectedItems.push(payload.items[0].name);
    }
    const selectedItemPillarId = properties.selectorId + '-' + payload.items[0].name;

    setFocusItemName(selectedItemPillarId);
    updateState({
        selectedItems: [...selectedItems]
    });
    dispatch(SELECTED_ITEMS_UPDATED, {
        selectorId: properties.selectorId,
        value: [...selectedItems]
    });
};

const handlePillarDrag = ({
    state: { properties, selectedItems },
    action: { payload },
    dispatch,
    updateState
}) => {
    const { startIndex, targetIndex } = payload;
    const movedItem = selectedItems[startIndex];

    selectedItems.splice(startIndex, 1);
    selectedItems.splice(targetIndex, 0, movedItem);
    setFocusItemName('');
    updateState({
        selectedItems: [...selectedItems]
    });
    dispatch(SELECTED_ITEMS_UPDATED, {
        selectorId: properties.selectorId,
        value: [...selectedItems]
    });
};


export const actionHandlers = {
    [actionTypes.COMPONENT_BOOTSTRAPPED]: handleComponentBootstrap,
    [DROPDOWN_OPEN_SET]: handleDropdownClose,
    [DROPDOWN_SELECTED_ITEMS_SET]: handleFieldSelected,
    [DROPDOWN_PANEL_SELECTED_ITEMS_SET]: handleFieldSelected,
    [BUTTON_ICONIC_CLICKED]: handlePillarClicked,
    [PILLAR_DRAG_EVENT]: handlePillarDrag
};
