
import { renderHeader } from '../components/header/header';
import { renderTimelineHeader } from './timeline-view';
import '@servicenow/now-dropdown';
import '@servicenow/now-tooltip';
import '@servicenow/now-illustration';
import '@servicenow/now-modal';
import isEmpty from 'lodash/isEmpty';
import { renderBody } from '../components/body/body';
import { roadmapItemPopover } from '../components/popovers/popover-util';
import { renderSideToolBar } from '../components/side-panel/toolbar';
import {
    BAR_MAX_METRICS,
    GROUP_SECTION_WIDTH,
    MISMATCHED_GROUP_BY_VALUE_HEADER,
    TIMELINE_SCROLL_REF,
    getMismatchedGroupMessage,
    GROUP_OVERRIDE_MODAL_YES,
    GROUP_OVERRIDE_MODAL_NO,
    RTL,
    LTR
} from '../constants';
import { ifJsx, getScrollSynchronizer, resetGridHorizontalScroll, ShouldResetHorizontalScrcoll } from '../util';
import { setGroupTooltipRef } from '../components/body/util';
import { getViewDate } from '../date-util';
import { renderColorByLegend } from '../components/legend/legend';

// eslint-disable-next-line no-unused-vars
import { Fragment, createRef } from '@seismic/snabbdom-renderer';
import ItemMetaData from '../item-metadata';
import { renderShortcutsModal } from '../components/body/kb-shortcuts';

const refs = {};
const getRef = (key, path) => path ? refs[path][key] : refs[key];
const setRef = (key, value, path) => {
    if (path) {
        refs[path] = Object.assign({}, refs[path], { [key]: value });
    } else {
        refs[key] = value;
    }
};
const syncScroll = getScrollSynchronizer(getRef);
const resetScroll = resetGridHorizontalScroll(getRef);

const getGroupHeaderTitle = (
    { groupBy, isRTL },
    roadmapItemMetadata,
    updateState
) => {
    let groupByFields = roadmapItemMetadata.groupableFields||[];

    groupByFields=groupByFields.filter((field)=>{
        return groupBy.includes(field.name);
    });

    groupByFields.sort(function(a, b) {
        return groupBy.indexOf(a.name) < groupBy.indexOf(b.name)?-1:1;
    });

    const temp = [...groupByFields];
    const tooltip = isRTL? temp.reverse().map((g) => g.label).join(' <- ')
        : groupByFields.map((g) => g.label).join(' -> ');

    return groupByFields.map((g, index) =>
        <Fragment>
            <span className="group-filter"
                onmouseenter={event => setGroupTooltipRef(event.target, updateState, tooltip)}
                onmouseleave={() => setGroupTooltipRef(null, updateState)}>
                {g.label}
            </span>
            { ifJsx(index < groupByFields.length -1,
                <span>
                    <now-icon icon= { isRTL ? 'arrow-left-fill' : 'arrow-right-fill' }
                        className='sn-roadmap-body-group-header-arrow' size="sm">
                    </now-icon>
                </span>)
            }
        </Fragment>);
};

export const roadmapView = (state, { dispatch, updateState }) => {
    const {
        properties: { roadmapItemMetadata, headerTitle },
        timelineData,
        popover: { target, data },
        barResize: { leftTooltipRef, rightTooltipRef, startDate, endDate },
        settings,
        config,
        groupTooltip,
        isRoadmapDateTimeValid,
        scheduledItems,
        overrideGroupModal
    } = state;
    if (!isRoadmapDateTimeValid) {
        return null;
    }
    const verticalScrollContainerRef = createRef();
    const displayStartDate = getViewDate(startDate.displayValue);
    const displayEndDate = getViewDate(endDate.displayValue);
    if (isEmpty(settings)) {
        return null;
    }
    const { showItemOwner, groupBy, isRTL } = settings;
    const { itemOwnerField }=ItemMetaData.get();
    const groupSectionWidth = GROUP_SECTION_WIDTH;
    const updatedPopoverData = !isEmpty(data) ? {
        ...data,
        itemOwner: showItemOwner ? data.itemOwner : null,
        itemOwnerField,
        metrics: data.metrics ? data.metrics.slice(0, BAR_MAX_METRICS) : []
    } : null;

    if (ShouldResetHorizontalScrcoll.get()) {
        resetScroll(TIMELINE_SCROLL_REF);
        ShouldResetHorizontalScrcoll.set(false);
    }
    const mismatchedGroupByValues = overrideGroupModal.mismatchedGroupByValues;
    let mismatchedGroupByFieldsAsString = '';
    if (!isEmpty(mismatchedGroupByValues)) {
        switch (mismatchedGroupByValues.length) {
            case 1:
                mismatchedGroupByFieldsAsString = mismatchedGroupByValues[0];
                break;
            case 2:
                mismatchedGroupByFieldsAsString = mismatchedGroupByValues[0] + ' & '+ mismatchedGroupByValues[1];
                break;
            default:
                mismatchedGroupByFieldsAsString = '';
                break;
        }
    }

    return (
        <div className="sn-roadmap-container" role="main">
            <div className="scrollbar-measure"></div>
            { config.showHeader ? renderHeader(config, headerTitle) : <Fragment />}
            <section className="sn-roadmap-main-section">
                <div className="sn-roadmap-main-section-container">
                    <div className="sn-roadmap-grid-header" style={{ height: `${timelineData.config.headerHeight}px` }}>
                        <div>
                            {
                                ifJsx(groupBy.length > 0,
                                    <div className="sn-roadmap-body-group-header" style={{ width: `${groupSectionWidth}px`, minWidth: `${groupSectionWidth}px` }}>
                                        { getGroupHeaderTitle(settings, roadmapItemMetadata, updateState)}
                                    </div>)
                            }
                        </div>
                        { renderTimelineHeader({
                            state,
                            setRef,
                            syncScroll
                        })}
                    </div>
                    <div ref={verticalScrollContainerRef} tabindex="-1" className="sn-roadmap-grid-body">
                        <div className="sn-roadmap-grid-body-container"
                            style={{ height: isEmpty(scheduledItems) ? '100%' : 'inherit' }}>
                            { ifJsx(timelineData.totalWidth > 0, () => renderBody({
                                state,
                                dispatch,
                                updateState,
                                verticalScrollContainerRef,
                                setRef,
                                syncScroll
                            }))
                            }
                            <now-modal
                                managedOpen={true}
                                opened={overrideGroupModal.display}
                                dir={isRTL ? RTL : LTR}
                                size="sm"
                                headerLabel={MISMATCHED_GROUP_BY_VALUE_HEADER}
                                content={getMismatchedGroupMessage(mismatchedGroupByFieldsAsString, mismatchedGroupByValues && mismatchedGroupByValues.length > 1)}
                                footerActions={[{ label: GROUP_OVERRIDE_MODAL_YES, variant: 'primary' },
                                    { label: GROUP_OVERRIDE_MODAL_NO, variant: 'secondary' }]}>
                            </now-modal>
                            { renderShortcutsModal(state.openKeyboardShortcuts, isRTL) }
                        </div>
                    </div>
                    <div className="sn-roadmap-legend-sticky">
                        { renderColorByLegend({ state, dispatch, updateState }) }
                    </div>
                    { roadmapItemPopover(updatedPopoverData, target, isRTL) }
                    { ifJsx(leftTooltipRef,
                        <now-tooltip style={{ pointerEvents: 'none' }} target-ref={isRTL ? rightTooltipRef : leftTooltipRef}
                            content={isRTL ? displayEndDate : displayStartDate}
                            manageOpened={true} opened={true}
                            position={['center-start center-end', 'center-end center-start', 'top-start top-end',
                                'bottom-start bottom-end', 'top-center bottom-center', 'bottom-center top-center',
                                'top-end top-start', 'bottom-end bottom-start']}/>)
                    }
                    { ifJsx(rightTooltipRef,
                        <now-tooltip style={{ pointerEvents: 'none' }} target-ref={isRTL ? leftTooltipRef : rightTooltipRef}
                            content={isRTL ? displayStartDate : displayEndDate}
                            manageOpened={true} opened={true} position={['center-end center-start', 'center-start center-end',
                                'top-end top-start', 'bottom-end bottom-start', 'top-center bottom-center',
                                'bottom-center top-center', 'top-start top-end', 'bottom-start bottom-end']}/>)
                    }
                </div>
                { renderSideToolBar({ state, dispatch }) }
            </section>
            { ifJsx(groupTooltip.ref,
                <now-tooltip target-ref={ groupTooltip.ref } content={ groupTooltip.text }
                    position={['top-center bottom-start']} opened='true'/>)
            }
        </div>
    );
};
