import find from 'lodash/find';
import cloneDeep from 'lodash/cloneDeep';
import isEmpty from 'lodash/isEmpty';
import moment from 'moment';
import { getStartDayOfUnit, getEndDayOfUnit, getCalculatedBottomWidth, getScaleData } from './timeline-data-manager';
import { TIME_UNITS } from '../constants/constants';
import DateHelper from './data-moment-helper';

const { getDiffOfUnit } = DateHelper;

const sortPeriod = (itemList, dateTimeFormat) => {
    return itemList.sort((a, b) => {
        return moment(a.startDateTime, dateTimeFormat) - moment(b.startDateTime, dateTimeFormat);
    });
};

export const getTimelineDataByDate = ({
    startDateTime,
    endDateTime,
    visibleWidth,
    currentTimeScale,
    possibleTimeScales
})=> {
    const isTopLevel = currentTimeScale === TIME_UNITS.YEAR;
    const bottomScaleItem = find(possibleTimeScales, ['id', currentTimeScale]);
    const bottomLeveUnit = bottomScaleItem.config.bottom.rangeUnit;
    // calculate real start and end date
    const updatedStartDateTime = getStartDayOfUnit({
        startDateTime,
        bottomLeveUnit
    });
    const updatedEndDateTime = getEndDayOfUnit({
        endDateTime,
        bottomLeveUnit

    });
    // calculate bottom width
    const actualBottomWidth = getCalculatedBottomWidth({
        updatedStartDateTime,
        updatedEndDateTime,
        bottomScaleItem,
        visibleWidth
    });
    const updatedScaleData = cloneDeep(bottomScaleItem);

    updatedScaleData.config.bottom.width = actualBottomWidth;
    //  get toplevelScales and bottomLevelScales data
    const newTimelineData = getScaleData({
        updatedStartDateTime,
        updatedEndDateTime,
        updatedScaleData
    });

    return {
        ...newTimelineData,
        scaleWidth: actualBottomWidth,
        scaleUnit: bottomLeveUnit,
        scaleUnitCount: 1,
        isTopLevel: isTopLevel,
        startDateTime: updatedStartDateTime,
        endDateTime: updatedEndDateTime,
        visibleWidth
    };
};

export const getTimelineDataByHeaderData = ({
    currentTimeScale,
    flatHeaderData,
    visibleWidth,
    timelineData: {
        config: {
            bottomScaleWidth
        }
    },
    possibleTimeScales,
    dateTimeFormat
}) => {
    const bottomList = sortPeriod(flatHeaderData[currentTimeScale].items, dateTimeFormat);

    const bottomParentLevel = flatHeaderData[currentTimeScale].parent;
    const bottomParentLevelList = flatHeaderData[bottomParentLevel];
    if (bottomParentLevel && !bottomParentLevelList) {
        throw new Error('Headerline data is not valid, check property timelineHeaderData');
    }
    const parentList = bottomParentLevel ? flatHeaderData[bottomParentLevel].items : [];
    const isTopLevel = !bottomParentLevel || parentList.length === 0;
    const bottomLevelScales = [];
    const topLevelScales = [];
    const childCount = {};
    const topList = [];
    let actualWidth = bottomScaleWidth;
    let totalWidth = bottomList.length * bottomScaleWidth;
    if (totalWidth < visibleWidth) {
        actualWidth = Number((visibleWidth / bottomList.length).toFixed(2));
    } else if (totalWidth > visibleWidth) {
        const moreWidth = totalWidth - visibleWidth;
        if (moreWidth < bottomScaleWidth) {
            actualWidth -= moreWidth / bottomList.length;
        }
    }
    possibleTimeScales.map(viewItem => {
        if (!flatHeaderData[viewItem.id] || flatHeaderData[viewItem.id].items.length === 0) {
            throw new Error('Headerline data is not match to availableView, check property timelineHeaderData');
        }
    });
    bottomList.map(item => {
        const parentId = item.parentId;

        bottomLevelScales.push(item.displayValue);
        if (parentId) {
            if (Object.prototype.hasOwnProperty.call(childCount, parentId)) {
                childCount[parentId] = childCount[parentId] + 1;
            } else {
                childCount[parentId] = 1;
                topList.push(parentId);
            }
        }
    });
    if (parentList && topList) {
        topList.map(parentId => {
            const parentItem = find(parentList, ['id', parentId]);
            const count = childCount[parentId];
            if (!parentItem) {
                throw new Error('Headerline data is invalid, check property timelineHeaderData');
            }

            const parentScal = {
                width: count * actualWidth,
                displayValue: parentItem.displayValue,
                parentLabel: parentItem.topParentLabel
            };

            topLevelScales.push(parentScal);
        });
        totalWidth = bottomList.length * actualWidth;
    }
    const periodStartDateTime = bottomList[0].startDateTime;
    const periodEndDate = bottomList[0].endDateTime;
    const bottomUnit = TIME_UNITS.DAY;
    const bottomUnitCount = Math.ceil(
        getDiffOfUnit(
            periodStartDateTime,
            periodEndDate,
            bottomUnit
        )
    );
    const periodEndDateTime = bottomList[bottomList.length-1].endDateTime;

    return {
        topLevelScales: topLevelScales,
        bottomLevelScales: bottomLevelScales,
        scaleWidth: actualWidth,
        scaleUnit: bottomUnit,
        scaleUnitCount: bottomUnitCount,
        isTopLevel: isTopLevel,
        totalWidth: totalWidth,
        startDateTime: periodStartDateTime,
        endDateTime: periodEndDateTime,
        visibleWidth
    };
};

export const getCustomTimelineFlatData = (timelineData)=> {
    const headerDatalist = {};
    if (!isEmpty(timelineData)) {
        timelineData.map(item => {
            if (Object.prototype.hasOwnProperty.call(headerDatalist, item.calendarScale)) {
                headerDatalist[item.calendarScale].push(item);
            } else {
                headerDatalist[item.calendarScale] = [item];
            }
        });
        const headerDataGroups = {};

        Object.keys(headerDatalist).map(scalItem => {
            const scalList = headerDatalist[scalItem];

            headerDataGroups[scalItem] = { parent: scalList[0].parent, items: scalList };
        });

        return headerDataGroups;
    }
};
