import {
    BAR_DROPPED,
    BAR_RESIZE_COMPLETED,
    BAR_RESIZE_UPDATED_REF,
    BAR_RESIZE_UPDATED_SIZE,
    BAR_DRAG_STARTED,
    BAR_DRAGGED,
    BAR_DRAG_COMPLETED,
    ACTIVE_ROW_PATH_UPDATED,
    BAR_CLICKED,
    BAR_FOCUS_CLEARED,
    BAR_POPOVER_HIDDEN
} from '../actions';
import {
    getResizedDate,
    updateGroupedRoadmapItem,
    updateDroppedItem,
    updateDroppedUnscheduledItem
} from '../../components/body/util';
import {
    ROADMAP_ITEM_UPDATED,
    ITEM_DETAILS_PANEL,
    ROADMAP_ITEM_SELECTED,
    EMPTY_KEY,
    NOW_MODAL_FOOTER_ACTION_CLICKED,
    ROADMAP_SIDEPANEL_TOGGLED
} from '../../constants';
import isEmpty from 'lodash/isEmpty';
import ItemMetaData from '../../item-metadata';

export const barActionHandlers = {
    [BAR_CLICKED]: {
        effect: ({ state: { sidePanel }, updateState, action: { payload }, dispatch }) => {
            updateState({
                sidePanel: {
                    ...sidePanel,
                    panelName: ITEM_DETAILS_PANEL,
                    currentSidePanelSlotName: '',
                    sidePanelTitle: '',
                    showEmptyImage: false,
                    display: true,
                    lastBarSelected: payload.roadmapItem.id.value
                }
            });
            dispatch(BAR_POPOVER_HIDDEN);
            dispatch(ROADMAP_ITEM_SELECTED, payload);
            dispatch(ROADMAP_SIDEPANEL_TOGGLED, {
                display: true,
                panelName: ITEM_DETAILS_PANEL
            });
        },
        stopPropagation: true
    },

    [BAR_RESIZE_UPDATED_REF]: {
        effect: ({ state, updateState, action }) => {
            const { id, leftTooltipRef, rightTooltipRef, startDate, endDate, groupByValues } = action.payload;

            updateState({
                barResize: {
                    ...state.barResize,
                    id,
                    leftTooltipRef,
                    rightTooltipRef,
                    startDate,
                    endDate,
                    groupByValues,
                    width: -2
                }
            });
        },
        stopPropagation: true
    },
    [BAR_RESIZE_UPDATED_SIZE]: {
        effect: ({ state, updateState, action }) => {
            const { width, resizer, startDate, endDate } = action.payload;
            const { timelineData } = state;
            let newStartDate = {};
            let newEndDate = {};

            switch (resizer) {
                case 'left':
                    newStartDate = getResizedDate(startDate, endDate, width, timelineData, true);
                    break;
                case 'right':
                    newEndDate = getResizedDate(startDate, endDate, width, timelineData, false);
                    break;
            }
            updateState({
                barResize: {
                    ...state.barResize,
                    width,
                    startDate: !isEmpty(newStartDate) ? newStartDate : startDate,
                    endDate: !isEmpty(newEndDate) ? newEndDate : endDate
                }
            });
        },
        stopPropagation: true
    },
    [BAR_RESIZE_COMPLETED]: {
        effect: ({ state, updateState, dispatch }) => {
            const { startDateField, endDateField }=ItemMetaData.get();
            const { barResize: { id, startDate, endDate, groupByValues }, scheduledItems, groupedRoadmapItems } = state;
            const updatedItems = [];
            let lastModifiedRoadmapItem;

            for (const item of scheduledItems) {
                if (item.id.value === id) {
                    lastModifiedRoadmapItem = {
                        ...item,
                        [startDateField.name]: startDate,
                        [endDateField.name]: endDate
                    };
                    updatedItems.push(lastModifiedRoadmapItem);
                    const updatedItemInfo = {
                        id: item.id,
                        [startDateField.name]: startDate,
                        [endDateField.name]: endDate
                    };

                    dispatch(ROADMAP_ITEM_UPDATED, { roadmapItem: updatedItemInfo });
                } else {
                    updatedItems.push(item);
                }
            }

            updateGroupedRoadmapItem(lastModifiedRoadmapItem, groupedRoadmapItems, groupByValues, groupByValues);

            updateState({
                barResize: {
                    width: -1,
                    startDate: {},
                    endDate: {},
                    leftTooltipRef: null,
                    rightTooltipRef: null,
                    id: -1
                },
                scheduledItems: updatedItems,
                groupedRoadmapItems,
                lastModifiedRoadmapItem
            });
        },
        stopPropagation: true
    },
    [BAR_DROPPED]: {
        effect: ({ state, updateState, action, dispatch }) => {
            const { settings: { colorMap, colorBy } } = state;
            const { lastModifiedRoadmapItem,
                groupedRoadmapItems,
                unscheduledItems,
                scheduledItems,
                updatedItemInfo,
                groupModalPayload } = updateDroppedItem({ state, action });

            if (isEmpty(groupModalPayload)) {
                const fieldValue =
                isEmpty(lastModifiedRoadmapItem[colorBy]) || isEmpty(lastModifiedRoadmapItem[colorBy].value) ? EMPTY_KEY : lastModifiedRoadmapItem[colorBy].value;

                if (colorMap.has(fieldValue)) {
                    colorMap.set(fieldValue, { ...colorMap.get(fieldValue), hasScheduledItem: true });
                }

                updateState({
                    scheduledItems,
                    unscheduledItems,
                    lastModifiedRoadmapItem,
                    groupedRoadmapItems,
                    dropZone: {
                        startClientX: 0,
                        dragClientX: 0,
                        start: -2,
                        barStart: 0
                    },
                    settings: {
                        ...state.settings,
                        colorMap
                    }
                });
                dispatch(ROADMAP_ITEM_UPDATED, { roadmapItem: updatedItemInfo });
            } else {
                updateState({
                    overrideGroupModal: {
                        ...groupModalPayload
                    },
                    dropZone: {
                        startClientX: 0,
                        dragClientX: 0,
                        start: -2,
                        barStart: 0
                    }
                });
            }
        },
        stopPropagation: true
    },
    [BAR_DRAG_STARTED]: {
        effect: ({ updateState, action }) => {
            const { payload: { start, startClientX } } = action;

            updateState({
                dropZone: {
                    startClientX,
                    start
                }
            });
        },
        stopPropagation: true
    },
    [BAR_DRAGGED]: {
        effect: ({ state, updateState, action }) => {
            const { payload: { dragClientX, barStart } } = action;

            if (dragClientX !== state.dragClientX) {
                updateState({
                    dropZone: {
                        ...state.dropZone,
                        dragClientX,
                        barStart
                    }
                });
            }
        },
        stopPropagation: true
    },
    [BAR_DRAG_COMPLETED]: {
        effect: ({ updateState }) => {
            updateState({
                dropZone: {
                    start: -2,
                    dragClientX: 0,
                    startClientX: 0
                }
            });
        },
        stopPropagation: true
    },
    [ACTIVE_ROW_PATH_UPDATED]: {
        effect: ({ updateState, action }) => {
            const { payload: { path, activeBarId, enableResizers } } = action;
            const updatedPath = path.map(subPath => subPath.value);

            updateState({
                activeRowPath: updatedPath,
                activeBarId: activeBarId ? activeBarId: -1,
                enableResizers
            });
        },
        stopPropagation: true
    },
    [BAR_FOCUS_CLEARED]: {
        effect: ({ updateState }) => {
            updateState({
                lastModifiedRoadmapItem: {}
            });
        },
        stopPropagation: true
    },
    [NOW_MODAL_FOOTER_ACTION_CLICKED]: {
        effect: ({ state, updateState, action, dispatch }) => {
            const { payload: { action: { label } } } = action;
            const { settings: { colorBy, colorMap } } = state;
            const isOverride = label.includes('Yes');
            const { unscheduledItems,
                scheduledItems,
                groupedRoadmapItems,
                lastModifiedRoadmapItem,
                updatedItemInfo } = updateDroppedUnscheduledItem({ isOverride, state });
            const fieldValue = isEmpty(lastModifiedRoadmapItem[colorBy]) || isEmpty(lastModifiedRoadmapItem[colorBy].value) ? EMPTY_KEY : lastModifiedRoadmapItem[colorBy].value;

            if (colorMap.has(fieldValue)) {
                colorMap.set(fieldValue, { ...colorMap.get(fieldValue), hasScheduledItem: true });
            }

            updateState({
                scheduledItems,
                unscheduledItems,
                groupedRoadmapItems,
                lastModifiedRoadmapItem,
                overrideGroupModal: {
                    display: false
                },
                settings: {
                    ...state.settings,
                    colorMap
                }
            });
            dispatch(ROADMAP_ITEM_UPDATED, { roadmapItem: updatedItemInfo });
        },
        stopPropagation: true
    }
};
