import { createCustomElement } from '@servicenow/ui-core';
import rtlBehavior from '@servicenow/behavior-rtl';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { view } from './view';
import { actionHandlers } from './actions';
import styles from './styles.scss';

createCustomElement('sn-roadmap-config-selector', {
    renderer: { type: snabbdom },
    view,
    properties: {
        draggable: {
            default: false,
            schema: {
                type: 'boolean'
            }
        },
        header: {
            default: 'Selector header',
            schema: {
                type: 'string'
            }
        },
        selectorId: {
            default: 'Selector Id',
            schema: {
                type: 'string'
            }
        },
        limit: {
            schema: {
                type: 'number'
            }
        },
        maxLimitMsg: {
            schema: {
                type: 'string'
            }
        },
        items: {
            default: [],
            schema: {
                type: 'array',
                items: {
                    type: 'object',
                    properties: {
                        name: { type: 'string' },
                        label: { type: 'string' }
                    }
                }
            }
        },
        selectedItems: {
            default: [],
            schema: {
                type: 'array',
                items: {
                    type: 'string'
                }
            }
        }
    },
    styles,
    actionHandlers,
    behaviors: [
        { behavior: rtlBehavior }
    ]
});
