// eslint-disable-next-line no-unused-vars
import { Fragment } from '@seismic/snabbdom-renderer';
import '@servicenow/now-avatar';
import {
    renderLine2,
    renderRightBarContainer,
    delayedFocus,
    highlightBar
} from './barUtils';
import { handleDragBar } from './draggable';
import { handleResizeBar, handleFocusBar } from './resizable';
import { ifJsx } from '../../util';
import {
    BAR_MIN_CONTENT_WIDTH,
    BAR_SHOW_AVATAR_WIDTH,
    BAR_LARGE_HEIGHT,
    BAR_CLASS,
    KEYS
} from '../../constants';
import { BAR_CLICKED } from '../../comp-config/actions';
import isEmpty from 'lodash/isEmpty';
import { getBarProperties, getItemMetricFieldsWithValue, getTimelineDayWidth } from '../body/util';
import ItemMetadata from '../../item-metadata';
import { getViewDate } from '../../date-util';
import { generateIcon } from '../../iconGen';

export const renderBarContent = ({
    roadmapItem,
    barProperties: { isLeftOverflow, isRightOverflow },
    metricFieldsWithValues,
    showAvatar,
    showMetric,
    showItemOwner
}) => {
    const { startDateField, endDateField, itemNameField, itemOwnerField }=ItemMetadata.get();
    const {
        avatar,
        [startDateField.name]: startDate,
        [endDateField.name]: endDate,
        [itemOwnerField.name]: itemOwner,
        [itemNameField.name]: itemName
    } = roadmapItem;

    const roadmapItemName = itemName.displayValue;

    let barAria = roadmapItemName;

    if (!isEmpty(startDate) && !isEmpty(endDate)) {
        barAria = `${barAria} ${getViewDate(startDate.displayValue)} - ${getViewDate(endDate.displayValue)}`;
    }

    return (
        <Fragment>
            <div className="sn-roadmap-bar-left-container">
                <div tabindex="-1" aria-label={barAria} className="sn-roadmap-bar-left-container-line1">{ roadmapItemName }</div>
                { ifJsx(showMetric, () => renderLine2(metricFieldsWithValues, isLeftOverflow || isRightOverflow)) }
            </div>
            {
                ifJsx(showItemOwner && showAvatar && !isEmpty(itemOwner),
                    () => renderRightBarContainer(itemOwner.displayValue, avatar.displayValue))
            }
        </Fragment>
    );
};

export const renderBar = ({
    roadmapItem,
    timelineData,
    dispatch = null,
    horizontalScrollContainerRef,
    barResize,
    groupByValues,
    lastModifiedRoadmapItemId,
    barOverlapBuffer,
    itemDetailsPanel
}) => {
    const { itemNameField, startDateField, endDateField }=ItemMetadata.get();
    const { id, [itemNameField.name]: itemName, [startDateField.name]: startDate, [endDateField.name]: endDate, settings, canEditBar, activeBarId, enableResizers } = roadmapItem;
    const { rowHeight, isRTL, showItemOwner, metricFields } = settings;
    const barProperties = getBarProperties(roadmapItem, settings, timelineData);
    const timelineWidth = timelineData.totalWidth;
    const metricFieldsWithValues = getItemMetricFieldsWithValue(roadmapItem, metricFields);
    const { color, isLeftOverflow, isRightOverflow } = barProperties;
    let { width } = barProperties;
    const left = barProperties.left + barOverlapBuffer;

    width = width - barOverlapBuffer;
    const updatedWidth = barResize && barResize.width > -1 && barResize.id === id.value ? barResize.width : width;
    const showAvatar = updatedWidth >= BAR_SHOW_AVATAR_WIDTH && rowHeight === BAR_LARGE_HEIGHT;
    const showMetric = updatedWidth > BAR_MIN_CONTENT_WIDTH && rowHeight === BAR_LARGE_HEIGHT;
    const showBarContent = updatedWidth >= BAR_MIN_CONTENT_WIDTH;
    const dayWidth = getTimelineDayWidth(timelineData);
    const isActiveBar = activeBarId === id.value;
    const barContainerCustomStyle = {
        width: width + 'px',
        backgroundColor: color,
        position: 'absolute',
        minWidth: Math.floor(dayWidth) + 'px',
        zIndex: isActiveBar ? '2000' : '1000'
    };
    if (isRTL) {
        barContainerCustomStyle.right = left + 'px';
    } else {
        barContainerCustomStyle.left = left + 'px';
    }
    const attrs = {
        tabindex: '-1',
        role: 'button',
        id: id.value
    };
    const addHighlight = id.value === itemDetailsPanel.id && itemDetailsPanel.display;
    const showLeftResizer = (!isRTL && !isLeftOverflow || isRTL && !isRightOverflow) && enableResizers && canEditBar && isActiveBar;
    const showRightResizer = (!isRTL && !isRightOverflow || isRTL && !isLeftOverflow) && enableResizers && canEditBar && isActiveBar;

    return (
        <div id={id.value}
            attrs={attrs}
            aria-label= {!showBarContent ? itemName.displayValue : ''}
            className={ `${BAR_CLASS}${isActiveBar? ' sn-roadmap-bar-box-shadow': ''}`}
            style={barContainerCustomStyle}
            onmouseenter={e => handleFocusBar({
                status: true, barElement: e.target, enableResizers: true, dispatch, roadmapItem, showItemOwner, metricFieldsWithValues, isKeyboardFocus: false })}
            onmouseleave={e => handleFocusBar({
                status: false, barElement: e.target, enableResizers: false, dispatch, isKeyboardFocus: false })}
            onfocus={e => handleFocusBar({
                status: true, barElement: e.target, enableResizers: false, dispatch, roadmapItem, showItemOwner, metricFieldsWithValues, isKeyboardFocus: true })}
            onblur={e => handleFocusBar({
                status: false, barElement: e.target, enableResizers: false, dispatch, isKeyboardFocus: true })}
            onmousedown={e => {
                if (canEditBar && !e.target.className.includes('resizer')) {
                    handleDragBar(true, startDate, endDate, e, dispatch, isRTL);
                }
            }}
            onmouseup={e => {
                if (canEditBar && !e.target.className.includes('resizer')) {
                    handleDragBar(false, startDate, endDate, e, dispatch, isRTL);
                }
            }}
            on-click={(e) => {
                e.stopPropagation();
                if (!e.target.className.includes('resizer')) {
                    dispatch(BAR_CLICKED, {
                        roadmapItem: roadmapItem
                    });
                }
            }}
            on-keydown={(e) => {
                if (e.keyCode === KEYS.ENTER) {
                    dispatch(BAR_CLICKED, {
                        roadmapItem: roadmapItem
                    });
                }
            }}
            hook-update={({ elm }) => {
                const addKeyboardFocus = elm.id === lastModifiedRoadmapItemId;
                if (addKeyboardFocus) {
                    delayedFocus(elm, dispatch);
                }
                highlightBar(elm, addHighlight);
            }}
            hook-insert={({ elm }) => {
                const addKeyboardFocus = elm.id === lastModifiedRoadmapItemId;
                if (addKeyboardFocus) {
                    delayedFocus(elm, dispatch);
                }
            }}
        >
            {
                ifJsx(isActiveBar || addHighlight, () => {
                    return <Fragment>
                        {
                            ifJsx(isLeftOverflow || isRightOverflow,
                                <Fragment>
                                    <div className="sn-roadmap-bar-border sn-roadmap-bar-border-top"/>
                                    <div className="sn-roadmap-bar-border sn-roadmap-bar-border-bottom"/>
                                </Fragment>
                            )
                        }
                        {
                            ifJsx(showLeftResizer,
                                <now-icon className="sn-roadmap-bar-resizer sn-roadmap-bar-resizer-left"
                                    icon="grid-vertical-fill"
                                    size="md"
                                    onmousedown={e =>
                                        handleResizeBar(true, isRTL ? 'resizer-right' : 'resizer-left', id,
                                            startDate, endDate, e, isRTL, dispatch, horizontalScrollContainerRef, timelineWidth)
                                    }
                                    onmouseup={() => handleResizeBar(false)}/>
                            )
                        }
                        {
                            ifJsx(showRightResizer,
                                <now-icon className="sn-roadmap-bar-resizer sn-roadmap-bar-resizer-right"
                                    icon="grid-vertical-fill"
                                    size="md"
                                    onmousedown={e =>
                                        handleResizeBar(true, isRTL ? 'resizer-left' : 'resizer-right', id,
                                            startDate, endDate, e, isRTL, dispatch, horizontalScrollContainerRef, timelineWidth)
                                    }
                                    onmouseup={() => handleResizeBar(false)}/>
                            )
                        }
                    </Fragment>;
                })
            }
            {
                ifJsx(showBarContent, <Fragment>
                    { ifJsx(isLeftOverflow, () => {
                        return <div className= "sn-roadmap-bar-gradient-arrow" style={{ background: `${color}` }}>
                            <div className='sn-roadmap-bar-left-fill'>
                                { generateIcon(isRTL ? 'arrow-right-fill' : 'arrow-left-fill') }
                            </div>
                        </div>;
                    })
                    }
                    {renderBarContent({
                        roadmapItem,
                        barProperties,
                        metricFieldsWithValues,
                        showAvatar,
                        showMetric,
                        showItemOwner,
                        groupByValues
                    })}
                    { ifJsx(isRightOverflow, () => {
                        return <div className= "sn-roadmap-bar-gradient-arrow" style={{ background: `${color}` }}>
                            <div className='sn-roadmap-bar-right-fill'>
                                { generateIcon(isRTL ? 'arrow-left-fill' : 'arrow-right-fill') }
                            </div>
                        </div>;
                    })}
                </Fragment>)
            }
            <span className="sn-roadmap-bar-right-tooltip-target"/>
            <span className="sn-roadmap-bar-left-tooltip-target"/>
        </div>
    );
};
