import { setMomentFormat, isItemInRoadmapRange, isDateTimeValid } from '../../date-util';
import { getGroupedRows } from '../../plot-rows';
import { getColorMap, buildMetricFields, transformSettings } from '../settings-util';
import isEmpty from 'lodash/isEmpty';
import { RTL, GROUP_SECTION_WIDTH, UNSCHEDULED_ITEMS_PANEL, SETTINGS_PANEL } from '../../constants';
import { sortItemsByField, transformItemMetadata } from '../../util';
import ItemMetaData from '../../item-metadata';

const handleComponentDomReady = ({
    action: {
        payload: { host }
    },
    properties,
    state: { unscheduledItemsPanelPagination, sidePanel },
    updateState
}) => {
    const {
        config,
        dateFormat,
        roadmapItems,
        roadmapItemMetadata,
        dir,
        startDateTime,
        endDateTime,
        timezone,
        showSidePanel
    } = properties;
    const settings = transformSettings(properties.settings);
    const transformedItemMetadata=transformItemMetadata(roadmapItemMetadata);
    const roadmapItemNameField = roadmapItemMetadata.itemNameField.name;
    const {
        groupBy,
        metrics,
        colorBy
    } = settings;
    const scheduledItems = [];
    const unscheduledItems = [];
    let updatedSidePanel = {};

    ItemMetaData.set(transformedItemMetadata);
    const { startDateField, endDateField }=ItemMetaData.get();

    setMomentFormat({ _dateValueFormat: dateFormat.valueFormat,
        _dateDisplayValueFormat: dateFormat.displayValueFormat,
        _timezone: timezone });
    roadmapItems.forEach(function(item) {
        if (isEmpty(item[startDateField.name].displayValue) || isEmpty(item[endDateField.name].displayValue)) {
            unscheduledItems.push(item);
        } else if (isItemInRoadmapRange(item, startDateTime, endDateTime)) {
            scheduledItems.push(item);
        }
    });
    sortItemsByField(unscheduledItems, roadmapItemNameField);
    const groupedRoadmapItems = getGroupedRows([...scheduledItems], groupBy);
    const colorMap = getColorMap([...scheduledItems], [...unscheduledItems], colorBy);
    const metricFields = buildMetricFields(roadmapItemMetadata, metrics);

    if (showSidePanel) {
        updatedSidePanel.display = true;
        updatedSidePanel.panelName = sidePanel.panelName || SETTINGS_PANEL;
    } else if (scheduledItems.length === 0 && unscheduledItems.length !== 0) {
        updatedSidePanel = {
            display: true,
            currentSidePanelSlotName: '',
            sidePanelTitle: '',
            panelName: UNSCHEDULED_ITEMS_PANEL
        };
    }
    updateState({
        componentHost: host,
        config: {
            ...config,
            groupSectionWidth: isEmpty(config.groupSectionWidth) ? GROUP_SECTION_WIDTH : config.groupSectionWidth
        },
        sidePanel: {
            ...sidePanel,
            ...updatedSidePanel
        },
        settings: {
            ...settings,
            colorMap,
            metricFields,
            isRTL: dir === RTL
        },
        roadmapItems,
        groupedRoadmapItems,
        scheduledItems,
        unscheduledItems,
        unscheduledItemsPanelPagination: {
            ...unscheduledItemsPanelPagination,
            total: unscheduledItems.length
        },
        isRoadmapDateTimeValid: isDateTimeValid(startDateTime.displayValue, endDateTime.displayValue)
    });
};

export default handleComponentDomReady;
