import '../../sn-roadmap-timeline-header/sn-roadmap-timeline-header.js';
import { createRef } from '@seismic/snabbdom-renderer';
import { TIMELINE_SCROLL_REF } from '../constants';
import { getDateDisplayValueFormat } from '../date-util';
export const renderTimelineHeader = ({
    state: {
        properties: {
            locale,
            timelineHeaderData,
            startDateTime = { value: '', displayValue: '' },
            endDateTime = { value: '', displayValue: '' },
            defaultTimeScale
        },
        recalculateScale,
        timelineHeaderContainerWidth,
        hasBodyVertialScroll,
        scrollbarWidth,
        settings: {
            isRTL
        }
    },
    setRef,
    syncScroll
}) => {
    const timeLineHeaderRef = createRef();
    const marginWidth = hasBodyVertialScroll ? scrollbarWidth : 0;
    const dateTimeFormat = getDateDisplayValueFormat();

    setRef('timeLineHeaderRef', timeLineHeaderRef, TIMELINE_SCROLL_REF);

    return (
        <div className='sn-roadmap-timeline-header-container'
            tabindex="-1"
            ref={timeLineHeaderRef}
            on-scroll={e =>
                syncScroll(e, TIMELINE_SCROLL_REF, 'header')
            }
            style = {{ [isRTL ? 'marginLeft' : 'marginRight']: `${marginWidth}px` }}
        >
            {
                <sn-roadmap-timeline-header
                    locale = {locale}
                    timelineHeaderData = {timelineHeaderData}
                    defaultTimeScale = {defaultTimeScale}
                    startDateTime = {startDateTime.displayValue}
                    endDateTime = {endDateTime.displayValue}
                    dateTimeFormat = {dateTimeFormat}
                    recalculateScale = {recalculateScale}
                    hostWidth = {timelineHeaderContainerWidth}
                >
                </sn-roadmap-timeline-header>
            }
        </div>
    );
};
