import { renderBar } from '../bar/bar';
import { handleDrop } from '../bar/draggable';
import { updateDropTargetPath } from '../bar/barUtils';
import { ROW_CLASS } from '../../constants';
import { getDiffInUnits } from '../../date-util';
import ItemMetadata from '../../item-metadata';
import { getTimelineHourWidth } from './util';

export function renderRow({
    row,
    settings,
    dispatch,
    canEditBar,
    activeBarId,
    enableResizers,
    timelineData,
    groupByValues = [],
    horizontalScrollContainerRef,
    barResize,
    lastModifiedRoadmapItemId,
    itemDetailsPanel
}) {
    const { rowHeight } = settings;
    let prevRoadmapItem;
    let barOverlapBuffer = 0;
    const { startDateField, endDateField }=ItemMetadata.get();
    const timelineHourWidth = getTimelineHourWidth(timelineData);
    const childrenJSX = row.map(roadmapItem => {
        roadmapItem = {
            ...roadmapItem,
            settings,
            canEditBar,
            activeBarId,
            enableResizers
        };
        if (prevRoadmapItem && timelineHourWidth > 0) {
            const diffInHours = getDiffInUnits(prevRoadmapItem[endDateField.name].displayValue, roadmapItem[startDateField.name].displayValue, 'hours');
            if (diffInHours <= timelineHourWidth) {
                barOverlapBuffer = parseFloat(((timelineHourWidth - diffInHours)/timelineHourWidth).toFixed(2));
            }
        }
        prevRoadmapItem = roadmapItem;

        return renderBar({ roadmapItem, timelineData, dispatch, horizontalScrollContainerRef, barResize,
            lastModifiedRoadmapItemId, barOverlapBuffer, itemDetailsPanel });
    });

    const attrs = { groupByValues: groupByValues.slice(0, groupByValues.length-1)
        .map(g => g.value).join() };

    return (
        <div attrs={attrs} className={ROW_CLASS} style={{ height: `${rowHeight}px` }}
            groupByValues={groupByValues}
            ondragover={e => e.preventDefault()}
            ondragenter={e => updateDropTargetPath(true, e.target, dispatch)}
            ondragleave={e => {
                if (e.target.classList.contains(ROW_CLASS)) {
                    updateDropTargetPath(false, e.target, dispatch);
                }
            }}
            ondrop={e => handleDrop(e, dispatch, horizontalScrollContainerRef)}>
            {childrenJSX}
        </div>
    );
}
