import '../../../sn-roadmap-config-panel';
import '../../../sn-roadmap-config-selector';
import '@servicenow/now-toggle';
import {
    COLOR_BY,
    GROUP_BY,
    SELECTOR_ID,
    SHOW_ON_BARS,
    METRICS_FIELDS,
    MAX_GROUPBY_LIMIT_MESSAGE,
    MAX_METRIC_LIMIT_MESSAGE,
    TOGGLE_SHOW_ITEM_OWNER,
    GROUP_BY_LIMIT,
    BAR_MAX_METRICS,
    PERSONALIZE
} from '../../constants';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import isEmpty from 'lodash/isEmpty';
import { ifJsx } from '../../util';

const renderPersonalizeContent = (state) => {
    const {
        settings: { colorBy, groupBy, metrics, showItemOwner },
        properties: { roadmapItemMetadata },
        config: { settingsPanel } } = state;
    const itemOwnerFieldLabel = isEmpty(roadmapItemMetadata.itemOwnerField) ? '' : roadmapItemMetadata.itemOwnerField.label;
    const colorBySelectedItems = isEmpty(colorBy) ? []: [colorBy];
    const colorByItems = roadmapItemMetadata.colorableFields?roadmapItemMetadata.colorableFields.map(item => ({
        id: item.name, ...item })):[];
    const groupBySelectedItems = isEmpty(groupBy) ? []: groupBy;
    const groupByItems = roadmapItemMetadata.groupableFields?roadmapItemMetadata.groupableFields.map(item => ({
        id: item.name, ...item })):[];
    const metricsSelectedItems = isEmpty(metrics) ? []: metrics;
    const metricsItems = roadmapItemMetadata.metricsFields?roadmapItemMetadata.metricsFields.map(item => ({
        id: item.name, ...item })):[];

    return (
        <Fragment>
            { ifJsx(!settingsPanel.hideColorBySelector,
                <div className="select-section">
                    <now-heading label={COLOR_BY} level="2" variant="header-tertiary" hasNoMargin={true}></now-heading>
                    <sn-roadmap-config-selector
                        limit={1}
                        items={colorByItems}
                        draggable={false}
                        selectedItems={colorBySelectedItems}
                        selectorId={SELECTOR_ID.COLOR_BY}
                    />
                </div>)
            }
            { ifJsx(!settingsPanel.hideGroupBySelector,
                <div className="select-section">
                    <now-heading label={GROUP_BY} level="2" variant="header-tertiary" hasNoMargin={true}></now-heading>
                    <sn-roadmap-config-selector
                        limit={GROUP_BY_LIMIT}
                        items={groupByItems}
                        draggable={true}
                        selectedItems={groupBySelectedItems}
                        selectorId={SELECTOR_ID.GROUP_BY}
                        maxLimitMsg={MAX_GROUPBY_LIMIT_MESSAGE}
                    />
                </div>)
            }
            { ifJsx(!settingsPanel.hideMetricsSelector,
                <Fragment>
                    <div className="select-section other-fields-title">
                        <now-heading label={SHOW_ON_BARS} level="2" variant='header-tertiary' hasNoMargin={true}></now-heading>
                        { ifJsx(!isEmpty(itemOwnerFieldLabel), <div className="select-section-toggle-container">
                            <div className="select-section-toggle-container-label">{itemOwnerFieldLabel}</div>
                            <now-toggle
                                size="sm"
                                config-aria={{ switch: { 'aria-label': itemOwnerFieldLabel } }}
                                checked={showItemOwner}
                                append-to-payload={{ type: TOGGLE_SHOW_ITEM_OWNER }}
                            />
                        </div>)}
                    </div>
                    <div className="select-section-other-fields-container"><span>{ METRICS_FIELDS }</span></div>
                    <div className="select-section other-fields-pillar">
                        <sn-roadmap-config-selector
                            limit={BAR_MAX_METRICS}
                            items={metricsItems}
                            draggable={true}
                            selectedItems={metricsSelectedItems}
                            selectorId={SELECTOR_ID.METRICS}
                            maxLimitMsg={MAX_METRIC_LIMIT_MESSAGE}
                        />
                    </div>
                </Fragment>)
            }
        </Fragment>
    );
};

const renderSettingsPanelContent = (state) => {
    return (
        <div className="sn-roadmap-settings-panel-content">
            {renderPersonalizeContent(state)}
        </div>
    );
};

export const renderSettingsPanel = (state) => {
    return (
        <sn-roadmap-config-panel className="sn-roadmap-main-section-settings-panel" headerTitle={ PERSONALIZE }>
            <div slot="sn-roadmap-config-panel-content">
                { renderSettingsPanelContent(state) }
            </div>
        </sn-roadmap-config-panel>
    );
};
