import moment from 'moment-timezone';
import { DEFAULT_DATE_TIME_FORMAT, TIME_UNITS, VIEW_DATE_FORMAT } from './constants';
import isString from 'lodash/isString';
import isEmpty from 'lodash/isEmpty';
import ItemMetadata from './item-metadata';
import memoize from 'lodash/memoize';

let dateValueFormat = null;
let dateDisplayValueFormat = null;
let dateViewFormat = null;
let timezone = null;

export const getDateAndTimeFormat = (dtF) => {
    if (isEmpty(dtF)) {
        return {};
    }
    let dF = '';
    let tF = '';
    const dtFArr = dtF.split(' ');

    dF = dtFArr[0];
    if (dF.length > 1) {
        tF = dtFArr.splice(1, dtFArr.length).join(' ');
    }

    return {
        dateFormat: dF,
        timeFormat: tF
    };
};

const getUpperCaseFormat = (dateFormat) => {
    if (isEmpty(dateFormat)) {
        return '';
    }
    const dateTimeFormatObj = getDateAndTimeFormat(dateFormat);

    return dateTimeFormatObj.timeFormat ? dateTimeFormatObj.dateFormat.toUpperCase() + ' ' + dateTimeFormatObj.timeFormat : dateTimeFormatObj.dateFormat.toUpperCase();
};

export const setMomentFormat = ({
    _dateValueFormat = DEFAULT_DATE_TIME_FORMAT,
    _dateDisplayValueFormat = DEFAULT_DATE_TIME_FORMAT,
    _timezone }
) => {
    dateValueFormat = getUpperCaseFormat(_dateValueFormat);
    dateDisplayValueFormat = getUpperCaseFormat(_dateDisplayValueFormat);
    dateViewFormat = dateDisplayValueFormat.split(' ')[0];
    timezone = _timezone;
};

export const getDateViewFormat = () => {
    return dateViewFormat ? dateViewFormat : VIEW_DATE_FORMAT;
};

export const getDateValueFormat = () => {
    return dateValueFormat ? dateValueFormat : DEFAULT_DATE_TIME_FORMAT;
};

export const getDateDisplayValueFormat = () => {
    return dateDisplayValueFormat ? dateDisplayValueFormat : DEFAULT_DATE_TIME_FORMAT;
};

const getMomentDate = memoize((
    date,
    format
) => {
    return moment.tz(date, format, timezone);
}, (...args) => `${args[0]}_${timezone}`);

export const getDate = (date, isValueFormat) => {
    if (isString(date)) {
        const format = isValueFormat ? dateValueFormat: dateDisplayValueFormat;

        return getMomentDate(date, format);
    } else if (date instanceof moment) {
        return date;
    } else if (date instanceof Date) {
        return moment.tz(date, timezone);
    } else {
        return moment();
    }
};

export const getUtcFormat = (date) => {
    return getDate(date).clone().utc().format(dateValueFormat);
};

export const getFormattedDate = (date, isInputValueFormat, isOutputValueFormat) => {
    const dateMoment = getDate(date, isInputValueFormat);
    const outputFormat = isOutputValueFormat ? dateValueFormat : dateDisplayValueFormat;

    return dateMoment.format(outputFormat);
};

export const getViewDate = (inputDateAsStr) => {
    if (isEmpty(inputDateAsStr)) {
        return '';
    }
    const jsDate = getDate(inputDateAsStr);
    if (jsDate) {
        return jsDate.format(dateViewFormat);
    }
};

export const getDiff = (start, end, unit) => {
    const _start = getDate(start);
    const _end = getDate(end);

    return _end.diff(_start, unit, true);
};

export const getAbsDiff = (start, end, unit) => {
    const diff = getDiff(start, end, unit);

    return diff > 0 ? diff : 0;
};

export const getDiffWithFormat = (startDate, isStartDateValueFormat, endDate, isEndDateValueFormat, unit) => {
    const _start = getDate(startDate, isStartDateValueFormat);
    const _end = getDate(endDate, isEndDateValueFormat);

    return _start.diff(_end, unit, true);
};

export const getDiffInUnits = (start, end, unit) => {
    return unit === TIME_UNITS.QUARTER
        ? getAbsDiff(start, end, TIME_UNITS.MONTH) / 3
        : getAbsDiff(start, end, unit);
};

export const getAddedDateTime = (date, duration, unit) => {
    if (isEmpty(date)) {
        return date;
    }

    return getDate(date).clone().add(duration, unit);
};

export const isItemInRoadmapRange = (item, roadmapStartDate, roadmapEndDate) => {
    const { startDateField, endDateField }=ItemMetadata.get();
    const startDateTime = getDate(roadmapStartDate.displayValue);
    const endDateTime = getDate(roadmapEndDate.displayValue);
    const itemStartDateTime = getDate(item[startDateField.name].displayValue);
    const itemEndDateTime = getDate(item[endDateField.name].displayValue);

    return !(itemEndDateTime < startDateTime || itemStartDateTime > endDateTime);
};

export const isDateTimeValid = (startDate, endDate) => {
    return getDate(startDate) < getDate(endDate);
};
