import '@servicenow/now-button';
import { t } from 'sn-translate';
import {
    TOOLBAR,
    PERSONALIZE,
    UNSCHEDULED_ITEMS,
    SETTINGS_PANEL,
    UNSCHEDULED_ITEMS_PANEL,
    ITEM_DETAILS_PANEL,
    ITEM_DETAILS,
    ROADMAP_DETAILS,
    ROADMAP_DETAILS_PANEL,
    SIDE_TOOLBAR
} from '../../constants';
import { renderSettingsPanel } from './settings-panel';
import { renderUnscheduledItemsPanel } from './unscheduled-items-panel';
import { renderItemDetailsPanel } from './item-details-panel';
import isEmpty from 'lodash/isEmpty';
import { focusLoop } from '../../accessibility';
import { toggleFocusRing } from '../../util';

const renderSideBar = (state) => {
    const { properties: { sideBarActionItems }, sidePanel } = state;
    const { panelName } = sidePanel;

    return (
        <div className="sn-roadmap-main-section-toolbar"
            tabindex="-1"
            on-keydown={(e) => focusLoop(e, true)}
            on-blur={(e) => toggleFocusRing(e.target, true)}
            aria-label={SIDE_TOOLBAR}
        >
            <now-button-iconic
                className="sn-roadmap-main-section-toolbar-icon roadmap-details"
                attrs={{ panel: ROADMAP_DETAILS_PANEL }}
                icon="roadmap-outline"
                tooltipContent={ROADMAP_DETAILS}
                size="md"
                configAria={{ button: { 'aria-label': ROADMAP_DETAILS } }}
                bare
                appendToPayload={{
                    type: TOOLBAR.ROADMAP_DETAILS_BUTTON_CLICKED,
                    currentSlotName: '',
                    panelName: ROADMAP_DETAILS_PANEL
                }}
                variant={ panelName === ROADMAP_DETAILS_PANEL ? 'secondary' : 'tertiary' }
                on-click={event => {
                    event.stopPropagation();
                }}
                on-keydown={(e) => focusLoop(e)}
            />
            <now-button-iconic
                className="sn-roadmap-main-section-toolbar-icon settings"
                attrs={{ panel: SETTINGS_PANEL }}
                icon="gear-outline"
                tooltipContent={PERSONALIZE}
                size="md"
                configAria={{ button: { 'aria-label': PERSONALIZE } }}
                bare
                variant={ panelName === SETTINGS_PANEL? 'secondary' : 'tertiary' }
                appendToPayload={{
                    type: TOOLBAR.SETTINGS_PANEL_TOGGLED,
                    currentSlotName: '',
                    panelName: SETTINGS_PANEL
                }}
                on-click={event => {
                    event.stopPropagation();
                }}
                on-keydown={(e) => focusLoop(e)}
                active={true}
            />
            <now-button-iconic
                className="sn-roadmap-main-section-toolbar-icon item-details"
                attrs={{ panel: ITEM_DETAILS_PANEL }}
                icon="circle-info-outline"
                tooltipContent={ITEM_DETAILS}
                size="md"
                configAria={{ button: { 'aria-label': ITEM_DETAILS } }}
                bare
                variant={ panelName === ITEM_DETAILS_PANEL? 'secondary' : 'tertiary' }
                appendToPayload={{
                    type: TOOLBAR.ITEM_DETAILS_PANEL_TOGGLED,
                    currentSlotName: '',
                    panelName: ITEM_DETAILS_PANEL,
                    showEmptyImage: true
                }}
                on-click={event => {
                    event.stopPropagation();
                }}
                on-keydown={(e) => focusLoop(e)}
            />
            <now-button-iconic
                className="sn-roadmap-main-section-toolbar-icon unscheduled-item"
                attrs={{ panel: UNSCHEDULED_ITEMS_PANEL }}
                icon="list-fill"
                tooltipContent={UNSCHEDULED_ITEMS}
                size="md"
                configAria={{ button: { 'aria-label': UNSCHEDULED_ITEMS } }}
                bare
                variant={ panelName === UNSCHEDULED_ITEMS_PANEL? 'secondary' : 'tertiary' }
                appendToPayload={{
                    type: TOOLBAR.UNSCHEDULED_ITEMS_PANEL_TOGGLED,
                    currentSlotName: '',
                    panelName: UNSCHEDULED_ITEMS_PANEL
                }}
                on-click={event => {
                    event.stopPropagation();
                }}
                on-keydown={(e) => focusLoop(e)}
            />
            { sideBarActionItems.map((item) => {
                return (
                    <now-button-iconic
                        className={'sn-roadmap-main-section-toolbar-icon ' + item.id + ' ' + (sidePanel.currentSidePanelSlotName === item.slotName ? 'active' : '') }
                        attrs={{ panel: item.slotName }}
                        icon={item.icon}
                        tooltipContent={item.label}
                        size="md"
                        variant={ panelName === item.slotName? 'secondary' : 'tertiary' }
                        bare
                        config-aria={{ 'aria-label': sidePanel.currentSidePanelSlotName === item.slotName ? t('Hide {0}', item.label) : t('Show {0}', item.label) }}
                        appendToPayload={{
                            type: TOOLBAR.CUSTOM_BUTTON_ICONIC_CLICKED,
                            currentSlotName: item.slotName,
                            panelName: item.slotName,
                            sidePanelTitle: item.label
                        }}
                        on-click={event => {
                            event.stopPropagation();
                        }}
                        on-keydown={(e) => focusLoop(e)}
                    />
                );
            })}
        </div>
    );
};

const renderBuildInPanel = ({ state, dispatch }) => {
    const { sidePanel } = state;
    switch (sidePanel.panelName) {
        case SETTINGS_PANEL: {
            return renderSettingsPanel(state);
        }
        case UNSCHEDULED_ITEMS_PANEL: {
            return renderUnscheduledItemsPanel({ state, dispatch });
        }
        case ITEM_DETAILS_PANEL: {
            return renderItemDetailsPanel({ state });
        }
    }
};

const renderCustomerPanel = (currentSidePanelSlotName, sidePanelTitle) => {
    return (
        <sn-roadmap-config-panel className="sn-roadmap-main-section-panel" headerTitle={ sidePanelTitle }>
            <div slot="sn-roadmap-config-panel-content">
                <slot name={currentSidePanelSlotName}></slot>
            </div>
        </sn-roadmap-config-panel>
    );
};

const renderSidePanel = ({ state, dispatch }) => {
    const { sidePanel: { currentSidePanelSlotName, sidePanelTitle } } = state;

    return (
        isEmpty(currentSidePanelSlotName) ? renderBuildInPanel({ state, dispatch })
            : renderCustomerPanel(currentSidePanelSlotName, sidePanelTitle)
    );
};

export const renderSideToolBar = ({ state, dispatch }) => {
    const { sidePanel } = state;

    return (
        <div className="sn-roadmap-main-section-sidebar-container">
            { sidePanel.display ? renderSidePanel({ state, dispatch }) : null}
            { renderSideBar(state) }
        </div>
    );
};
