// eslint-disable-next-line no-unused-vars
import { Fragment } from '@seismic/snabbdom-renderer';
import '@servicenow/now-pagination-control';
import '@servicenow/now-illustration';
import { focusLoop } from '../../accessibility';
import { getParentNodeByClass } from '../../util';
import {
    UNSCHEDULED_ITEMS,
    UNSCHEDULED_ITEM_SELECTED,
    UNSCHEDULED_ITEMS_PANEL_EMPTY_STATE,
    UNSCHEDULED_ITEM_ATTRIBUTE,
    UNSCHEDULED_PANEL_CLASS,
    CLOSE_UNSCHEDULED_ITEMS_PANEL,
    CLOSE,
    BAR_LARGE_HEIGHT,
    BAR_CLASS,
    BACK_TO_UNSCHEDULED_ITEMS_LIST,
    SIDE_PANEL_DND_MSG,
    RTL,
    BACK,
    KEYS,
    ITEM_DETAILS,
    NEUTRAL2_COLOR
} from '../../constants';
import { renderBarContent } from '../bar/bar';
import { getItemMetricFieldsWithValue } from '../body/util';
import { getScrollContainers, handleDragBar } from '../bar/draggable';
import ItemMetaData from '../../item-metadata';

const renderItems = ({ state, dispatch }) => {
    const {
        settings: {
            rowHeight,
            showItemOwner,
            metricFields
        },
        unscheduledItems,
        unscheduledItemsPanelPagination: {
            selectedPage,
            selectedPageSize
        },
        properties: { dir, canEditBar }
    } = state;
    const { verticalScrollContainerRef, horizontalScrollContainerRef } = getScrollContainers();
    const isRTL = dir === RTL;
    const showAvatar = true;
    const showMetric = rowHeight === BAR_LARGE_HEIGHT;
    const barContainerCustomStyle = {
        width: '272px',
        height: rowHeight + 'px',
        backgroundColor: NEUTRAL2_COLOR
    };
    const { startDateField, endDateField, itemOwnerField }=ItemMetaData.get();

    if (unscheduledItems.length > 0) {
        const startIndex = selectedPage * selectedPageSize;
        const currentPageItems = unscheduledItems.slice(startIndex, startIndex + selectedPageSize);

        return (
            currentPageItems.map((roadmapItem, index) => {
                const metricFieldsWithValues = getItemMetricFieldsWithValue(roadmapItem, metricFields);
                const { [startDateField.name]: startDate, [endDateField.name]: endDate, id } = roadmapItem;
                const tabIn = index === 0 ? '0': '-1';

                return (
                    <div role="button"
                        id={id.value}
                        style={barContainerCustomStyle}
                        className={BAR_CLASS}
                        unscheduledRoadmapItem = { UNSCHEDULED_ITEM_ATTRIBUTE }
                        on-click={() => {
                            dispatch(UNSCHEDULED_ITEM_SELECTED, {
                                roadmapItem: roadmapItem
                            });
                        }}
                        onmousedown={e => {
                            if (canEditBar) {
                                handleDragBar(true, startDate, endDate, e, dispatch, isRTL,
                                    verticalScrollContainerRef, horizontalScrollContainerRef);
                            }
                        }}
                        tabindex={ tabIn }
                        on-keydown={(e) => {
                            if (e.keyCode === KEYS.ENTER) {
                                dispatch(UNSCHEDULED_ITEM_SELECTED, {
                                    roadmapItem: roadmapItem
                                });

                                return;
                            }
                            focusLoop(e);
                        }}>
                        {
                            renderBarContent({
                                roadmapItem,
                                barProperties: {},
                                metricFieldsWithValues,
                                showAvatar,
                                showMetric,
                                showItemOwner,
                                itemOwnerField
                            })
                        }
                    </div>
                );
            })
        );
    }
};

const focusFirstItem = (event) => {
    if ([KEYS.UP, KEYS.DOWN, KEYS.RIGHT, KEYS.LEFT].indexOf(event.keyCode) === -1 ||
        event.shiftKey) {
        return;
    }
    const panelBody = getParentNodeByClass(event.target, UNSCHEDULED_PANEL_CLASS);

    const bar = panelBody.querySelector(`[class="${BAR_CLASS}"]`);

    if (bar) {
        bar.focus();
    }
};

const renderUnscheduledItemsPanelContent = ({ state, dispatch }) => {
    const { unscheduledItemsPanelPagination } = state;

    return (
        <div className={UNSCHEDULED_PANEL_CLASS}>
            {
                state.unscheduledItems.length > 0
                    ? <Fragment>
                        <div className="sn-roadmap-main-section-unscheduled-items-panel-content-subheader">
                            { SIDE_PANEL_DND_MSG }
                        </div>
                        <div className="sn-roadmap-main-section-unscheduled-items-panel-content-body">
                            { renderItems({ state, dispatch }) }
                        </div>
                        <div className="sn-roadmap-main-section-unscheduled-items-panel-content-footer"
                            on-keydown={(e)=> focusFirstItem(e) }>
                            <now-pagination-control {...unscheduledItemsPanelPagination} />
                        </div>
                    </Fragment>
                    : <div className="sn-roadmap-main-section-unscheduled-items-panel-content-empty">
                        <now-illustration illustration="completed-tasks" size="md" />
                        <div className="sn-roadmap-main-section-unscheduled-items-panel-content-empty-header">{ UNSCHEDULED_ITEMS_PANEL_EMPTY_STATE }</div>
                    </div>
            }
        </div>
    );
};

const renderDetailsContent = () => {
    return (
        <div className="sn-roadmap-main-section-unscheduled-item-detail">
            <div className="sn-roadmap-main-section-unscheduled-item-detail-header">
                <now-button-iconic
                    bare
                    config-aria={{
                        'aria-label': BACK
                    }}
                    icon="arrow-left-fill"
                    size="md"
                    append-to-payload={{
                        type: BACK_TO_UNSCHEDULED_ITEMS_LIST
                    }}
                    variant="tertiary"
                />
                <now-heading variant="header-secondary" label={ITEM_DETAILS} hasNoMargin={true} className="sn-roadmap-main-section-unscheduled-item-detail-header-title"/>
                <now-button-iconic
                    bare
                    config-aria={{
                        'aria-label': CLOSE
                    }}
                    icon="close-fill"
                    size="md"
                    append-to-payload={{
                        type: CLOSE_UNSCHEDULED_ITEMS_PANEL
                    }}
                    variant="tertiary"
                />
            </div>
            <div className="sn-roadmap-main-section-unscheduled-item-detail-body">
                <slot name="sn-roadmap-item-details"></slot>
            </div>
        </div>
    );
};

export const renderUnscheduledItemsPanel = ({ state, dispatch }) => {
    const { unscheduledItemSelected, displayPanelHeader } = state;

    return (
        <sn-roadmap-config-panel className="sn-roadmap-main-section-unscheduled-items-panel" headerTitle={UNSCHEDULED_ITEMS} displayHeader={ displayPanelHeader }>
            <div slot="sn-roadmap-config-panel-content" style={{ height: '100%' }}>
                { unscheduledItemSelected
                    ? renderDetailsContent()
                    : renderUnscheduledItemsPanelContent({ state, dispatch })}
            </div>
        </sn-roadmap-config-panel>
    );
};
