import { BAR_POPOVER_SHOWN, BAR_POPOVER_HIDDEN, BAR_POPOVER_MOUSE_ENTERED, BAR_POPOVER_MOUSE_LEFT } from '../actions';
import { getViewDate } from '../../date-util';
import ItemMetaData from '../../item-metadata';
import isEmpty from 'lodash/isEmpty';

export const popoverActionHandlers = {
    [BAR_POPOVER_SHOWN]: {
        effect: ({ state, updateState, action }) => {
            const {
                target,
                roadmapItem,
                showItemOwner,
                metricFieldsWithValues
            } = action.payload;
            const { startDateField, endDateField, itemOwnerField, itemNameField }=ItemMetaData.get();
            const { [itemNameField.name]: name, [startDateField.name]: startDate, [endDateField.name]: endDate } = roadmapItem;
            const roadmapItemOwner = itemOwnerField && itemOwnerField.name ? roadmapItem[itemOwnerField.name] : null;
            const roadmapItemOwnerValue = roadmapItemOwner && roadmapItemOwner.displayValue ? roadmapItemOwner.displayValue : '';
            const roadmapItemOwnerLabel = itemOwnerField && itemOwnerField.label ? itemOwnerField.label : '';
            const itemOwner = !isEmpty(roadmapItemOwnerValue) && !isEmpty(roadmapItemOwnerLabel) ? {
                label: roadmapItemOwnerLabel,
                value: roadmapItemOwnerValue
            } : {};
            let newOpenedState = !state.popover.opened;
            if (state.popover.target && target !== state.popover.target && state.popover.opened) {
                newOpenedState = true;
            }

            updateState({
                popover: {
                    opened: newOpenedState,
                    target,
                    data: {
                        title: name.displayValue,
                        startDate: { label: startDateField.label, value: getViewDate(startDate.displayValue) },
                        endDate: { label: endDateField.label, value: getViewDate(endDate.displayValue) },
                        itemOwnerField,
                        itemOwner,
                        showItemOwner,
                        metrics: metricFieldsWithValues
                    },
                    mouseOver: false
                }
            });
        },
        stopPropagation: true
    },

    [BAR_POPOVER_HIDDEN]: {
        effect: ({ updateState, state }) => {
            if (!state.popover.mouseOver) {
                updateState({
                    popover: {
                        opened: false,
                        target: null,
                        data: null,
                        mouseOver: false
                    }
                });
            }
        },
        stopPropagation: true
    },

    [BAR_POPOVER_MOUSE_ENTERED]: {
        effect: ({ updateState, state }) => {
            updateState({
                popover: {
                    ...state.popover,
                    mouseOver: true
                }
            });
        },
        stopPropagation: true
    },

    [BAR_POPOVER_MOUSE_LEFT]: {
        effect: ({ updateState }) => {
            updateState({
                popover: {
                    opened: false,
                    target: null,
                    data: null,
                    mouseOver: false
                }
            });
        },
        stopPropagation: true
    }
};
