// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { TOOLTIP_MAX_METRICS_PER_SECTION } from '../../constants';
import isEmpty from 'lodash/isEmpty';
import { BAR_POPOVER_MOUSE_ENTERED, BAR_POPOVER_MOUSE_LEFT } from '../../comp-config/actions';

const getMetricsSection = (metrics) => {
    let metricsSection = [];
    const bodySection = [];

    metrics.forEach((metric, index) => {
        if (metric.value) {
            metricsSection.push({ label: metric.label, value: { type: 'jsx', value: <span>{metric.value}</span> } });
        }
        if ((index + 1) % TOOLTIP_MAX_METRICS_PER_SECTION === 0) {
            bodySection.push(metricsSection);
            metricsSection = [];
        }
    });
    if (metricsSection.length > 0) {
        bodySection.push(metricsSection);
    }

    return bodySection;
};

export const popoverContentView = (state, dispatch) => {
    {
        const { data } = state.properties;
        if (!data) {
            return null;
        }

        const { title, startDate, endDate, itemOwner, itemOwnerField, showItemOwner, metrics } = data;
        const itemOwnerSection = [];
        if (showItemOwner && !isEmpty(itemOwner)) {
            itemOwnerSection.push({ label: itemOwnerField.label, value: { type: 'string', value: itemOwner.value } });
        }
        const bodySection = getMetricsSection(metrics);

        return (
            <div id='slotted-content' className='popover'
                onmouseenter={() => {
                    dispatch(BAR_POPOVER_MOUSE_ENTERED);
                }}
                onmouseleave={() => {
                    dispatch(BAR_POPOVER_MOUSE_LEFT);
                }}
            >
                <div className='popover-title'>{title}</div>
                <div className='popover-date'>
                    <now-label-value-stacked className='popover-date-start'
                        items={[startDate]} size="md" align="horizontal"/>
                    <now-label-value-stacked className='popover-date-end'
                        items={[endDate]} size="md" align="horizontal"/>
                </div>
                { itemOwnerSection.length ? <now-label-value-stacked className='popover-assigned-to-section'
                    items={itemOwnerSection} size="md" align="horizontal"/> : <Fragment/> }
                { bodySection.map(section => {
                    return section.length ? <now-label-value-stacked className='popover-metrics-section'
                        items={section} size="md" align="horizontal" truncated={false} wrapped={true}/> : <Fragment/>;
                })}
            </div>
        );
    }
};
