import { getDate } from './date-util';
import { EMPTY_GROUP } from './constants/translation-strings';
import isEmpty from 'lodash/isEmpty';
import { sortItemsByField } from './util';
import ItemMetadata from './item-metadata';

function sortRoadmapItems(roadmapItems) {
    const { startDateField, itemNameField }=ItemMetadata.get();
    const itemNameFieldName=itemNameField.name;

    roadmapItems.sort(function(a, b) {
        const aStartDate = getDate(a[startDateField.name].displayValue);
        const bStartDate = getDate(b[startDateField.name].displayValue);
        if (aStartDate.isAfter(bStartDate)) {
            return 1;
        }
        if (bStartDate.isAfter(aStartDate)) {
            return -1;
        }
        if (a[itemNameFieldName].value.toLowerCase() > b[itemNameFieldName].value.toLowerCase()) {
            return 1;
        }
        if (b[itemNameFieldName].value.toLowerCase() > a[itemNameFieldName].value.toLowerCase()) {
            return -1;
        }

        return 0;
    });
}

export function plotRows(roadmapItems) {
    if (roadmapItems.length === 0) {
        return [];
    }
    const { startDateField, endDateField }=ItemMetadata.get();

    sortRoadmapItems(roadmapItems);

    const plot = [[]];

    plot[0].push(roadmapItems[0]);

    for (let i = 1; i < roadmapItems.length; i++) {
        let j = 0;
        let inserted = false;
        for (j; j < plot.length; j++) {
            const index = plot[j].length - 1;
            if (getDate(plot[j][index][endDateField.name].displayValue).isSameOrBefore(getDate(
                roadmapItems[i][startDateField.name].displayValue))) {
                inserted = true;
                plot[j].push(roadmapItems[i]);
                break;
            }
        }
        if (!inserted) {
            plot.push([]);
            plot[j].push(roadmapItems[i]);
        }
    }

    return plot;
}

function grouping(roadmapItems, groupBy, groupByIndex, output) {
    if (groupByIndex === groupBy.length) {
        // plot road map items
        const rows = plotRows(roadmapItems);

        output.rows = rows;

        return;
    }
    const groupByField = groupBy[groupByIndex];

    sortItemsByField(roadmapItems, groupByField);
    let h =0;
    for (h = 0; h < roadmapItems.length; h++) {
        const input = [];
        let j = h;
        const groupVal = roadmapItems[j][groupByField];
        if (isEmpty(groupVal) || !groupVal.value) {
            break;
        }

        const object1 = {
            value: roadmapItems[j][groupByField].value,
            displayValue: roadmapItems[j][groupByField].displayValue,
            rows: [],
            groups: []
        };

        input.push(roadmapItems[j]);
        while (j <= roadmapItems.length - 2) {
            const groupByVal = roadmapItems[j + 1][groupByField];
            if (isEmpty(groupByVal) || isEmpty(groupByVal.value) ||
             roadmapItems[j][groupByField].value !== groupByVal.value) {
                break;
            }
            input.push(roadmapItems[j + 1]);
            j++;
        }
        output.groups.push(object1);
        grouping(input, groupBy, groupByIndex + 1, object1);
        h = j;
    }
    if (roadmapItems.length > h) { // move rest into none category
        const none = {
            value: EMPTY_GROUP,
            displayValue: EMPTY_GROUP,
            rows: [],
            groups: []
        };
        const emptyItems = roadmapItems.slice(h);

        output.groups.push(none);
        grouping(emptyItems, groupBy, groupByIndex + 1, none);
    }

    return 0;
}

export function getGroupedRows(roadmapItems, groupBy = []) {
    const output = { groups: [], rows: [] };
    const groupByIndex = 0;
    if (!groupBy || groupBy.length === 0) {
        output.rows = plotRows(roadmapItems);

        return output;
    }
    grouping(roadmapItems, groupBy, groupByIndex, output);

    return output;
}

