
import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-roadmap-colorby-legend-popover.scss';
import { keyDownHandler, setFocusToNextItem } from './accessibility-helper';
import { ifJsx } from '../../../util';
import { t } from 'sn-translate';
import { actionTypes } from '@seismic/core';

const { COMPONENT_RENDERED } = actionTypes;


const MAX_ITEMS_COUNT=20;
const COLORBY_LEGEND_POPOVER=t('Colorby legend popover');

const toolTipHandler=({ target }, updateState)=>{
    let colorByTooltip={
        ref: null,
        text: ''
    };
    if (target) {
        const textElem=target.childNodes[1];
        if (textElem.offsetWidth < textElem.scrollWidth) {
            colorByTooltip={
                ref: target,
                text: textElem.innerText
            };
        }
    }
    updateState({ colorByTooltip });
};

const colorbyLegendView=({ properties: { colorMap, colorBy, parent, focusFirstChild }, colorByTooltip }, { dispatch, updateState })=>{
    if (colorMap.size === 0) {
        return;
    }
    const colorbyArray = [...colorMap.entries()].filter(entry => entry[1].hasScheduledItem).slice(0, MAX_ITEMS_COUNT) || [];

    return (
        <div>
            <div role="list"
                aria-label={COLORBY_LEGEND_POPOVER}
                className="sn-roadmap-colorby-legend-popover-content"
                tabIndex="0"
                on-keydown={ e=> keyDownHandler(e, dispatch, parent)}
                on-keyup={e=>keyDownHandler(e, dispatch, parent)}
                onmousedown={e=> e.stopPropagation()}
                onfocus={(e)=> {
                    if (focusFirstChild) {
                        e.preventDefault();
                        e.stopPropagation();
                        e.target.querySelector('.sn-roadmap-colorby-legend-popover-content-list-item').focus();
                    }
                }}>
                <now-heading label={colorBy} level="3" variant="title-primary"></now-heading>
                <ul className="sn-roadmap-colorby-legend-popover-content-list">
                    {colorbyArray.map((colorByEntry)=>{
                        return (
                            <li
                                className="sn-roadmap-colorby-legend-popover-content-list-item"
                                tabIndex="-1"
                                on-keydown={e=>setFocusToNextItem(e, dispatch, parent)}
                                on-mouseenter={e=> toolTipHandler(e, updateState)}
                                on-mouseleave={()=>toolTipHandler({ target: null }, updateState)}
                                role="listitem">
                                <span className="sn-roadmap-colorby-legend-popover-content-list-item-icon">
                                    <now-icon icon='square-fill' size="sm" style={{ color: colorByEntry[1].color }}></now-icon>
                                </span>
                                <span className="sn-roadmap-colorby-legend-popover-content-list-item-value">
                                    {colorByEntry[1].displayValue}
                                </span>
                            </li>
                        );
                    })}
                </ul>
            </div>
            <div>
                { ifJsx(colorByTooltip.ref,
                    <now-tooltip
                        target-ref={colorByTooltip.ref} content={colorByTooltip.text}
                        position={['top-center bottom-end']} opened='true'/>)
                }
            </div>
        </div>
    );
};

createCustomElement('sn-roadmap-colorby-legend-popover', {
    properties: {
        colorMap: { default: null },
        colorBy: { default: '' },
        parent: { default: null },
        focusFirstChild: { default: false }
    },
    initialState: {
        colorByTooltip: { ref: null, text: '' },
        parent: { default: null }
    },
    view: colorbyLegendView,
    styles: styles,
    actionHandlers: {
        [COMPONENT_RENDERED]: ({ state: { properties: { parent } }, updateState }) => {
            updateState({
                parent
            });
        }
    }
});

