import {
    TOOLBAR,
    BUTTON_ICONIC_CLICKED,
    KB_SHORTCUTS_CLOSED,
    CLOSE_UNSCHEDULED_ITEMS_PANEL,
    BACK_TO_UNSCHEDULED_ITEMS_LIST,
    ROADMAP_ITEM_SELECTED,
    COLORBY_LEGEND_POPOVER_CLOSED,
    ROADMAP_SIDEBAR_BUTTON_CLICKED,
    NOW_MODAL_OPENED_SET,
    ROADMAP_SIDEPANEL_TOGGLED
} from '../../constants';
import { getSidebar, setFocusToBody } from '../../util';
export const roadmapActionHandlers = {
    [BUTTON_ICONIC_CLICKED]: {
        effect: ({
            state: { sidePanel },
            action: { payload },
            updateState,
            dispatch
        }) => {
            updateState({
                colorByLegendTargetRef: null,
                openColorByLegendPopover: false
            });
            const sidePanelValue = { ...sidePanel };

            sidePanelValue.unscheduledItemSelected = false;
            let closeSidePanel = false;

            switch (payload.type) {
                case TOOLBAR.SETTINGS_PANEL_TOGGLED:
                case TOOLBAR.UNSCHEDULED_ITEMS_PANEL_TOGGLED:
                case TOOLBAR.ITEM_DETAILS_PANEL_TOGGLED:
                case TOOLBAR.CUSTOM_BUTTON_ICONIC_CLICKED: {
                    if (sidePanelValue.display && payload.panelName === sidePanelValue.panelName) {
                        closeSidePanel = true;
                    } else {
                        sidePanelValue.currentSidePanelSlotName = payload.currentSlotName;
                        sidePanelValue.sidePanelTitle = payload.sidePanelTitle;
                        sidePanelValue.panelName = payload.panelName;
                        sidePanelValue.display = true;
                        sidePanelValue.showEmptyImage = payload.showEmptyImage;
                    }
                    if (closeSidePanel) {
                        dispatch(TOOLBAR.SIDE_PANEL_CLOSED);
                    } else {
                        updateState({
                            sidePanel: sidePanelValue,
                            unscheduledItemSelected: false,
                            displayPanelHeader: true
                        });
                        dispatch(ROADMAP_SIDEPANEL_TOGGLED, {
                            display: true,
                            panelName: sidePanelValue.panelName
                        });
                    }
                    dispatch(ROADMAP_SIDEBAR_BUTTON_CLICKED, { sideBarPayload: { ...payload } });
                    break;
                }
                case TOOLBAR.ROADMAP_DETAILS_BUTTON_CLICKED:
                    dispatch(TOOLBAR.SIDE_PANEL_CLOSED);
                    dispatch(ROADMAP_SIDEBAR_BUTTON_CLICKED, { sideBarPayload: { ...payload } });
                    break;
                case CLOSE_UNSCHEDULED_ITEMS_PANEL: {
                    dispatch(TOOLBAR.SIDE_PANEL_CLOSED);
                    break;
                }
                case BACK_TO_UNSCHEDULED_ITEMS_LIST: {
                    updateState({
                        unscheduledItemSelected: false,
                        displayPanelHeader: true
                    });
                }
            }
        },
        stopPropagation: true
    },
    [NOW_MODAL_OPENED_SET]: ({ state, updateState, host })=> {
        updateState({
            openKeyboardShortcuts: false,
            overrideGroupModal: {
                display: false
            }
        });
        if (state.openKeyboardShortcuts === true) {
            setFocusToBody(host);
        }
    },
    [KB_SHORTCUTS_CLOSED]: ({
        updateState,
        host }) => {
        updateState({ openKeyboardShortcuts: false });
        setFocusToBody(host);
    },
    [ROADMAP_ITEM_SELECTED]: ({ updateState })=>{
        updateState({
            colorByLegendTargetRef: null,
            openColorByLegendPopover: false
        });
    },
    [COLORBY_LEGEND_POPOVER_CLOSED]: ({ action, updateState, host })=>{
        const { payload: { sideBarFocus } }=action;

        action.stopPropagation();
        updateState({
            colorByLegendTargetRef: null,
            openColorByLegendPopover: false
        });
        if (!sideBarFocus) {
            return;
        }
        const sideBar=getSidebar(host);
        if (sideBar) {
            sideBar.focus();
        }
    }

};
