import { createElementFromNode } from '@servicenow/ui-renderer-snabbdom';

const ICON_SIZES = {
    sm: 12,
    md: 16,
    lg: 24,
    xl: 32
};

/* add icon svg to this list if it is missing. Refer design-system repo for the svg code*/
const icons = {
    arrowRightFill16: '<g stroke="none" fill="none"><path fill="currentColor" d="M13.086 9H1.003V8h12.083L8.439 3.354l.707-.708L15 8.5l-5.854 5.854-.707-.708z"></path></g>',
    arrowLeftFill16: '<g stroke="none" fill="none"><path d="M6.856 2.646l.708.708-4.647 4.645L15 8v1H2.917l4.647 4.646-.708.708L1.003 8.5l5.853-5.854z" fill="currentColor"></path></g>'
};

const camelize = (str) => {
    return str.replace(/^([A-Z])|[\s-_]+(\w)/g, function(match, p1, p2) {
        return p2 ? p2.toUpperCase() : p1.toLowerCase();
    });
};

export const generateIcon = (icon, size) => {
    if (!size) {
        size = 'md';
    }
    const iconSize = ICON_SIZES[size];
    const iconName = camelize(icon) + iconSize;
    if (!iconName || !icons[iconName]) {
        return null;
    }

    const wrapper = document.createElement('SPAN');

    wrapper.innerHTML = `<svg
        class="now-icon -${size} ${icon}"
        xmlns="http://www.w3.org/2000/svg"
        aria-hidden="true"
        viewBox="0 0 ${iconSize} ${iconSize}">
            ${icons[iconName]}
        </svg>
    `;

    const vnode = createElementFromNode(wrapper);

    return vnode.children[0];
};
