import { CALENDAR } from '../constants/constants';
import { TIMELINE_HEADER_SCALE_CHANGED } from '../constants/constants';
import { getTimelineDataByHeaderData, getTimelineDataByDate, getCustomTimelineFlatData } from '../helpers/timeline-helper';
import isEmpty from 'lodash/isEmpty';
import { REGULAR_CALENDAR_DEFAULT_SCALE } from '../constants/constants';
import { setMomentDefaultFormat } from '../helpers/data-moment-helper';

export const handleScaleChange = ({
    action: {
        payload
    },
    state: {
        flatHeaderData,
        properties: {
            timelineHeaderData,
            defaultTimeScale,
            dateTimeFormat,
            startDateTime,
            endDateTime,
            hostWidth
        },
        timelineData,
        possibleTimeScales
    },
    updateState,
    dispatch,
    timelineFlatHeaderData
}) => {
    const isRegularCalendar = isEmpty(timelineHeaderData.timelineData);
    const { type } = payload;
    let { visibleWidth, currentTimeScale } = timelineData;
    if (!isRegularCalendar) {
        possibleTimeScales = timelineHeaderData.calendarTimeScales;
        if (isEmpty(defaultTimeScale)) {
            throw new Error('defaultTimeScale should not be empty!');
        }
    }

    if (type === CALENDAR) {
        const { value } = payload;

        currentTimeScale = value[0];
    }

    if (!currentTimeScale) {
        if (!isEmpty(defaultTimeScale) &&
        isEmpty(possibleTimeScales.filter(item => item.id === defaultTimeScale))) {
            throw new Error('defaultTimeScale is not correct!');
        }
        currentTimeScale = defaultTimeScale || REGULAR_CALENDAR_DEFAULT_SCALE;
    }

    if (hostWidth) {
        visibleWidth = hostWidth;
    }

    flatHeaderData = isEmpty(flatHeaderData) ? timelineFlatHeaderData: flatHeaderData;
    const updatedTimelineData = isRegularCalendar
        ? getTimelineDataByDate({
            currentTimeScale,
            startDateTime,
            endDateTime,
            visibleWidth,
            possibleTimeScales,
            dateTimeFormat
        }) : getTimelineDataByHeaderData({
            currentTimeScale,
            flatHeaderData,
            visibleWidth,
            timelineData,
            possibleTimeScales,
            dateTimeFormat
        });
    const newTimelineData = {
        ...timelineData,
        ...updatedTimelineData,
        currentTimeScale
    };

    updateState({
        timelineData: newTimelineData,
        flatHeaderData,
        possibleTimeScales
    });
    const timelineDataForDispatch = { ...newTimelineData };

    delete timelineDataForDispatch.topLevelScales;
    delete timelineDataForDispatch.bottomLevelScales;
    delete timelineDataForDispatch.isTopLevel;

    dispatch(TIMELINE_HEADER_SCALE_CHANGED, timelineDataForDispatch);
};

export const initializeComponent = ({
    action,
    state,
    updateState,
    dispatch
}) => {
    const { timelineHeaderData: { timelineData }, dateTimeFormat, locale } = state.properties;
    const timelineFlatHeaderData = getCustomTimelineFlatData(timelineData);

    setMomentDefaultFormat({
        dateTimeFormat,
        locale
    });
    handleScaleChange({ action, state, updateState, dispatch, timelineFlatHeaderData });
};
