import { getCurrentDatePosition } from './util';
import { renderGroupHeader, renderGroupRows } from './group';
import { renderRow } from './row';
import { ifJsx, toggleFocusRing } from '../../util';
import { createRef } from '@seismic/snabbdom-renderer';
import { handleDrop, updateMousePos, updateScrollContainers } from '../bar/draggable';
import {
    TIMELINE_SCROLL_REF,
    ROADMAP_BODY_CLASS,
    ROADMAP_BODY_PADDING,
    GROUP_SECTION_WIDTH,
    ROWS_WRAPPER_CLASS,
    SCROLL_BAR_HEIGHT,
    NO_ITEM_TO_DISPLAY,
    DRAG_UNSCHEDULED_ITEM,
    ROADMAP_BODY, ITEM_DETAILS_PANEL
} from '../../constants';
import { moveFocusInBody } from './accessibility';
import isEmpty from 'lodash/isEmpty';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

export const renderBody = ({
    state: {
        properties: { canEditBar, showCurrentDateLine },
        settings,
        scheduledItems,
        groupedRoadmapItems,
        timelineData,
        activeRowPath,
        activeBarId,
        enableResizers,
        keyboardShortcutsOpenedOnce,
        dropZone,
        barResize,
        lastModifiedRoadmapItem,
        sidePanel,
        unscheduledItems,
        roadmapItems
    },
    dispatch,
    updateState,
    verticalScrollContainerRef,
    setRef,
    syncScroll
}) => {
    const itemDetailsPanel = {
        display: sidePanel.display && sidePanel.panelName === ITEM_DETAILS_PANEL,
        id: sidePanel.lastBarSelected
    };
    const groupSectionWidth = GROUP_SECTION_WIDTH;
    const {
        isRTL,
        groupBy
    } = settings;
    const lastModifiedRoadmapItemId = !isEmpty(lastModifiedRoadmapItem) ? lastModifiedRoadmapItem.id.value : -1;
    const timelineTotalWidth = timelineData? timelineData.totalWidth: 0;
    const groupsSectionJsx = [];
    const horizontalScrollContainerRef = createRef();
    const { dragClientX, startClientX, start } = dropZone;
    const dragDistance = dragClientX - startClientX;
    const dragLinePos = isRTL ? start - dragDistance : start + dragDistance;

    setRef('roadmapBodyRef', horizontalScrollContainerRef, TIMELINE_SCROLL_REF);
    const rowsSectionJsx = groupedRoadmapItems.rows.map((row, index) => {
        return renderRow({ row, settings, dispatch, canEditBar, activeBarId, enableResizers, timelineData,
            groupByValues: [{ value: index.toString(), displayValue: index.toString() }],
            horizontalScrollContainerRef, barResize, lastModifiedRoadmapItemId, itemDetailsPanel });
    });

    groupedRoadmapItems.groups.forEach(group => {
        const groupByValues = [{ value: group.value, displayValue: group.displayValue }];

        const groupRowsJsx = group.groups.length === 0
            ? <div className="sn-roadmap-body-group">
                { renderGroupRows({ group, settings, canEditBar, activeBarId, enableResizers, timelineData, dispatch, groupByValues,
                    horizontalScrollContainerRef, barResize, lastModifiedRoadmapItemId, itemDetailsPanel }) }
            </div> : renderGroupRows({ group, settings, canEditBar, activeBarId, enableResizers, timelineData, dispatch, groupByValues,
                horizontalScrollContainerRef, barResize, lastModifiedRoadmapItemId, itemDetailsPanel });

        rowsSectionJsx.push(groupRowsJsx);
        groupsSectionJsx.push(renderGroupHeader({ updateState, group, settings, groupSectionWidth, activeRowPath }));
    });

    let currentDateStyle;
    let displayCurrentDate = showCurrentDateLine && !isEmpty(scheduledItems);
    if (showCurrentDateLine) {
        let pos = getCurrentDatePosition(timelineData);
        if (pos > -1) {
            pos = isRTL? pos*-1: pos;
            currentDateStyle = {
                transform: 'translateX('+ pos + 'px)'
            };
            if (isEmpty(groupBy)) {
                currentDateStyle.height = `calc(100% - ${ROADMAP_BODY_PADDING-1}px)`;
            }
        } else {
            displayCurrentDate = false;
        }
    }

    let mouseDown = false;
    const openKeyboardShortcutModal = () => {
        if (keyboardShortcutsOpenedOnce || mouseDown) {
            return;
        }
        updateState({
            openKeyboardShortcuts: true,
            keyboardShortcutsOpenedOnce: true
        });
        mouseDown = false;
    };

    const dragLineStyle = isRTL ? {
        right: dragLinePos.toString() + 'px'
    } : {
        left: dragLinePos.toString() + 'px'
    };

    const virtualScrollStyle = isRTL ? {
        paddingRight: isEmpty(groupBy) ? 0 : groupSectionWidth + ROADMAP_BODY_PADDING + 'px',
        left: ROADMAP_BODY_PADDING + 'px'
    } : {
        paddingLeft: isEmpty(groupBy) ? 0 : groupSectionWidth + ROADMAP_BODY_PADDING + 'px',
        right: ROADMAP_BODY_PADDING + 'px'
    };

    const bodyRowsContainerStyle = isRTL ? {
        marginRight: !isEmpty(groupBy) && isEmpty(scheduledItems) ? groupSectionWidth + 'px' : '0px'
    } : {
        marginLeft: !isEmpty(groupBy) && isEmpty(scheduledItems) ? groupSectionWidth + 'px' : '0px'
    };

    const noRecordContainerStyle = isRTL ? {
        marginRight: !isEmpty(groupBy) && isEmpty(scheduledItems) ? -groupSectionWidth + 'px' : '0px'
    } : {
        marginLeft: !isEmpty(groupBy) && isEmpty(scheduledItems) ? -groupSectionWidth + 'px' : '0px'
    };

    const renderVirtualScrollbars = ({
        timelineData,
        setRef,
        syncScroll
    }) => {
        const virtualTimelineScrollRef = createRef();

        setRef('virtualTimelineScroll', virtualTimelineScrollRef, TIMELINE_SCROLL_REF);

        return (
            <div className="virtual-scrollbar-container" style={virtualScrollStyle}>
                <div
                    tabindex="-1"
                    className="scrollbar"
                    ref={virtualTimelineScrollRef}
                    on-scroll={e =>
                        syncScroll(e, TIMELINE_SCROLL_REF, 'virtualscroll')
                    }>
                    <div
                        className="inner-scroller"
                        style={{
                            width: `${timelineData.totalWidth}px`
                        }}
                    ></div>
                </div>
            </div>
        );
    };

    return (
        <div
            aria-label={ROADMAP_BODY}
            className={ROADMAP_BODY_CLASS}
            tabIndex="0"
            on-focus={() => openKeyboardShortcutModal()}
            on-blur={(e) => toggleFocusRing(e.target, true)}
            on-mousedown={() => {
                mouseDown = true;
            }}
            on-keydown={(e) => moveFocusInBody(e)}
            style={{ paddingBottom: isEmpty(groupBy) ? SCROLL_BAR_HEIGHT + 'px' : '0' }}>
            {
                ifJsx(groupsSectionJsx.length > 0,
                    <div className={ `sn-roadmap-body-groups-container${isRTL? ' rtl': ''}`}
                        style={{ width: `${groupSectionWidth-ROADMAP_BODY_PADDING}px` }}>
                        { groupsSectionJsx }
                    </div>)
            }
            <div tabindex="-1" className="sn-roadmap-body-rows-container" style={bodyRowsContainerStyle}
                ref={horizontalScrollContainerRef}
                hook-insert={ () => updateScrollContainers(verticalScrollContainerRef, horizontalScrollContainerRef)}
                hook-update={() => updateScrollContainers(verticalScrollContainerRef, horizontalScrollContainerRef)}
                on-scroll={e => syncScroll(e, TIMELINE_SCROLL_REF, 'body')}
                ondragover={e => updateMousePos(e)}>
                {
                    ifJsx(displayCurrentDate, <div className="sn-roadmap-body-current-date-line" style={currentDateStyle}/>)
                }
                <div className='sn-roadmap-body-drag-line' id='roadmapBodyDragLine' style={dragLineStyle}/>
                { ifJsx(roadmapItems.length === 0,
                    <div className='sn-roadmap-no-record-container'>
                        <now-illustration size='lg' illustration='no-search-results'/>
                        <span className='sn-roadmap-no-record-title'>{ NO_ITEM_TO_DISPLAY }</span>
                    </div>)
                }
                { ifJsx(scheduledItems.length === 0 && unscheduledItems.length !== 0,
                    <div className='sn-roadmap-no-record-container' style={noRecordContainerStyle}
                        ondragover={e => e.preventDefault()} tabIndex="-1"
                        ondrop={e => handleDrop(e, dispatch, horizontalScrollContainerRef)}>
                        <now-illustration size='lg' illustration='no-search-results'/>
                        <span className='sn-roadmap-no-record-title'>{ DRAG_UNSCHEDULED_ITEM }</span>
                    </div>)
                }
                <div tabindex="-1" className={ROWS_WRAPPER_CLASS} style={{ width: `${timelineTotalWidth}px` }}>
                    {rowsSectionJsx}
                </div>
            </div>
            { ifJsx(scheduledItems.length > 0, renderVirtualScrollbars({
                settings,
                timelineData,
                setRef,
                syncScroll
            }))}
        </div>
    );
};
