import moment from 'moment';

import isString from 'lodash/isString';
import { TIME_UNITS, DEFAULT_DATE_TIME_FORMAT, DEFAULT_DATE_FORMAT, DEFAULT_LOCALE } from '../constants/constants';

const {
    MONTH,
    QUARTER
} = TIME_UNITS;

let defaultFormat;
let defaultDateFormat;
let currentTimeZone;

const localeMap = { en: 'en', es: 'es', it: 'it', et: 'et', zt: 'zh-cn', xl: 'x-pseudo', fi: 'fi', pb: 'pt-br', pl: 'pl', fr: 'fr', ko: 'ko', pt: 'pt', ja: 'ja', ru: 'ru', tr: 'tr', zh: 'zh-cn', sv: 'sv', he: 'he', nb: 'nb', hu: 'hu', cs: 'cs', de: 'de', th: 'th', fq: 'fa-ca', nl: 'nl' };

export const setMomentDefaultFormat = ({
    dateFormat = DEFAULT_DATE_FORMAT,
    dateTimeFormat = DEFAULT_DATE_TIME_FORMAT,
    locale = DEFAULT_LOCALE,
    timeZone = null
}) => {
    let mapdLocale = localeMap[locale];
    if (!mapdLocale) {
        mapdLocale = DEFAULT_LOCALE;
    }
    moment.locale(mapdLocale);
    defaultFormat = dateTimeFormat;
    defaultDateFormat = dateFormat;
    currentTimeZone = timeZone;
};

const getDate = (date) => {
    if (!date) {
        return moment(moment(), currentTimeZone);
    } else if (isString(date)) {
        return moment(date, defaultFormat);
    } else if (date instanceof moment) {
        return date.clone();
    } else if (date instanceof Date) {
        return moment(date);
    } else {
        return date;
    }
};

const getToday = () => {
    return getDate();
};

const getFormattedValue = (date, format) => {
    return getDate(date).format(format);
};

const getDateFormattedValue= (date) => {
    return getDate(date).format(defaultDateFormat);
};

const getDateTimeFormattedValue = (date) => {
    return getDate(date).format(defaultFormat);
};

// Moment diff supports these measurement units: years, months, weeks, days, hours, minutes, and seconds
const getDiff = (start, end, unit) => {
    const _start = getDate(start);
    const _end = getDate(end);

    return Math.abs(_start.diff(_end, unit, true));
};

const getDiffOfUnit = (start, end, unit) => {
    return unit === QUARTER
        ? getDiff(start, end, MONTH) / 3
        : getDiff(start, end, unit);
};

const getStartOfUnit = (date, unit) => {
    return getDate(date).startOf(unit);
};

const getEndOfUnit = (date, unit) => {
    return getDate(date).endOf(unit);
};

const getPrevUnit = (date, unit, count = 1) => {
    return getDate(date).subtract(count, unit);
};

const getNextUnit = (date, unit, count = 1) => {
    return getDate(date).add(count, unit);
};

const isAfter = (start, end) => {
    return getDate(start).isAfter(end);
};

const getDurationInMilliseconds = (count = 1, unit) => {
    return moment.duration(count, unit).asMilliseconds();
};

export default {
    isAfter,
    getNextUnit,
    getPrevUnit,
    getEndOfUnit,
    getStartOfUnit,
    getDiffOfUnit,
    getFormattedValue,
    getDateTimeFormattedValue,
    getDateFormattedValue,
    getDate,
    getToday,
    getDurationInMilliseconds
};
