import moment from 'moment';
import {
    getDiffInUnits,
    getDate,
    getDateDisplayValueFormat,
    getAddedDateTime,
    getFormattedDate,
    getDateAndTimeFormat,
    getUtcFormat
} from '../../date-util';
import {
    BAR_COLORS,
    NEUTRAL6_COLOR,
    EMPTY_GROUP,
    TIME_UNITS_WITH_DURATION_IN_DAYS,
    RTL
} from '../../constants';
import isEmpty from 'lodash/isEmpty';
import inRange from 'lodash/inRange';
import ItemMetadata from '../../item-metadata';
import ItemMetaData from '../../item-metadata';
import { getGroupedRows } from '../../plot-rows';

const getTimeScaleWidth = (startDate, endDate, timelineData) => {
    if (!timelineData.scaleUnit) {
        return;
    }
    const { scaleUnit, scaleUnitCount, scaleWidth } = timelineData;
    let difference = getDiffInUnits(startDate, endDate, scaleUnit);

    difference = Number(difference.toFixed(3));

    return difference/scaleUnitCount * scaleWidth;
};

export const getDuration = (timelineData, distance) => {
    const { scaleUnit, scaleUnitCount, scaleWidth } = timelineData;
    const difference = distance * scaleUnitCount / scaleWidth;

    return moment.duration(difference, scaleUnit);
};

export const getTimelineDayWidth = (timelineData) => {
    const { scaleUnit, scaleUnitCount, scaleWidth } = timelineData;

    if (scaleWidth === 0) {
        return 0;
    }

    const durationInDays = TIME_UNITS_WITH_DURATION_IN_DAYS[scaleUnit];

    return durationInDays > 0 ? scaleWidth * scaleUnitCount / durationInDays : 0;
};

export const getTimelineHourWidth = (timelineData) => {
    const dayWidth = getTimelineDayWidth(timelineData);

    return dayWidth > 0 ? 24/dayWidth : 0;
};

export const getDatesForUnscheduledItem = (start, timelineData) => {
    const timelineStartDate = timelineData.startDateTime.displayValue;
    const duration = getDuration(timelineData, start);
    const updatedStartDateDV = getAddedDateTime(timelineStartDate, duration.asDays(), 'd');
    const updatedEndDateDV = getAddedDateTime(updatedStartDateDV, 30, 'd');
    const updatedStartDate = getUtcFormat(updatedStartDateDV);
    const updatedEndDate = getUtcFormat(updatedEndDateDV);

    return {
        updatedStartDate: { value: updatedStartDate, displayValue: getFormattedDate(updatedStartDateDV) },
        updatedEndDate: { value: updatedEndDate, displayValue: getFormattedDate(updatedEndDateDV) }
    };
};

export const getDatesWithBarData = (distance, startDate, endDate, timelineData, isRTL) => {
    const barPeriod = {
        updatedStartDate: {},
        updatedEndDate: {}
    };

    if (!timelineData) {
        return barPeriod;
    }
    distance = isRTL ? -1 * distance : distance;
    const duration = getDuration(timelineData, distance);
    const updatedStartDateDV = getAddedDateTime(startDate.displayValue, duration.asDays(), 'd');
    const updatedEndDateDV = getAddedDateTime(endDate.displayValue, duration.asDays(), 'd');
    const updatedStartDate = getUtcFormat(updatedStartDateDV);
    const updatedEndDate = getUtcFormat(updatedEndDateDV);

    barPeriod.updatedStartDate = { value: updatedStartDate, displayValue: getFormattedDate(updatedStartDateDV) };
    barPeriod.updatedEndDate = { value: updatedEndDate, displayValue: getFormattedDate(updatedEndDateDV) };

    return { ...barPeriod };
};

const getDateObjectWithDuration = (startDate, endDate, duration, timelineData, isStartDate) => {
    const timelineStartDateDV = getDate(timelineData.startDateTime.displayValue);
    const timelineEndDateDV = getDate(timelineData.endDateTime.displayValue);
    const dateDisplayValueFormat = getDateDisplayValueFormat();
    const formatObj = getDateAndTimeFormat(dateDisplayValueFormat);

    if (isStartDate) {
        const endDateDV = getDate(endDate.displayValue);
        const updatedEndDateDV = endDateDV > timelineEndDateDV ? timelineEndDateDV : endDateDV;
        const updatedStartDateDV = updatedEndDateDV.clone().subtract(duration.asDays(), 'd');
        let newStartDateDV = updatedStartDateDV;
        if (formatObj.timeFormat) {
            const sDateDisValueTime = getDate(startDate.displayValue).format(formatObj.timeFormat);

            newStartDateDV = getDate(updatedStartDateDV.format(formatObj.dateFormat) + ' ' + sDateDisValueTime);
        }
        let newStartDate = '';
        if (newStartDateDV < timelineStartDateDV) {
            newStartDateDV = timelineStartDateDV;
            newStartDate = timelineData.startDateTime.value;
        } else {
            newStartDate = getUtcFormat(newStartDateDV);
        }

        return {
            value: newStartDate,
            displayValue: newStartDateDV.format(dateDisplayValueFormat)
        };
    } else {
        const startDateDV = getDate(startDate.displayValue);
        const updatedStartDateDV = startDateDV < timelineStartDateDV ? timelineStartDateDV : startDateDV;
        const updatedEndDateDV = updatedStartDateDV.clone().add(duration.asDays(), 'd');
        let newEndDateDV = updatedEndDateDV;
        let newEndDateValue = '';
        if (formatObj.timeFormat) {
            const eDateDisValueTime = getDate(endDate.displayValue).format(formatObj.timeFormat);

            newEndDateDV = getDate(updatedEndDateDV.format(formatObj.dateFormat) + ' ' + eDateDisValueTime);
        }
        if (newEndDateDV > timelineEndDateDV) {
            newEndDateDV = timelineEndDateDV;
            newEndDateValue = timelineData.endDateTime.value;
        } else {
            newEndDateValue = getUtcFormat(newEndDateDV);
        }

        return {
            value: newEndDateValue,
            displayValue: newEndDateDV.format(dateDisplayValueFormat)
        };
    }
};

export const getResizedDate = (startDate, endDate, newBarWidth, timelineData, isStartDate) => {
    if (!timelineData) {
        return endDate;
    }
    const duration = getDuration(timelineData, newBarWidth);

    return getDateObjectWithDuration(startDate, endDate, duration, timelineData, isStartDate);
};

const getBarPositions = (roadmapItem, timelineData) => {
    const { startDateField, endDateField } = ItemMetadata.get();
    const timelineStartDate = getDate(timelineData.startDateTime.displayValue);
    const timelineEndDate = getDate(timelineData.endDateTime.displayValue);
    const itemStartDateDisplayValue = roadmapItem[startDateField.name].displayValue;
    const itemEndDateDisplayValue = roadmapItem[endDateField.name].displayValue;
    const itemStartDate = getDate(itemStartDateDisplayValue);
    const itemEndDate = getDate(itemEndDateDisplayValue);
    const isLeftOverflow = timelineStartDate > itemStartDate;
    const isRightOverflow = itemEndDate > timelineEndDate;

    const left = isLeftOverflow ? 0 : getTimeScaleWidth(timelineStartDate, itemStartDate, timelineData);
    const right = isRightOverflow ? timelineData.totalWidth : getTimeScaleWidth(timelineStartDate, itemEndDate, timelineData);

    return {
        isLeftOverflow,
        isRightOverflow,
        left,
        right
    };
};

const getBarColor = (roadmapItem, settings)=> {
    let barColor = BAR_COLORS[0];
    if (isEmpty(settings) || isEmpty(settings.colorBy)) {
        return barColor;
    }
    const { colorBy, colorMap } = settings;
    const colorByVal = roadmapItem[colorBy];
    if (isEmpty(colorByVal) || isEmpty(colorByVal.value)) {
        return NEUTRAL6_COLOR;
    }

    const fieldValue = colorByVal.value;
    if (colorMap.has(fieldValue)) {
        barColor = colorMap.get(fieldValue).color;
    }

    return barColor;
};

export const getBarProperties = (roadmapItem, settings, timelineData) => {
    const { isLeftOverflow, isRightOverflow, left, right } = getBarPositions(roadmapItem, timelineData);
    const width = right - left;
    const color = getBarColor(roadmapItem, settings);

    return {
        isLeftOverflow,
        isRightOverflow,
        left,
        width,
        color
    };
};

export const getCurrentDatePosition = (timelineData) => {
    const { startDateTime, endDateTime } = timelineData;
    const today = getDate();

    if (today < getDate(startDateTime.displayValue) || today > getDate(endDateTime.displayValue)) {
        return -1;
    }

    return getTimeScaleWidth(startDateTime.displayValue, today, timelineData);
};

export const getGroupColor = (groupName, grpIndex, settings)=> {
    let groupColor = BAR_COLORS[0];
    const { colorBy, colorMap, groupBy } = settings;

    if (!colorBy) {
        return groupColor;
    }

    if (groupBy[grpIndex] !== colorBy || groupName === EMPTY_GROUP) {
        return NEUTRAL6_COLOR;
    }

    if (colorMap.has(groupName)) {
        groupColor = colorMap.get(groupName).color;
    }

    return groupColor;
};

export const setGroupTooltipRef = (element, updateState, text = null) => {
    if (element === null) {
        updateState({ groupTooltip: { ref: null, text: '' } });

        return;
    }
    if (element.offsetWidth < element.scrollWidth) { // if text overflow
        updateState({ groupTooltip: { ref: element, text: text? text: element.textContent } });
    }
};

const isOverlap = (roadmapItemA, roadmapItemB) => {
    const { startDateField, endDateField }=ItemMetadata.get();
    const startDateA = getDate(roadmapItemA[startDateField.name].displayValue);
    const endDateA = getDate(roadmapItemA[endDateField.name].displayValue);
    const startDateB = getDate(roadmapItemB[startDateField.name].displayValue);
    const endDateB = getDate(roadmapItemB[endDateField.name].displayValue);
    const startDateOverlaps = inRange(startDateA, startDateB, endDateB) || inRange(startDateB, startDateA, endDateA);
    const endDateOverlaps = inRange(endDateA, startDateB, endDateB) || inRange(endDateB, startDateA, endDateA);

    return startDateOverlaps || endDateOverlaps;
};

const findNextSortedIndex = (sortedItemList, sortItemField, itemValueToInsert) => {
    if (isEmpty(sortedItemList) || sortedItemList.length === 0) {
        return 0;
    }

    if (itemValueToInsert === EMPTY_GROUP) {
        return sortedItemList.length;
    }

    for (const [index, item] of sortedItemList.entries()) {
        if (itemValueToInsert < item[sortItemField]) {
            return index;
        }
    }

    return sortedItemList[sortedItemList.length - 1][sortItemField] === EMPTY_GROUP ? sortedItemList.length - 1 : sortedItemList.length;
};

const traverseToRow = (groupedRoadmapItems, groupByValues) => {
    let groups = groupedRoadmapItems.groups && groupedRoadmapItems.groups.length > 0
        ? groupedRoadmapItems.groups : groupedRoadmapItems.rows;
    let rowDetails = {};

    groupByValues.forEach((groupByValue, index) => {
        const updatedGroupByValue = {
            displayValue: groupByValue && groupByValue.displayValue ? groupByValue.displayValue : EMPTY_GROUP,
            value: groupByValue && groupByValue.value ? groupByValue.value : EMPTY_GROUP
        };

        if (index === groupByValues.length - 1) {
            rowDetails = {
                rows: groups,
                targetRowIndex: parseInt(updatedGroupByValue.value)
            };
        } else {
            let groupIndex = groups.findIndex(group => group.value === updatedGroupByValue.value);
            if (index !== groupByValues.length - 2) {
                if (groupIndex === -1) {
                    groupIndex = findNextSortedIndex(groups, 'displayValue', updatedGroupByValue.displayValue);
                    groups.splice(groupIndex, 0, {
                        ...updatedGroupByValue,
                        groups: [],
                        rows: []
                    });
                }
                groups = groups[groupIndex].groups;
            } else {
                if (groupIndex === -1) {
                    groupIndex = findNextSortedIndex(groups, 'displayValue', updatedGroupByValue.displayValue);
                    groups.splice(groupIndex, 0, {
                        ...updatedGroupByValue,
                        groups: [],
                        rows: [[]]
                    });
                }
                groups = groups[groupIndex].rows;
            }
        }
    });

    return rowDetails;
};

const addToGroupedRoadmapItems = (roadmapItem, groupedRoadmapItems, targetGroupByValues, moveDirection) => {
    const { startDateField }=ItemMetadata.get();
    const { rows, targetRowIndex } = traverseToRow(groupedRoadmapItems, targetGroupByValues);
    const targetRow = rows[targetRowIndex];
    const overlappingBars = [];
    const barIndexesToRemove = [];
    const insertRowIndex = targetRowIndex + moveDirection;

    for (const [index, barInRow] of targetRow.entries()) {
        if (barInRow.id.value !== roadmapItem.id.value && isOverlap(barInRow, roadmapItem)) {
            overlappingBars.push(barInRow);
            barIndexesToRemove.push(index);
        }
    }

    if (overlappingBars.length > 0) {
        for (let index = barIndexesToRemove.length - 1; index >= 0; index--) {
            targetRow.splice(barIndexesToRemove[index], 1);
        }
        rows.splice(insertRowIndex, 0, overlappingBars);
    }

    let insertBarIndex = -1;
    for (const [index, bar] of targetRow.entries()) {
        if (getDate(bar[startDateField.name].displayValue) > getDate(roadmapItem[startDateField.name].displayValue)) {
            insertBarIndex = index;
            break;
        }
    }

    insertBarIndex === -1 ? targetRow.push(roadmapItem) : targetRow.splice(insertBarIndex, 0, roadmapItem);
};

const removeFromGroupedRoadmapItems = (barId, groupedRoadmapItems, groupByValues) => {
    const { rows, targetRowIndex } = traverseToRow(groupedRoadmapItems, groupByValues);
    const targetRow = rows[targetRowIndex];
    const markedBarIndex = targetRow.findIndex(bar => bar.id === barId);

    targetRow.splice(markedBarIndex, 1);

    return targetRow.length;
};

const clearEmptyRows = (groupedRoadmapItems, groupByValues) => {
    const { rows } = traverseToRow(groupedRoadmapItems, groupByValues);
    const targetRowIndex = rows.findIndex(row => row.length === 0);
    if (targetRowIndex !== -1) {
        const targetRow = rows[targetRowIndex];
        if (targetRow.length === 0 && rows.length > 1) {
            rows.splice(targetRowIndex, 1);
        }
    }
};

const isSameGroup = (srcPath, targetPath) => {
    if (isEmpty(srcPath)) {
        return false;
    }

    for (const [index, srcValue] of srcPath.entries()) {
        if (index !== srcPath.length - 1 && JSON.stringify(srcValue) !== JSON.stringify(targetPath[index])) {
            return false;
        }
    }

    return true;
};

export const updateGroupedRoadmapItem = (updatedRoadmapItem, groupedRoadmapItems,
    srcGroupByValues, targetGroupByValues, distanceY) => {
    const isMoveWithinSameRow = JSON.stringify(srcGroupByValues) === JSON.stringify(targetGroupByValues);
    const isMoveWithinSameGroup = isSameGroup(srcGroupByValues, targetGroupByValues);
    let moveDirection = 1;

    if (isEmpty(srcGroupByValues)) {
        addToGroupedRoadmapItems(updatedRoadmapItem, groupedRoadmapItems, targetGroupByValues, moveDirection);
    } else {
        const srcRowBarCount = removeFromGroupedRoadmapItems(updatedRoadmapItem.id, groupedRoadmapItems, srcGroupByValues);
        if (distanceY > 0 && isMoveWithinSameGroup && srcRowBarCount === 0) {
            moveDirection = 0;
        }
        addToGroupedRoadmapItems(updatedRoadmapItem, groupedRoadmapItems, targetGroupByValues, moveDirection);
        if (!isMoveWithinSameRow) {
            clearEmptyRows(groupedRoadmapItems, srcGroupByValues);
        }
    }
};

export const getItemMetricFieldsWithValue = (roadmapItem, metricFields)=> {
    return metricFields.map(field => {
        const { name, label } = field;
        const value = isEmpty(roadmapItem[name]) ? '0' : roadmapItem[name].displayValue;

        return {
            name,
            label,
            value
        };
    });
};

export const getUpdatedGroupBy = (groupBy, roadmapItem) => {
    const updatedGroupBy = {};

    groupBy.forEach((groupByField) => {
        updatedGroupBy[groupByField] = roadmapItem[groupByField];
    });

    return updatedGroupBy;
};

export const buildGroupByValues = (groupBy, targetPath) => {
    const updatedGroupBy = {};

    if (!isEmpty(targetPath)) {
        groupBy.forEach((groupByField, index) => {
            const updatedTargetPath = targetPath[index] ? targetPath[index] : { value: '', displayValue: '' };
            const dstGroup = updatedTargetPath.value;

            if (dstGroup === EMPTY_GROUP) {
                updatedGroupBy[groupByField] = {
                    value: '',
                    displayValue: ''
                };

                return updatedGroupBy;
            }
            updatedGroupBy[groupByField] = {
                value: updatedTargetPath.value,
                displayValue: updatedTargetPath.displayValue
            };
        });
    }

    return updatedGroupBy;
};

export const updateDroppedItem = ({ state, action: { payload } }) => {
    const { startDateField, endDateField } = ItemMetaData.get();
    const { id, dragDistance, startDate, endDate, groupByValues, srcGroupByValues } = payload;
    const isUnscheduledItemDropped = isEmpty(srcGroupByValues);
    const { scheduledItems, unscheduledItems, timelineData, settings, overrideGroupModal, dropZone: { barStart },
        properties: { dir, roadmapItemMetadata: { groupableFields } } } = state;
    let { groupedRoadmapItems } = state;
    const { groupBy } = settings;
    const isEmptyRoadmap = isEmpty(scheduledItems);
    const isRTL = dir === RTL;
    const { updatedStartDate, updatedEndDate } = !isUnscheduledItemDropped
        ? getDatesWithBarData(dragDistance.x, startDate, endDate, timelineData, isRTL)
        : getDatesForUnscheduledItem(barStart, timelineData);
    let lastModifiedRoadmapItem = {};
    const updatedGroupBy = buildGroupByValues(groupBy, groupByValues);

    if (isUnscheduledItemDropped) {
        const droppedItemIndex = unscheduledItems.findIndex(item => item.id.value === id);
        if (droppedItemIndex !== -1) {
            const droppedItem = unscheduledItems[droppedItemIndex];
            if (!isEmpty(groupBy) && !isEmptyRoadmap) {
                const mismatchedGroupByValues = [];
                for (const groupByField of groupBy) {
                    if (JSON.stringify(droppedItem[groupByField]) !== JSON.stringify(updatedGroupBy[groupByField])) {
                        const filteredGroupableFields = groupableFields.filter(field => field.name === groupByField);

                        mismatchedGroupByValues.push(!isEmpty(filteredGroupableFields) ? filteredGroupableFields[0].label : groupByField);
                    }
                }
                if (!isEmpty(mismatchedGroupByValues)) {
                    const groupModalPayload = {
                        ...overrideGroupModal,
                        display: true,
                        droppedItem,
                        droppedItemIndex,
                        targetPath: groupByValues.length > 1 ? groupByValues.slice(0, groupByValues.length-1) : groupByValues,
                        updatedStartDate,
                        updatedEndDate,
                        mismatchedGroupByValues
                    };

                    return {
                        unscheduledItems,
                        scheduledItems,
                        groupModalPayload,
                        groupedRoadmapItems
                    };
                }
            }

            lastModifiedRoadmapItem = {
                ...droppedItem,
                ...updatedGroupBy,
                [startDateField.name]: updatedStartDate,
                [endDateField.name]: updatedEndDate,
                settings
            };
            unscheduledItems.splice(droppedItemIndex, 1);
            scheduledItems.push(lastModifiedRoadmapItem);
            if (isEmptyRoadmap) {
                groupedRoadmapItems = getGroupedRows(scheduledItems, groupBy);
            }
        }
    } else {
        const scheduledItemIndex = scheduledItems.findIndex(item => item.id.value === id);
        if (scheduledItemIndex !== -1) {
            const scheduledItem = scheduledItems[scheduledItemIndex];

            lastModifiedRoadmapItem = {
                ...scheduledItem,
                ...updatedGroupBy,
                [startDateField.name]: updatedStartDate,
                [endDateField.name]: updatedEndDate
            };
            scheduledItems.splice(scheduledItemIndex, 1, lastModifiedRoadmapItem);
        }
    }

    const updatedItemInfo = {
        ...updatedGroupBy,
        id: lastModifiedRoadmapItem.id,
        [startDateField.name]: updatedStartDate,
        [endDateField.name]: updatedEndDate
    };

    if (!isEmptyRoadmap) {
        updateGroupedRoadmapItem(
            lastModifiedRoadmapItem,
            groupedRoadmapItems,
            srcGroupByValues,
            groupByValues,
            dragDistance.y);
    }

    return {
        unscheduledItems,
        scheduledItems,
        lastModifiedRoadmapItem,
        groupedRoadmapItems,
        updatedItemInfo,
        groupModalPayload: {}
    };
};

export const updateDroppedUnscheduledItem = ({ isOverride, state }) => {
    const { startDateField, endDateField } = ItemMetaData.get();
    const {
        unscheduledItems,
        scheduledItems,
        groupedRoadmapItems,
        settings: { groupBy },
        overrideGroupModal: { droppedItem, droppedItemIndex, updatedStartDate, updatedEndDate }
    } = state;

    let { targetPath } = state.overrideGroupModal;
    if (!isOverride) {
        targetPath = groupBy.map(groupByField => {
            return droppedItem[groupByField];
        });
    }
    targetPath.push({ value: '0', displayValue: '0' });
    const updatedGroupBy = buildGroupByValues(groupBy, targetPath);
    const lastModifiedRoadmapItem = isOverride ? {
        ...droppedItem,
        ...updatedGroupBy,
        [startDateField.name]: updatedStartDate,
        [endDateField.name]: updatedEndDate
    } : {
        ...droppedItem,
        [startDateField.name]: updatedStartDate,
        [endDateField.name]: updatedEndDate
    };

    unscheduledItems.splice(droppedItemIndex, 1);
    scheduledItems.push(lastModifiedRoadmapItem);
    updateGroupedRoadmapItem(
        lastModifiedRoadmapItem,
        groupedRoadmapItems,
        {},
        targetPath,
        0);
    const updatedItemInfo = isOverride ? {
        ...updatedGroupBy,
        id: lastModifiedRoadmapItem.id,
        [startDateField.name]: updatedStartDate,
        [endDateField.name]: updatedEndDate
    } : {
        id: lastModifiedRoadmapItem.id,
        [startDateField.name]: updatedStartDate,
        [endDateField.name]: updatedEndDate
    };

    return {
        unscheduledItems,
        scheduledItems,
        groupedRoadmapItems,
        lastModifiedRoadmapItem,
        updatedItemInfo
    };
};
