import isEmpty from 'lodash/isEmpty';
import {
    DROPDOWN_PANEL_ITEM_CLASSNAME,
    SELECTED_ITEM_FOCUS_TIMEOUT
} from './constants';

let focusItemName;

export const setFocusItemName = (name) => {
    focusItemName = name;
};

export const getFocusItemName = () => {
    return focusItemName;
};

export const delayedFocusSelectedItem = (elm, selectedItem) => {
    setTimeout(() => {
        const listItems = elm.shadowRoot.querySelectorAll(DROPDOWN_PANEL_ITEM_CLASSNAME);
        if (!isEmpty(listItems)) {
            if (isEmpty(selectedItem)) {
                listItems[0].focus();
            } else {
                for (const listItem of listItems) {
                    if (listItem.id.includes(selectedItem)) {
                        listItem.focus();
                    }
                }
            }
        }
    }, SELECTED_ITEM_FOCUS_TIMEOUT);
};

export const delayedFocusLastModifiedPillar = (elm) => {
    setTimeout(() => {
        if (!isEmpty(focusItemName)) {
            const editIcon = elm.querySelector('.edit-' + focusItemName);
            if (editIcon) {
                editIcon.focus();
                setFocusItemName('');
            }
        }
    }, SELECTED_ITEM_FOCUS_TIMEOUT);
};

export const delayedFocusAddFieldList = (addFieldList) => {
    setTimeout(() => {
        if (!isEmpty(focusItemName) && focusItemName.includes('add-field')) {
            if (addFieldList) {
                addFieldList.focus();
                setFocusItemName('');
            }
        }
    }, SELECTED_ITEM_FOCUS_TIMEOUT);
};
