import isEmpty from 'lodash/isEmpty';
import { renderRow } from './row';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { getGroupColor, setGroupTooltipRef } from './util';
import {
    PARENT_GROUPS_HEIGHT,
    PARENT_GROUP_INDICATOR_WIDTH,
    PARENT_GROUP_INDICATOR_HEIGHT,
    ROW_BOTTOM_MARGIN,
    PARENT_GROUP_CLASS,
    LEAF_GROUP_CLASS,
    NEUTRAL6_COLOR,
    GROUP_SECOND_LEVEL_INDENTATION
} from '../../constants';
import { moveFocusInBody } from './accessibility';
import { ifJsx, toggleFocusRing } from '../../util';
import { updateDropTargetPath } from '../bar/barUtils';

export function renderGroupHeader({
    updateState,
    group,
    settings,
    groupSectionWidth,
    activeRowPath,
    highlight = true,
    color = null,
    level = 0,
    groupByValues = [] }
) {
    const { rowHeight } = settings;
    const childrenJSX = [];
    if (isEmpty(color) || color === NEUTRAL6_COLOR) {
        color = getGroupColor(group.value, level, settings);
    }
    highlight = highlight && activeRowPath.length > 0 && activeRowPath[level] === group.value;
    groupByValues.push({ value: group.value, displayValue: group.displayValue });
    group.groups.forEach(subGroup => {
        const subGrpWidth = groupSectionWidth - GROUP_SECOND_LEVEL_INDENTATION;

        childrenJSX.push(renderGroupHeader({ updateState, group: subGroup, settings, groupSectionWidth: subGrpWidth,
            activeRowPath, highlight, color, level: level+1, groupByValues: [...groupByValues] }));
    });

    let groupIndicatorWidth = PARENT_GROUP_INDICATOR_WIDTH;
    let groupIndicatorHeight = PARENT_GROUP_INDICATOR_HEIGHT;
    const rowsCount = group.rows.length;
    const rowsExist = rowsCount > 0;
    let headerHeight = rowsExist? rowHeight: PARENT_GROUPS_HEIGHT;
    let attrs = {};
    if (rowsExist) {
        headerHeight = rowHeight*rowsCount + (rowsCount-1) * ROW_BOTTOM_MARGIN;
        groupIndicatorHeight = headerHeight;
        if (level > 0 && !highlight) {
            groupIndicatorWidth = 2;
        }
        attrs = { groupByValues: groupByValues.map(g => g.value).join(),
            tabIndex: -1 };
    }
    const groupHeadStyle = {
        height: headerHeight + 'px',
        width: groupSectionWidth + 'px'
    };
    const groupIndicatorStyle = {
        width: groupIndicatorWidth + 'px',
        height: groupIndicatorHeight + 'px',
        backgroundColor: color
    };
    const className = rowsExist ? LEAF_GROUP_CLASS : PARENT_GROUP_CLASS;
    const groupNameStyle = {
        // set width to enable text-overflow
        width: groupSectionWidth - 40 + 'px', // minus the margins
        fontWeight: highlight || !rowsExist? 700 : 400 // bold <-> normal
    };

    const ariaLbl = groupByValues.map(g => g.displayValue).join();

    return <Fragment>
        <div
            className={className}
            style={groupHeadStyle}
            attrs={attrs}
            on-blur={(e) => toggleFocusRing(e.target, true)}
            on-keydown={(e) => moveFocusInBody(e)}>
            <div className="sn-roadmap-body-group-indicator" style={groupIndicatorStyle}></div>
            <span aria-label={ariaLbl}
                className="group-name" style={groupNameStyle}
                onmouseenter={event => setGroupTooltipRef(event.target, updateState)}
                onmouseleave={() => setGroupTooltipRef(null, updateState)}>
                {group.displayValue}
            </span>
        </div>
        {childrenJSX}
    </Fragment>;
}

export function renderGroupRows({
    group,
    settings,
    canEditBar,
    activeBarId,
    enableResizers,
    timelineData,
    dispatch,
    groupByValues= [],
    horizontalScrollContainerRef,
    barResize,
    lastModifiedRoadmapItemId,
    itemDetailsPanel
}) {
    const childrenJSX = [];

    group.rows.forEach((row, index) => {
        const updatedGroupByValues = [...groupByValues, { value: index.toString(), displayValue: index.toString() }];
        const rowJsx = renderRow({
            row,
            settings,
            dispatch,
            canEditBar,
            activeBarId,
            enableResizers,
            timelineData,
            groupByValues: updatedGroupByValues,
            horizontalScrollContainerRef,
            barResize,
            lastModifiedRoadmapItemId,
            itemDetailsPanel
        });

        childrenJSX.push(rowJsx);
    });

    group.groups.forEach(subGroup => {
        const updatedGroupByValues = [...groupByValues, { value: subGroup.value, displayValue: subGroup.displayValue }];
        const groupJsx = renderGroupRows({
            group: subGroup,
            settings,
            canEditBar,
            activeBarId,
            enableResizers,
            timelineData, dispatch,
            groupByValues: updatedGroupByValues,
            horizontalScrollContainerRef,
            barResize,
            lastModifiedRoadmapItemId,
            itemDetailsPanel
        });

        childrenJSX.push(groupJsx);
    });

    return <Fragment>
        <div className="sn-roadmap-body-group" aria-label={group.displayValue}>
            {ifJsx(group.groups.length > 0,
                <div className="sn-roadmap-body-group-row"
                    style={{ height: `${PARENT_GROUPS_HEIGHT}px` }}
                    ondragover={(e) => updateDropTargetPath(false, e.target, dispatch)}/>
            )}
            { childrenJSX }
        </div>
    </Fragment>;
}


