import { ARIA_COLORBY_LEGEND_ICON, COLORBY_LEGEND_ICON } from '../../constants';
import { colorByLegendPopover } from '../popovers/popover-util';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { ifJsx } from '../../util';

const renderColorByLegendIcon=(state, updateState)=>{
    const { openColorByLegendPopover } =state;

    return (
        <now-button-iconic
            className="sn-roadmap-main-section-colorby-legend-icon"
            icon="score-list-outline"
            tooltipContent={ARIA_COLORBY_LEGEND_ICON}
            size="md"
            configAria={{ button: { 'aria-label': ARIA_COLORBY_LEGEND_ICON } }}
            bare
            variant="tertiary"
            appendToPayload={{
                type: COLORBY_LEGEND_ICON
            }}
            onmousedown={e=> e.stopPropagation()}
            on-click={e => {
                e.stopPropagation();
                updateState({
                    colorByLegendTargetRef: e.target,
                    openColorByLegendPopover: !openColorByLegendPopover,
                    colorByFocusFirstEl: e.screenX === 0 && e.screenY === 0
                });
            }}
        />
    );
};

export const renderColorByLegend=({ state, dispatch, updateState })=>{
    const { settings: { colorMap, colorBy },
        properties: { roadmapItemMetadata },
        colorByLegendTargetRef,
        openColorByLegendPopover, colorByFocusFirstEl } = state;
    let colorByLbl = colorBy;
    if (roadmapItemMetadata.colorableFields && colorBy) {
        const colorField = roadmapItemMetadata.colorableFields.find(a=>a.name === colorBy);
        if (colorField) {
            colorByLbl = colorField.label;
        }
    }

    return (
        <Fragment>
            {ifJsx(colorMap && colorMap.size, <div className="sn-roadmap-main-section-colorby-legend">
                {renderColorByLegendIcon(state, updateState, dispatch)}
                {colorByLegendPopover({ colorByLegendTargetRef, colorMap, colorByLbl, openColorByLegendPopover, colorByFocusFirstEl })}
            </div>)}
        </Fragment>
    );
};
