import {
    BAR_DRAG_COMPLETED,
    BAR_DRAG_STARTED,
    BAR_DRAGGED,
    BAR_DROPPED,
    BAR_POPOVER_HIDDEN
} from '../../comp-config/actions';
import throttle from 'lodash/throttle';
import isEmpty from 'lodash/isEmpty';
import { delayedClearScrollIntervals, throttledScroll } from './scrollable';
import {
    ACTIVE_ROW_ATTRIBUTE,
    AUTO_SCROLL_EDGE_WIDTH,
    BAR_CLASS,
    ROW_CLASS,
    ROWS_WRAPPER_CLASS,
    UNSCHEDULED_ITEM_ATTRIBUTE
} from '../../constants';
import { getParentElement, updateDropTargetPath } from './barUtils';
import { cancelPopover } from './resizable';

let mousePos = {
    clientX: 0,
    clientY: 0
};

export const updateMousePos = (e) => {
    mousePos = {
        ...mousePos,
        clientX: e.clientX,
        clientY: e.clientY
    };
};

let scrollContainers = {
    horizontalScrollContainerRef: null,
    verticalScrollContainerRef: null
};

export const updateScrollContainers = (verticalScrollContainerRef, horizontalScrollContainerRef) => {
    scrollContainers = {
        ...scrollContainers,
        horizontalScrollContainerRef,
        verticalScrollContainerRef
    };
};

export const getScrollContainers = () => {
    return scrollContainers;
};

export const handleDrop = (e, dispatch, horizontalScrollContainerRef) => {
    const horizontalScrollContainer = horizontalScrollContainerRef.current;
    const rawData = e.dataTransfer.getData('text/plain');
    let dropTarget = e.target;
    if (!dropTarget.classList.contains(ROW_CLASS)) {
        dropTarget = getParentElement(dropTarget, ROW_CLASS);
    }
    const dropScrollLeft = horizontalScrollContainer.scrollLeft;
    const groupByValues = dropTarget && dropTarget.groupByValues ? dropTarget.groupByValues : [];

    e.dataTransfer.dropEffect = 'move';
    if (rawData && rawData.length > 0) {
        const { id, left, top, startDate, endDate, startScrollLeft, srcGroupByValues } = JSON.parse(rawData);
        const dragDistance = {
            x: e.clientX - left + (dropScrollLeft - startScrollLeft),
            y: e.clientY - top
        };

        dispatch(BAR_DROPPED, {
            id,
            dragDistance,
            groupByValues,
            srcGroupByValues,
            startDate,
            endDate
        });
    }
};

const setBarDragDropActive = (currentBar) => {
    const resizers = currentBar.querySelectorAll('.sn-roadmap-bar-resizer');
    for (const resizer of resizers) {
        resizer.style.display = 'none';
    }
};

export const handleDragBar = (dragStatus, startDate, endDate, e, dispatch, isRTL) => {
    const verticalScrollContainer = scrollContainers.verticalScrollContainerRef.current;
    const horizontalScrollContainer = scrollContainers.horizontalScrollContainerRef.current;
    // eslint-disable-next-line prefer-const
    let currentBar;
    let currentRow;
    // eslint-disable-next-line prefer-const
    let rowsContainer;

    const disablePointerOnBars = (status, roadmapBody) => {
        if (roadmapBody) {
            const bars = roadmapBody.getElementsByClassName(BAR_CLASS);
            for (const bar of bars) {
                if (bar !== currentBar) {
                    bar.style.pointerEvents = status ? 'none' : 'all';
                }
            }
        }
    };

    const handleDragStart = (e) => {
        const isUnscheduledItem = !isEmpty(currentBar.getAttribute(UNSCHEDULED_ITEM_ATTRIBUTE));
        const barRect = currentBar.getBoundingClientRect();

        currentRow = getParentElement(currentBar, ROW_CLASS);
        const roadmapBodyRect = horizontalScrollContainer.getBoundingClientRect();
        const data = {
            id: currentBar.id,
            left: e.clientX,
            top: e.clientY,
            startScrollLeft: horizontalScrollContainer.scrollLeft,
            startDate,
            endDate,
            srcGroupByValues: currentRow && currentRow.groupByValues ? currentRow.groupByValues : []
        };

        if (currentRow) {
            currentRow.setAttribute(ACTIVE_ROW_ATTRIBUTE, '');
        }
        rowsContainer = getParentElement(currentBar, ROWS_WRAPPER_CLASS);
        updateDropTargetPath(true, currentRow, dispatch);
        e.dataTransfer.setData('text/plain', JSON.stringify(data));
        e.dataTransfer.effectAllowed = 'move';
        disablePointerOnBars(true, rowsContainer);

        const dragStart = isRTL ? roadmapBodyRect.right - barRect.right : barRect.left - roadmapBodyRect.left;

        dispatch(BAR_DRAG_STARTED, {
            start: isUnscheduledItem ? mousePos.clientX : dragStart,
            startClientX: mousePos.clientX
        });
    };

    const updateDragLine = (e, dispatch) => {
        const roadmapBodyRect = horizontalScrollContainer.getBoundingClientRect();
        const scrollDiffX = horizontalScrollContainer.scrollLeft;
        const dragLineElement = verticalScrollContainer.querySelector('#roadmapBodyDragLine');
        const dragLineRect = dragLineElement.getBoundingClientRect();

        dispatch(BAR_DRAGGED, {
            dragClientX: mousePos.clientX + scrollDiffX,
            barStart: isRTL
                ? roadmapBodyRect.right - dragLineRect.right - scrollDiffX
                : dragLineRect.left - roadmapBodyRect.left + scrollDiffX
        });
    };

    const throttledDrag = throttle(updateDragLine, 10);

    const handleDrag = (e) => {
        throttledScroll(mousePos, AUTO_SCROLL_EDGE_WIDTH, horizontalScrollContainer, verticalScrollContainer);
        throttledDrag(e, dispatch);
    };

    const clearDragLine = () => {
        dispatch(BAR_DRAG_COMPLETED);
    };

    const dragCompleted = () => {
        mousePos = {
            clientX: 0,
            clientY: 0
        };
        delayedClearScrollIntervals();
        if (currentRow) {
            currentRow.removeAttribute(ACTIVE_ROW_ATTRIBUTE);
        }
        updateDropTargetPath(false, null, dispatch);
        disablePointerOnBars(false, rowsContainer);
        setTimeout(clearDragLine, 10);
    };

    const handleDragEnd = (e) => {
        e.preventDefault();
        dragCompleted();
        currentBar.setAttribute('draggable', 'false');
        currentBar.removeEventListener('dragstart', handleDragStart);
        currentBar.removeEventListener('dragend', handleDragEnd);
        currentBar.removeEventListener('drag', handleDrag);
    };

    const addListeners = () => {
        currentBar.setAttribute('draggable', 'true');
        currentBar.addEventListener('dragstart', handleDragStart);
        currentBar.addEventListener('dragend', handleDragEnd);
        currentBar.addEventListener('drag', handleDrag);
    };

    const removeListeners = () => {
        currentBar.setAttribute('draggable', 'false');
        currentBar.removeEventListener('dragstart', handleDragStart);
        currentBar.removeEventListener('dragend', handleDragEnd);
        currentBar.removeEventListener('drag', handleDrag);
    };

    currentBar = getParentElement(e.target, BAR_CLASS);

    if (dragStatus && e.target) {
        mousePos = {
            clientX: e.clientX,
            clientY: e.clientY
        };
        addListeners();
        setBarDragDropActive(currentBar);
        cancelPopover();
        dispatch(BAR_POPOVER_HIDDEN);
    } else {
        removeListeners();
        dragCompleted();
    }
};
