import debounce from 'lodash/debounce';
import { delayedClearScrollIntervals, throttledScroll } from './scrollable';
import { AUTO_SCROLL_EDGE_WIDTH, BAR_CLASS, ROW_CLASS,
    TOOLTIP_TIME_DELAY } from '../../constants';
import {
    ACTIVE_ROW_PATH_UPDATED,
    BAR_POPOVER_HIDDEN,
    BAR_POPOVER_SHOWN,
    BAR_RESIZE_COMPLETED,
    BAR_RESIZE_UPDATED_REF,
    BAR_RESIZE_UPDATED_SIZE
} from '../../comp-config/actions';
import { getParentElement, updateDropTargetPath } from './barUtils';

let isResizing = false;
let closePopoverTimeout;

const showPopover = (dispatch, payload) => {
    if (dispatch && payload) {
        dispatch(BAR_POPOVER_SHOWN, payload);
    }
};

const delayedPopover = debounce(showPopover, TOOLTIP_TIME_DELAY);

export const cancelPopover = () => delayedPopover.cancel();

const delayedClosePopover = (dispatch) => {
    closePopoverTimeout = setTimeout(() => {
        dispatch(BAR_POPOVER_HIDDEN);
        delayedPopover(false, dispatch);
    }, TOOLTIP_TIME_DELAY/5);
};

export const handleFocusBar = ({
    status, barElement, enableResizers, dispatch, roadmapItem = null, showItemOwner, metricFieldsWithValues, isKeyboardFocus }) => {
    const parentRow = getParentElement(barElement, ROW_CLASS);
    const groupByValues = parentRow && parentRow.groupByValues ? parentRow.groupByValues : [];
    if (isResizing) {
        return;
    }

    dispatch(ACTIVE_ROW_PATH_UPDATED, {
        path: status ? groupByValues : [],
        activeBarId: status ? barElement.id: -1,
        enableResizers
    });

    if (status) {
        if (!barElement.draggable) {
            if (closePopoverTimeout) {
                clearTimeout(closePopoverTimeout);
            }
            delayedPopover(dispatch, {
                target: barElement,
                roadmapItem,
                showItemOwner,
                metricFieldsWithValues
            });
        }
    } else if (!isKeyboardFocus) {
        delayedClosePopover(dispatch);
    } else {
        dispatch(BAR_POPOVER_HIDDEN);
        delayedPopover(false, dispatch);
    }
};

export const handleResizeBar = (resizeStatus, resizerDir, id = -1, startDate = {}, endDate = {}, mouseEvent = null,
    isRTL = false, dispatch = null, horizontalScrollContainerRef = null, timelineWidth = -1) => {
    let horizontalScrollContainer;
    let currentResizer;
    let currentBar;
    let currentRow;
    let leftResizer;
    let rightResizer;
    let prevX = 0;
    let prevScrollLeft = 0;
    let groupByValues;
    let leftTooltipTarget;
    let rightTooltipTarget;
    let roadmapContainer;

    const setBarResizeActive = (status) => {
        isResizing = status;
        if (leftResizer) {
            status ? leftResizer.classList.add('sn-roadmap-bar-resizer-left-fade')
                : leftResizer.classList.remove('sn-roadmap-bar-resizer-left-fade');
        }
        if (rightResizer) {
            status ? rightResizer.classList.add('sn-roadmap-bar-resizer-right-fade')
                : rightResizer.classList.remove('sn-roadmap-bar-resizer-right-fade');
        }
    };

    const resize = (e) => {
        const barRect = currentBar.getBoundingClientRect();
        const rowRect = currentRow.getBoundingClientRect();
        const scrollDiff = horizontalScrollContainer.scrollLeft - prevScrollLeft;
        const diffX = prevX - e.clientX - scrollDiff;
        const mouseParams = {
            clientX: e.clientX,
            clientY: e.clientY
        };

        throttledScroll(mouseParams, AUTO_SCROLL_EDGE_WIDTH, horizontalScrollContainer, null, timelineWidth);

        if (resizerDir === 'resizer-left') {
            const newWidth = isRTL ? barRect.width - diffX : barRect.width + diffX;
            const newLeft = isRTL ? rowRect.right - barRect.right + diffX : barRect.left - rowRect.left - diffX;

            currentBar.style.width = newWidth.toString() + 'px';
            if (isRTL) {
                currentBar.style.right = newLeft + 'px';
            } else {
                currentBar.style.left = newLeft + 'px';
            }
            dispatch(BAR_RESIZE_UPDATED_SIZE, {
                resizer: 'left',
                width: newWidth,
                left: newLeft,
                endDate,
                startDate
            });
        } else if (resizerDir === 'resizer-right') {
            const newWidth = isRTL ? barRect.width + diffX : barRect.width - diffX;

            currentBar.style.width = newWidth.toString() + 'px';
            dispatch(BAR_RESIZE_UPDATED_SIZE, {
                resizer: 'right',
                width: newWidth,
                startDate,
                endDate
            });
        }

        prevX = e.clientX;
        prevScrollLeft = horizontalScrollContainer.scrollLeft;
    };

    const stopResize = () => {
        delayedClearScrollIntervals();
        horizontalScrollContainer.removeEventListener('mousemove', resize);
        roadmapContainer.removeEventListener('mouseup', stopResize);
        setBarResizeActive(false);
        dispatch(BAR_RESIZE_COMPLETED);
        updateDropTargetPath(false, currentRow, dispatch);
    };

    if (resizeStatus) {
        currentResizer = mouseEvent.currentTarget;
        currentBar = getParentElement(currentResizer, BAR_CLASS);
        currentRow = getParentElement(currentResizer, ROW_CLASS);
        roadmapContainer = getParentElement(currentResizer, 'sn-roadmap-container');
        groupByValues = currentRow.groupByValues;
        horizontalScrollContainer = horizontalScrollContainerRef.current;
        prevScrollLeft = horizontalScrollContainer.scrollLeft;
        prevX = mouseEvent.clientX;
        for (const child of currentBar.children) {
            if (child.classList.contains('sn-roadmap-bar-resizer-left')) {
                leftResizer = child;
            } else if (child.classList.contains('sn-roadmap-bar-resizer-right')) {
                rightResizer = child;
            } else if (child.classList.contains('sn-roadmap-bar-left-tooltip-target')) {
                leftTooltipTarget = child;
            } else if (child.classList.contains('sn-roadmap-bar-right-tooltip-target')) {
                rightTooltipTarget = child;
            }
        }
        horizontalScrollContainer.addEventListener('mousemove', resize);
        roadmapContainer.addEventListener('mouseup', stopResize);
        setBarResizeActive(true);
        updateDropTargetPath(true, currentRow, dispatch, currentBar.id);
        dispatch(BAR_POPOVER_HIDDEN);
        dispatch(BAR_RESIZE_UPDATED_REF, {
            leftTooltipRef: leftTooltipTarget,
            rightTooltipRef: rightTooltipTarget,
            startDate: startDate,
            endDate: endDate,
            id: id.value,
            groupByValues
        });
    } else {
        setBarResizeActive(false);
    }
};
