import { COLORBY_LEGEND_POPOVER_CLOSED, KEYS } from '../../../constants';

export const setFocusToNextItem=(e, dispatch, parent)=>{
    e.preventDefault();
    e.stopPropagation();
    switch (e.keyCode) {
        case KEYS.RIGHT:
        case KEYS.DOWN: {
            const nextSibling=e.target.nextSibling;
            if (nextSibling) {
                nextSibling.focus();
            }
            break;
        }
        case KEYS.LEFT:
        case KEYS.UP: {
            const previousSibling=e.target.previousSibling;
            if (previousSibling) {
                previousSibling.focus();
            }
            break;
        }
        case KEYS.ESC:
            dispatch(COLORBY_LEGEND_POPOVER_CLOSED, { sideBarFocus: false });
            parent && parent.focus();
            break;
        case KEYS.TAB:
            dispatch(COLORBY_LEGEND_POPOVER_CLOSED, { sideBarFocus: true });
            break;
    }
};

const moveFocusToPopoverContent=(e)=>{
    e.preventDefault();
    e.stopPropagation();
    e.target.querySelector('.sn-roadmap-colorby-legend-popover-content-list-item').focus();
};

export const keyDownHandler=(e, dispatch, parent)=>{
    switch (e.keyCode) {
        case KEYS.ESC:
            dispatch(COLORBY_LEGEND_POPOVER_CLOSED, { sideBarFocus: false });
            parent && parent.focus();
            break;
        case KEYS.TAB:
            dispatch(COLORBY_LEGEND_POPOVER_CLOSED, { sideBarFocus: true });
            break;
        case KEYS.DOWN:
            moveFocusToPopoverContent(e);
            break;
    }
};
