import debounce from 'lodash/debounce';
import isEmpty from 'lodash/isEmpty';
import { defaultItemMetadata } from './comp-config/default-state';
import { ROADMAP_BODY_CLASS } from './constants';

export const getShadowRootHost = el => {
    if (el.parentElement) {
        return getShadowRootHost(el.parentElement);
    }

    if (el.parentNode && el.parentNode instanceof DocumentFragment) {
        return el.parentNode.host;
    }

    if (el.shadowRoot) {
        return el;
    }

    return null;
};

export const querySelector = (el, selector) => {
    if (el.shadowRoot) {
        return el.shadowRoot.querySelector(selector);
    }

    return getShadowRootHost(el).shadowRoot.querySelector(selector);
};

export const ifJsx = (condition, returnOnTrue, returnOnFalse) => {
    if (condition) {
        return returnOnTrue instanceof Function? returnOnTrue():returnOnTrue;
    }

    return returnOnFalse instanceof Function? returnOnFalse():returnOnFalse;
};

export const getScrollSynchronizer = (getRef) => {
    let scrollOrigin;
    const resetScrollOrigin = debounce(() => {
        scrollOrigin = undefined;
    }, 50);

    return (e, section, origin) => {
        if (!scrollOrigin) {
            scrollOrigin = origin;
        } else if (scrollOrigin !== origin) {
            return;
        }

        Object.values(getRef(section)).forEach(sectionRef => {
            sectionRef.current.scrollTo({
                left: e.target.scrollLeft
            });
        });
        resetScrollOrigin();
    };
};

export const resetGridHorizontalScroll = (getRef) => {
    return (section) => {
        const sectionRefs = Object.values(getRef(section));

        if (sectionRefs) {
            sectionRefs.forEach(sectionRef => {
                const currentSectionRef = sectionRef.current;
                if (sectionRef.current) {
                    currentSectionRef.scrollTo({
                        left: 0
                    });
                }
            });
        }
    };
};

export const ShouldResetHorizontalScrcoll = (()=>{
    let shouldReset = false;

    return {
        set: (resetScroll)=>{
            shouldReset = resetScroll;
        },
        get: ()=>{
            return shouldReset;
        }
    };
})();

export const getBrowserScrollbarWidth = (host) => {
    const scrollDiv = host.shadowRoot.querySelector('.scrollbar-measure');

    return scrollDiv.offsetWidth - scrollDiv.clientWidth;
};

export const hasGridBodyVertialScroll = (host) => {
    const gridBodyElement = host.shadowRoot.querySelector('.sn-roadmap-grid-body');

    return gridBodyElement.scrollHeight > gridBodyElement.clientHeight;
};

export const getTimelineContainerWidth = (host) => {
    return host.shadowRoot.querySelector('.sn-roadmap-timeline-header-container').getBoundingClientRect().width;
};

export const hasClass = (element, cssClass) => {
    return element && element.classList && element.classList.contains(cssClass);
};

export const getParentNodeByClass = (activeEl, cssClass) => {
    let parent = activeEl.parentNode;
    while (parent && !hasClass(parent, cssClass)) {
        parent = parent.parentNode;
    }

    return parent;
};

export const toggleFocusRing = (targetEl, removeFocusRing) => {
    if (!targetEl) {
        return;
    }
    if (removeFocusRing === true) {
        targetEl.classList.remove('focus-visible');
    } else {
        targetEl.classList.add('focus-visible');
    }
};

export const setFocusToBody = (host) => {
    const bodyEl = host.shadowRoot.querySelector(`.${ROADMAP_BODY_CLASS}`);
    if (bodyEl) {
        bodyEl.focus();
        toggleFocusRing(bodyEl, false);
    }
};

const getHeaderHeight = (host) => {
    const header = host.shadowRoot.querySelector('.sn-roadmap-header');

    return header ? header.getBoundingClientRect().height : 0;
};

const getTimelineHeight = (host) => {
    const timelineHeader = host.shadowRoot.querySelector('.sn-roadmap-grid-header');

    return timelineHeader ? timelineHeader.getBoundingClientRect().height : 0;
};

export const getRoadmapBodyHeight = (host) => {
    const headerHeight = getHeaderHeight(host);
    const timelineHeaderHeight = getTimelineHeight(host);
    const body = host.shadowRoot.querySelector('.sn-roadmap-body');

    return body && headerHeight + timelineHeaderHeight + body.clientHeight;
};

export const getRoadmapGridBodyHeight = (host) => {
    const headerHeight = getHeaderHeight(host);
    const timelineHeaderHeight = getTimelineHeight(host);
    const gridBody = host.shadowRoot.querySelector('.sn-roadmap-grid-body');

    return gridBody && headerHeight + timelineHeaderHeight + gridBody.clientHeight;
};

export const getRoadmapMainSectionRect = (host) => {
    const mainSection=host.shadowRoot.querySelector('.sn-roadmap-main-section');

    return mainSection && mainSection.getBoundingClientRect();
};

export const getColorByLegendIcon = (host) => {
    return host.shadowRoot.querySelector('.sn-roadmap-main-section-colorby-legend-icon');
};

export const getSidebar = (host) => {
    return host.shadowRoot.querySelector('.sn-roadmap-main-section-toolbar');
};

export const sortItemsByField = (roadmapItems, field) => {
    roadmapItems.sort(function(a, b) {
        const aField = a[field];
        if (isEmpty(aField) || isEmpty(aField.value)) {
            return 1;
        }
        const bField = b[field];
        if (isEmpty(bField) || isEmpty(bField.value)) {
            return -1;
        }
        let aVal = aField.displayValue,
            bVal = bField.displayValue;
        if (aVal === bVal) {
            aVal = aField.value;
            bVal = bField.value;
        }
        if (isEmpty(aVal) || isEmpty(bVal)) {
            aVal = aField.value;
            bVal = bField.value;
        }
        if (aVal.toLowerCase() > bVal.toLowerCase()) {
            return 1;
        } else if (bVal.toLowerCase() > aVal.toLowerCase()) {
            return -1;
        }

        return 0;
    });
};

export const transformItemMetadata = (itemMetadata) =>{
    return {
        ...defaultItemMetadata,
        ...itemMetadata
    };
};
